<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'ClusterScatter',
        props: ['config', 'base'],
        data() {
            return {
                seriesOption: []
            }
        },
        created() {
            this.initStyle()
        },
        methods: {
            /**
             * 初始化样式，对新组件使用默认样式
             */
            initStyle() {
                this.config.style = _.assignIn({
                    top: 30,
                    left: 30,
                    width: 400,
                    height: 400,
                    opacity: 1,
                    display: 'block',
                    visibility: 'visible'
                }, this.config.style)
            },
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    color: ['rgba(62, 158, 255, 0.8)'],
                    grid: {
                        top: 50,
                        left: 50,
                        right: 50,
                        bottom: 50
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            lineStyle: {
                                color: 'rgba(62, 158, 255, 0.8)',
                                width: 1,
                            }
                        },
                        backgroundColor: '#2F3943',
                        borderColor: 'transparent',
                        borderWidth: 1,
                        padding: [5, 10, 5, 10],
                        textStyle: {
                            color: '#ffffff',
                        },
                        extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)',
                        formatter: '[{c0}]'
                    },
                    xAxis: {
                        type: 'value',
                        scale: true,
                        max: 12,
                        splitLine: {
                            show: true,
                            interval: 'auto',
                            lineStyle: {
                                color: ['rgba(212,223,245,0.2)']
                            }
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#609ee9'
                            }
                        },
                        axisLabel: {
                            show: true,
                            rotate: 0,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        }
                    },
                    yAxis: {
                        type: 'value',
                        scale: true,
                        min: 0,
                        max: undefined,
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: ['rgba(212,223,245,0.2)']
                            }
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#609ee9'
                            }
                        },
                        axisLabel: {
                            show: true,
                            rotate: 0,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        },
                    },
                    series: [{
                        name: '原数据',
                        type: 'scatter',
                        symbolSize: 10,
                        itemStyle: {
                            color: '#ffffff',
                        },
                        data: null
                    }, {
                        name: '簇中心点',
                        type: 'effectScatter',
                        symbolSize: 10,
                        itemStyle: {
                            color: '#609ee9',
                        },
                        data: null
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {x: 1, y: 1, clusterX: 2, clusterY: 2},
                    {x: 1, y: 3, clusterX: 2, clusterY: 2},
                    {x: 3, y: 1, clusterX: 2, clusterY: 2},
                    {x: 3, y: 3, clusterX: 2, clusterY: 2},
                    {x: 8, y: 8, clusterX: 9, clusterY: 9},
                    {x: 8, y: 10, clusterX: 9, clusterY: 9},
                    {x: 10, y: 8, clusterX: 9, clusterY: 9},
                    {x: 10, y: 10, clusterX: 9, clusterY: 9},
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'x', comment: 'x坐标', prop: 'x', state: null},
                            {name: 'y', comment: 'y坐标', prop: 'y', state: null},
                            {name: 'clusterX', comment: '中心点x', prop: 'clusterX', state: null},
                            {name: 'clusterY', comment: '中心点y', prop: 'clusterY', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        x: null,
                        y: null,
                        clusterX: null,
                        clusterY: null,
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let xProp = this.config.transform.mapper.x || 'x'
                let yProp = this.config.transform.mapper.y || 'y'
                let clusterXProp = this.config.transform.mapper.clusterX || 'clusterX'
                let clusterYProp = this.config.transform.mapper.clusterY || 'clusterY'
                let clusterMap = {}
                let dataset = []
                let resultList = this.config.data
                if (resultList && resultList.length) {
                    for (let i = 0; i < resultList.length; i++) {
                        let model = resultList[i]
                        dataset.push([_.get(model, xProp), _.get(model, yProp)])
                        let centerX = _.get(model, clusterXProp)
                        let centerY = _.get(model, clusterYProp)
                        let centerId = centerX + '_' + centerY
                        clusterMap[centerId] = [centerX, centerY]
                    }
                }
                let cluster = []
                for (let key in clusterMap) {
                    cluster.push(clusterMap[key])
                }
                this.config.option.series[0].data = dataset
                this.config.option.series[1].data = cluster
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
