import CONST from "../config/const";
import _ from "lodash";

const TriggerHandler = {
    do: function (trigger, data, store) {
        let result = trigger.defaultValue;
        let code = trigger.binder;
        if (code && code.length > 0) {
            code = this.replaceInternalDate(code, data);
            result = this.replaceExternalDate(code, store, trigger);
        }
        if (trigger.mapper && trigger.mapper.length) {
            for (let j = 0; j < trigger.mapper.length; j++) {
                let kv = trigger.mapper[j]
                if (kv.key == result) {
                    result = kv.value;
                    break;
                }
            }
        }
        return result
    },
    replaceInternalDate: function (code, data) {
        let props = code.match(CONST.REGEX.PLACEHOLDER_INTERNAL);
        if (props && props.length) {
            for (let i = 0; i < props.length; i++) {
                let dataClone = _.cloneDeep(data)
                let dataProp = props[i].substring(2, props[i].length - 1);
                let arrIndex = dataProp.match(/^\[\d+\]\./)
                let propPath = dataProp
                if (arrIndex && arrIndex.length) {
                    let index = arrIndex[0]
                    propPath = dataProp.replace(index, "")
                    index = parseInt(index.substring(1, index.length - 2))
                    dataClone = dataClone[index]
                }
                let val = _.get(dataClone, propPath);
                if (val === undefined || val === null) {
                    val = ""
                }
                if (val instanceof Array || val instanceof Object) {
                    val = JSON.stringify(val)
                    val = "'" + val + "'"
                } else if (val instanceof Date) {
                    val = val.getMilliseconds();
                } else if (typeof val == "string") {
                    val = "'" + val + "'"
                }
                code = code.split(props[i]).join(val)
            }
        }
        return code
    },
    replaceExternalDate: function (code, store, trigger) {
        let result = trigger.defaultValue
        for (let key in store) {
            let regex = new RegExp(':{' + key + '}', 'g')
            let val = store[key]
            if (val === undefined || val === null) {
                val = ""
            }
            if (val instanceof Array || val instanceof Object) {
                val = JSON.stringify(val)
                val = "'" + val + "'"
            } else if (val instanceof Date) {
                val = val.getMilliseconds();
            } else if (typeof val == "string ") {
                val = "'" + val + "'"
            }
            code = code.replace(regex, val);
        }
        if (trigger.isScript !== false) {
            try {
                let func = new Function('"use strict"; let result = ' + code + '; return result');
                result = func()
            } catch (e) {
            }
        } else {
            result = code
        }
        return result
    },
}

export default TriggerHandler;