<template>
    <Plugin :config="config" :base="base">
        <iframe class="iframe"
                :style="calcStyle()"
                :src="config.option.url"
                frameborder="0"></iframe>
    </Plugin>
</template>

<script>
    import '../../../../assets/styles/common/paint/iframe.scss'
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'Iframe',
        props: ['config', 'base'],
        data() {
            return {}
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        methods: {
            /**
             * 计算样式
             */
            calcStyle() {
                return {
                    opacity: this.config.style.opacity,
                }
            },
            /**
             * 初始化样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    width: 200,
                    height: 120,
                    opacity: 1,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = null
            },
            /**
             * 声明默认option
             */
            initOption() {
                this.config.option = this.config.option || {
                    url: 'https://www.baidu.com'
                }
            },
            /**
             * 初始化事件
             */
            initEvent() {
                this.config.event = null
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = {type: null}
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = null
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
            },
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
