<template>
    <div class="menu">
        <template v-for="(node, i) in menu">
            <div class="node"
                 :key="i"
                 @mouseover="node.show = true"
                 @mouseleave="node.show = false">
                <el-button type="primary"
                           size="mini"
                           circle>
                    <i :class="'icon-' + node.icon" class="icon iconfont"></i>
                </el-button>
                <el-tabs v-show="node.show"
                         type="border-card"
                         tab-position="left">
                    <template v-for="(category, j) in node.categories">
                        <el-tab-pane :key="j">
                            <span slot="label"><i :class="'icon-' + category.icon" class="icon iconfont"></i></span>
                            <template v-for="(item, k) in category.items">
                                <div class="item-card" :key="k" @click="addComponent(i, j, k)">
                                    <img :src="getComponentImage(item)"
                                         @drag="node.show = false"
                                         @dragstart="dragComponent($event, i, j, k)"
                                         @dragend="endDrag($event)">
                                    <div class="item-name">{{item.text}}</div>
                                </div>
                            </template>
                        </el-tab-pane>
                    </template>
                </el-tabs>
            </div>
        </template>
    </div>
</template>

<script>
    import IdGenerator from "../../utils/IdGenerator";
    import _ from "lodash";

    export default {
        name: "Menu",
        props: ["config", "state"],
        data() {
            return {
                showPanel: false,
                menu: null
            }
        },
        mounted() {
            this.initComponentMenu()
        },
        methods: {
            initComponentMenu() {
                this.menu = _.clone(this.$store.state.screen.design.menu)
                let components = this.state.dynamicComponent.list
                if (components && components.length) {
                    this.menu.push({
                        icon: 'star',
                        text: '自定义组件',
                        show: false,
                        categories: [{
                            text: '自定义组件',
                            icon: 'star',
                            items: components
                        }]
                    });
                }
            },
            /**
             * 获取组件缩略图
             */
            getComponentImage(item) {
                if (item.image) {
                    return item.image
                }
                return './img/components/' + item.category + '/' + item.name + '.png';
            },
            /**
             * 添加一个组件
             * @param i
             * @param j
             * @param k
             */
            addComponent(i, j, k) {
                let item = this.menu[i].categories[j].items[k]
                if (!this.config.components) {
                    this.config.components = {}
                }
                if (!this.config.sequence) {
                    this.config.sequence = []
                }
                let cmp = this.generateComponent(item)
                let id = cmp.config.id
                this.config.sequence.unshift(id)
                this.$set(this.config.components, id, cmp)
            },
            /**
             * 拖放一个组件
             * @param i
             * @param j
             * @param k
             */
            dragComponent(event, i, j, k) {
                this.state.menu.dragMode = true;
                let item = this.menu[i].categories[j].items[k]
                this.state.menu.component = this.generateComponent(item)
            },
            /**
             * 生成组件的配置文件
             */
            generateComponent(item) {
                let id = IdGenerator.randomCmpId()
                return {
                    text: item.text,
                    category: item.category,
                    name: item.name,
                    image: item.image,
                    config: {
                        remote: {
                            config: item.config
                        },
                        // id
                        id: id,
                        // 栅格ID
                        grid: {id: null},
                        // 临时状态存储
                        state: {
                            // 是否已初始化过配置
                            configInit: false
                        },
                        // 样式
                        style: {},
                        // 触发器
                        trigger: null,
                        // 功能配置
                        option: null,
                        // 与全局状态的绑定规则，当全局状态发生变更，会分发消息到此处，
                        // 组件内可以通过监听该状态，来更新自身的状态
                        store: {
                            proxy: null,
                            trigger: null
                        },
                        // 静态数据或者从远端获取的数据存储
                        data: null,
                        // 数据代理，获取数据的方式
                        proxy: null,
                        // 数据转换，proxy获取到的数据，经过transform转换规则的处理，存储到data属性中
                        transform: null,
                        // 将data数据根据展示需要，转换到option中
                        handler: null,
                        // 事件，以及事件中的参数与全局状态的绑定配置
                        event: null
                    }
                }
            },
            /**
             * 停止拖拽
             */
            endDrag(event) {
                this.state.menu.dragMode = false;
                this.state.menu.component = null;
            },
        },
        watch: {
            /**
             * 监听组件清单，变化后重新载入组件
             */
            "state.dynamicComponent.list": {
                immediate: true,
                handler: function () {
                    this.initComponentMenu()
                }
            }
        }
    }
</script>

