<template>
    <div :style="calcStyle()"
         class="chart-ins">
        <div :id="config.id"
             :style="calcMapSize">
        </div>
        <div class="full-screen icon iconfont icon-full-screen"
             @click=fullScreen></div>
    </div>
</template>

<script>
    import echarts from "echarts"
    import echartsMaps from '../../../map/basemap/echarts-china'
    import DataProxy from "../../../proxy/DataProxy"
    import CONST from "../../../../config/const"
    import IdGenerator from "../../../../utils/IdGenerator";
    import TriggerHandler from "../../../../utils/TriggerHandler"
    import _ from "lodash"

    export default {
        name: "DynamicMap",
        props: ['config', 'base'],
        data() {
            return {
                // chart图表实例
                chart: null,
                // 数据代理实例
                dataProxy: null,
                // 数据代理线程
                proxyThread: null,
                // 在config.data属性初始化完成，并且触发监听前，
                // 禁止config.transform等属性的监听，主要是防止词云重复渲染
                dataInitCompleted: false
            }
        },
        created() {
            this.initConfig()
            this.initStyle()
            this.initTrigger()
            this.initProxy()
            this.initTransform()
        },
        mounted() {
            let remoteConfig = this.config.remote.config
            let func = new Function('"use strict"; let result = ' + remoteConfig + '; return result')
            this.dynamicConfig = func()
            this.initConfig()
            this.init()
        },
        /**
         * 销毁时注销组件
         */
        beforeDestroy() {
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        computed: {
            /**
             * 计算图表尺寸
             */
            calcMapSize() {
                // 当组件为全屏显示，或组件装载进栅格时，使用100%宽高
                if ((this.base.fullScreen.component && this.base.fullScreen.component.id == this.config.id)
                    || this.config.grid.id) {
                    return {width: '100%', height: '100%',}
                } else {
                    return {
                        width: this.config.style.width + 'px',
                        height: this.config.style.height + 'px',
                    }
                }
            },
        },
        methods: {
            /**
             * 计算容器样式
             */
            calcStyle() {
                if (this.base.fullScreen.component && this.base.fullScreen.component.id == this.config.id) {
                    // 当组件全屏显示时，使用屏幕宽高
                    if (this.base.enableGrid && this.config.grid.id) {
                        return {
                            width: '100%',
                            height: '100%',
                            display: this.config.style.display,
                            visibility: this.config.style.visibility,
                            opacity: 1,
                        }
                    } else {
                        return {
                            width: this.base.size.width + 'px',
                            height: this.base.size.height + 'px',
                            display: this.config.style.display,
                            visibility: this.config.style.visibility,
                            opacity: 1
                        };
                    }
                } else {
                    // 组件装载进栅格时，使用100%宽度
                    if (this.base.enableGrid && this.config.grid.id) {
                        return {
                            width: '100%',
                            height: this.config.style.height + 'px',
                            opacity: this.config.style.opacity,
                            display: this.config.style.display,
                            visibility: this.config.style.visibility,
                        };
                    } else {
                        return {
                            width: this.config.style.width + 'px',
                            height: this.config.style.height + 'px',
                            opacity: this.config.style.opacity,
                            display: this.config.style.display,
                            visibility: this.config.style.visibility,
                        };
                    }
                }
            },
            /**
             * 调用父组件的初始化方法，初始化配置
             */
            initConfig() {
                // config未初始化时，则初始化
                if (!this.config.state.configInit) {
                    this.config.state.configInit = true
                    this.initStyleConfig();
                    this.initOptionConfig();
                    this.initDataConfig();
                    this.initEventConfig();
                    this.initProxyConfig();
                    this.initTransformConfig();
                    this.initTriggerConfig();
                } else {
                    /*
                     此处主要是当组件在栅格模式下，组件从自由状态装载到栅格中时，组件的渲染位置发生变更，
                     但是组建的config不变，此时无法触发下面的watch监听器进行渲染，所以需要收工触发
                     */
                    this.reloadConfig()
                }
            },
            initStyleConfig() {
                this.config.style = _.assignIn({
                    top: 30,
                    left: 30,
                    width: 400,
                    height: 300,
                    opacity: 1,
                    display: 'block',
                    visibility: 'visible'
                }, this.dynamicConfig.style, this.config.style)
            },
            initOptionConfig() {
                if (!this.dynamicConfig.option) {
                    let msg = this.formatErrorMsg("自定义地图的option属性未定义。")
                    throw new Error(msg)
                }
                this.config.option = _.assignIn(this.dynamicConfig.option, this.config.option)
            },
            initDataConfig() {
                if (!this.dynamicConfig.data) {
                    let msg = this.formatErrorMsg("自定义地图的data属性未定义。")
                    throw new Error(msg)
                }
                this.config.data = this.config.data || this.dynamicConfig.data
            },
            initEventConfig() {
                this.config.event = this.config.event || this.dynamicConfig.event || []
            },
            initProxyConfig() {
                this.config.proxy = this.config.proxy || _.assignIn({
                    // 类型，0-API / 1-静态数据 / 2-数据库,
                    type: 1,
                    // 自动更新
                    autoRequest: false,
                    // 自动更新间隔
                    interval: 60,
                    // url
                    url: null,
                    // 参数
                    param: [],
                }, this.dynamicConfig.proxy)
            },
            initTransformConfig() {
                if (!this.dynamicConfig.transform) {
                    let msg = this.formatErrorMsg("自定义地图的transform属性未定义。")
                    throw new Error(msg)
                }
                this.config.transform = this.config.transform || this.dynamicConfig.transform
            },
            initTriggerConfig() {
                this.config.trigger = this.config.trigger || this.dynamicConfig.trigger || [
                    {
                        show: true,
                        name: '可见性',
                        binder: null,
                        prop: "config.style.visibility",
                        defaultValue: true,
                        isScript: true,
                        mapper: [
                            {key: true, value: 'visible', comment: '返回 true 时显示'},
                            {key: false, value: 'hidden', comment: '返回 false 时隐藏'},
                        ]
                    }
                ]
            },
            /**
             * 重载config
             */
            reloadConfig() {
                this.config.option = _.cloneDeep(this.config.option)
                this.config.transform = _.cloneDeep(this.config.transform)
                this.config.data = _.cloneDeep(this.config.data)
                this.config.event = _.cloneDeep(this.config.event)
                this.config.proxy = _.cloneDeep(this.config.proxy)
                this.config.trigger = _.cloneDeep(this.config.trigger)
            },
            /**
             * 初始化组件
             */
            init() {
                this.initTrigger()
                this.initIns()
                this.initEvent()
                this.instanceProxy()
            },
            /**
             * 初始化chart实例
             */
            initIns() {
                let dom = document.getElementById(this.config.id)
                this.chart = echarts.init(dom)
            },
            /**
             * 初始化触发器
             */
            initTrigger() {
                this.initTriggerStore()
                this.calcTrigger()
            },
            /**
             * 初始化触发器的Store存储
             */
            initTriggerStore() {
                if (!this.config.trigger || !this.config.trigger.length) {
                    return
                }
                let store = {}
                let vuex = this.$store
                for (let i = 0; i < this.config.trigger.length; i++) {
                    let trig = this.config.trigger[i]
                    let binder = trig.binder;
                    if (binder && binder.length) {
                        let keys = binder.match(CONST.REGEX.PLACEHOLDER_EXTERNAL);
                        if (!keys || !keys.length) {
                            continue;
                        }
                        for (let i = 0; i < keys.length; i++) {
                            let storeProp = keys[i].substring(2, keys[i].length - 1)
                            store[storeProp] = vuex.commit(CONST.MUTATIONS.GET, {prop: storeProp}) || null
                            vuex.commit(CONST.MUTATIONS.REGISTER, {
                                // 绑定哪个vuex状态
                                store: storeProp,
                                // 当前的绑定对象
                                comp: this,
                                // 当vuex状态变更时，应该修改绑定对象的哪个属性
                                prop: CONST.INTERACTION.COMPONENT_TRIGGER_STORE + storeProp
                            })
                        }
                    }
                }
                this.config.store.trigger = store
            },
            /**
             * 计算trigger结果，并赋值给相应的属性
             */
            calcTrigger() {
                if (!this.config.trigger || !this.config.trigger.length) {
                    return
                }
                for (let i = 0; i < this.config.trigger.length; i++) {
                    let trig = this.config.trigger[i];
                    let result = TriggerHandler.do(trig, this.config.data, this.config.store.trigger)
                    _.set(this, trig.prop, result);
                }
            },
            /**
             * 初始化数据代理的配置参数
             */
            initProxy() {
                let proxy = _.assignIn({}, {
                    // 类型，0-API / 1-静态数据 / 2-数据库,
                    type: 1,
                    // 自动更新
                    autoRequest: false,
                    // 自动更新间隔
                    interval: 60,
                    // url
                    url: null,
                    // 参数
                    param: [],
                }, this.config.proxy)
                this.config.proxy = proxy
            },
            /**
             * 实例化proxy，并缓存为组件属性
             */
            instanceProxy() {
                this.dataProxy = new DataProxy(this)
            },
            /**
             * 初始化transform
             */
            initTransform() {
                let transform = _.assignIn({}, {
                    // 从源数据的什么属性获取展示用的数据集
                    prop: null,
                }, this.config.transform)
                this.config.transform = transform
            },
            /**
             * 注册所有传入的事件
             */
            initEvent() {
                if (!this.config.event) {
                    return
                }
                let me = this
                // 注册所有事件
                for (let i = 0; i < me.config.event.length; i++) {
                    let event = me.config.event[i]
                    me.chart.on(event.name, function (params) {
                        let filter = event.filter
                        if (filter && !filter(params)) {
                            return
                        }
                        /*
                         * 当事件触发时，遍历事件的所有参数，当参数与全局状态存在绑定关系时，
                         * 向全局状态提交状态变更的请求，全局状态接收到最新的值后，会向订阅该状态的组件进行分发
                         */
                        if (!event.binders || !event.binders.length) {
                            return
                        }
                        for (let j = 0; j < event.binders.length; j++) {
                            let binder = event.binders[j]
                            if (!binder.state) {
                                continue
                            }
                            let value
                            if (binder.prop) {
                                value = _.get(params, binder.prop)
                            } else {
                                let dataset = this.config.data
                                value = dataset[params.dataIndex][binder.name]
                            }
                            me.commit(binder.state, value)
                        }
                    })
                }
            },
            /**
             * 使用数据代理获取数据，并将数据载入配置项中，然后重新加载配置项，渲染组件
             */
            render() {
                clearInterval(this.proxyThread)
                this.proxyData()
                if (this.config.proxy.autoRequest && this.config.proxy.interval) {
                    this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
                }
            },
            /**
             * 代理数据
             */
            proxyData() {
                this.dataProxy.do()
                    .then((data) => {
                        this.config.data = data
                    })
                    .catch(() => {
                    })
                    .finally(() => {
                        // this.loadData()
                        // this.loadOption()
                    })
            },
            /**
             * 调用父组件的handler方法，handler定义了将数据载入option的过程，
             * 因为option配置项是根据具体的报表类型而不同的，所以handler必须定义在父组件中
             */
            loadData() {
                if (!this.config.data) {
                    return
                }
                if (!this.dynamicConfig.handler || !this.dynamicConfig.handler) {
                    return
                }
                this.dynamicConfig.handler(this.config)
            },
            /**
             * 加载最新的配置项，对chart实例进行修改
             */
            loadOption() {
                try {
                    this.chart.setOption(this.config.option, true)
                } catch (e) {
                    console.log(e)
                }
            },
            /**
             * 向全局状态管理发起状态修改请求
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 重新加载报表
             */
            reload() {
                this.render()
            },
            /**
             * chart刷新尺寸
             */
            resize() {
                this.chart.resize({
                    height: this.config.style.height,
                    width: this.config.style.width,
                    silent: true
                })
            },
            /**
             * 全屏显示
             */
            fullScreen() {
                this.base.fullScreen.id = this.config.id
                let config = _.cloneDeep(this.config)
                config.id = IdGenerator.randomFullScreenId()
                this.base.fullScreen.component = config
            },
            formatErrorMsg(errMsg) {
                if (this.dynamicConfig.name) {
                    errMsg = this.dynamicConfig.name + ": " + errMsg
                }
                return errMsg
            }
        },
        watch: {
            "config.style.height": function () {
                this.resize()
            },
            "config.style.width": function () {
                this.resize()
            },
            /**
             * 监听被外部触发修改的属性
             */
            "config.trigger": {
                deep: true,
                handler: function () {
                    this.initTriggerStore()
                    this.calcTrigger()
                }
            },
            /**
             * 监听option，发生变更时重新载入最新option
             */
            "config.option": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    if (this.config.option.geo) {
                        echartsMaps.registerMap(this.config.option.geo.map, this.loadOption);
                    } else if (this.config.option.geo3D) {
                        echartsMaps.registerMap(this.config.option.geo3D.map, this.loadOption);
                    } else {
                        for (let series of this.config.option.series) {
                            if (series.type == 'map') {
                                echartsMaps.registerMap(series.mapType, this.loadOption);
                                return;
                            }
                        }
                    }
                }
            },
            /**
             * 监听areaSource，发生变更时重新载入最新areaSource
             */
            "config.areaSource": {
                handler: function () {
                    echartsMaps.setSource(this.config.areaSource);
                }
            },
            /**
             * 监听数据转换规则，发生变更时，根据最新规则载入数据，并且修改chart的option配置项
             */
            "config.transform": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.proxyData()
                }
            },
            /**
             * 监听data，发生变更时重新载入最新data
             */
            "config.data": {
                deep: true,
                handler: function () {
                    this.loadData()
                    this.calcTrigger()
                    this.dataInitCompleted = true
                }
            },
            /**
             * 监听proxy，发生变更时，重新实例化数据代理，并渲染组件
             */
            "config.proxy": {
                deep: true,
                handler: function () {
                    this.instanceProxy()
                    this.render()
                }
            },
            /**
             * 监听全局状态绑定的触发器，发生变更时重新计算触发器相关的属性
             */
            "config.store.trigger": {
                deep: true,
                handler: function () {
                    this.calcTrigger()
                }
            },
            /**
             * 监听全局状态绑定的数据代理，发生变更时重新渲染组件
             */
            "config.store.proxy": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.render()
                }
            },
            /**
             * 监听从数据转为option的方法，发生变更时载入数据，并且修改chart的option配置项
             */
            "config.handler": {
                deep: true,
                handler: function () {
                    if (!this.dataInitCompleted) {
                        return
                    }
                    this.loadData()
                }
            },
        }
    }
</script>
