                                    <!--<el-option v-for="item in labelPositions"-->
                                               <!--:key="item.value"-->
                                               <!--:label="item.label"-->
                                               <!--:value="item.value">-->
                                    <!--</el-option>-->
                                <!--</el-select>-->
                                <!--<div class="description">位置</div>-->
                            <!--</el-col>-->
                        <!--</el-row>-->
                    </el-collapse-item>
                </template>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'
    import _ from 'lodash'

    export default {
        name: 'HorizontalStackBar',
        props: ['component', 'state'],
        data() {
            return {
                // 可见性
                visibility: [
                    {label: '显示', value: 'visible'},
                    {label: '隐藏', value: 'hidden'},
                ],
                legend: {
                    orient: [
                        {label: '水平', value: 'horizontal'},
                        {label: '垂直', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '左侧', value: 'left'},
                        {label: '居中', value: 'center'},
                        {label: '右侧', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '顶部', value: 'top'},
                        {label: '居中', value: 'middle'},
                        {label: '底部', value: 'bottom'},
                    ],
                    legendX:0
                },
                // 标签位置选项
                labelPositions: [
                    {label: "顶部", value: "top"},
                    {label: "左侧", value: "left"},
                    {label: "右侧", value: "right"},
                    {label: "底部", value: "bottom"},
                    {label: "内部", value: "inside"},
                    {label: "左内侧", value: "insideLeft"},
                    {label: "右内侧", value: "insideRight"},
                    {label: "内侧顶部", value: "insideTop"},
                    {label: "内侧底部", value: "insideBottom"},
                    {label: "左内侧顶部", value: "insideTopLeft"},
                    {label: "左内侧底部", value: "insideBottomLeft"},
                    {label: "右内侧顶部", value: "insideTopRight"},
                    {label: "右内侧底部", value: "insideBottomRight"},
                ]
            }
        },
        created() {
            this.init()
        },
        computed: {},
        methods: {
            init(){
                this.legend.legendX=parseInt(this.component.config.option.legend.left);
            },
            /**
             * 修改图例水平位置
             */
            changeLengXPositon(val){
                this.component.config.option.legend.left=val+'%';
            },
            /**
             * 增加一个系列
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper使用的是对象，此处遍历时，需要使用 this.$set() 方法赋值，
                // 否则无法调用mapper的setter，从而无法触发dom的重新渲染
                this.$set(this.component.config.transform.mapper, 'series' + index, null)
                let seriesName = '系列' + index
                this.component.config.option.legend.data.push(seriesName)
                let color = ColorUtils.randomHex()
                this.component.config.option.series.push({
                    type: 'bar',
                    name: seriesName,
                    stack: 'oneLine',
                    barGap: 1,
                    label: {
                        normal: {
                            show: true,
                            position: 'inside',
                            textStyle: {
                                color: '#fff',
                                fontSize: 16
                            }
                        }
                    },
                    legendHoverLink: false,
                    barWidth: 20,
                    itemStyle: {
                        normal: {
                            color: {
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                type: "linear",
                                global: false,
                                colorStops: [{
                                    offset: 0,
                                    color
                                }, {
                                    offset: 1,
                                    color
                                }]
                            },
                        }
                    },
                    data: null
                })
            },
            /**
             * 删除一个系列
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                // 删除mapper的属性
                let mapper = _.cloneDeep(this.component.config.transform.mapper)
                for (let i = index; i < this.component.config.option.series.length; i++) {
                    mapper['series' + i] = mapper['series' + (i + 1)]
                }
                delete mapper['series' + (this.component.config.option.series.length - 1)]
                this.component.config.transform.mapper = mapper
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
            },
            /**
             * 修改series名
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            }
        },
        components: {},
    }
</script>
