<template>
    <div class="config-interaction">
        <el-collapse v-if="component.config.trigger && component.config.trigger.length" :value="0" accordion>
            <el-collapse-item title="触发器" :name="0">
                <template v-for="(item, i) in component.config.trigger">
                    <br :key="i" v-if="i !== 0">
                    <el-row v-if="item.show !== false">
                        <el-col :span="8" class="tag">{{item.name}}</el-col>
                        <el-col :span="4" class="tag">默认值</el-col>
                        <el-col :span="11" :offset="1">
                            <template v-if="isBoolean(component.config.trigger[i].defaultValue)">
                                <el-switch v-model="component.config.trigger[i].defaultValue"
                                           active-text="true"
                                           inactive-text="false">
                                </el-switch>
                            </template>
                            <template v-else>
                                <el-input v-model="component.config.trigger[i].defaultValue"
                                          size="mini">
                                </el-input>
                            </template>
                        </el-col>
                    </el-row>
                    <el-row v-if="item.show !== false">
                        <el-col :span="2" :offset="6">
                            <el-tooltip placement="left">
                                <div slot="content">
                                    <template v-if="item.mapper && item.mapper.length">
                                        <template v-for="(kv, j) in item.mapper">
                                            {{kv.comment}} <br :key="j"/>
                                        </template>
                                    </template>
                                    <template v-else>
                                        脚本示例:<br/>
                                        (function() {<br/>
                                        &nbsp;&nbsp;if(变量占位符 == 1) {<br/>
                                        &nbsp;&nbsp;&nbsp;&nbsp;return '#ff0000'<br/>
                                        &nbsp;&nbsp;} else {<br/>
                                        &nbsp;&nbsp;&nbsp;&nbsp;return '#ffffff'<br/>
                                        &nbsp;&nbsp;}<br/>
                                        })()<br/>
                                        <br/>
                                        变量占位符分为外部变量和内部变量<br/><br/>
                                        1. 外部变量表示从全局变量中取值，例：<br/>
                                        :{var} 表示取全局变量var的值<br/><br/>
                                        2. 内部变量表示从组件自身数据中取值，例：<br/>
                                        数据为数组 [{attr: 5}, {attr: 6}] 时<br/>
                                        @{[1].attr} 表示取第2个元素的attr属性值6；<br/>
                                        数据为对象 {city: {code: 21}} 时<br/>
                                        @{city.code} 表示取city中的code的值21
                                    </template>
                                </div>
                                <i class="icon iconfont icon-help"></i>
                            </el-tooltip>
                        </el-col>
                        <el-col :span="16">
                            <el-input v-model="component.config.trigger[i].binder"
                                      type="textarea"
                                      :rows="3"
                                      size="mini"
                                      placeholder="触发规则"
                                      resize="none">
                            </el-input>
                        </el-col>
                    </el-row>
                </template>
            </el-collapse-item>
        </el-collapse>
        <template v-if="component.config.event">
            <el-collapse :value="0" accordion>
                <template v-for="(item, i) in component.config.event">
                    <el-collapse-item :key="i" :title="item.comment" :name="i">
                        <el-row>
                            <el-col :span="7" class="tag">属性</el-col>
                            <el-col :span="7" :offset="1" class="tag">绑定状态</el-col>
                            <el-col :span="8" :offset="1" class="tag">属性说明</el-col>
                        </el-row>
                        <template v-for="(binder, j) in item.binders">
                            <el-row :key="j">
                                <el-col :span="7" class="tag">
                                    <template v-if="!binder.prop">
                                        <el-input v-model="binder.name" size="mini"></el-input>
                                    </template>
                                    <template v-else>{{binder.name}}</template>
                                </el-col>
                                <el-col :span="7" :offset="1">
                                    <el-input v-model="binder.state" size="mini" @change="registerState"></el-input>
                                </el-col>
                                <el-col :span="8" :offset="1" class="tag">
                                    <template v-if="!binder.prop">
                                        <el-button type="text"
                                                   class="icon iconfont icon-minus"
                                                   size="mini"
                                                   @click="deleteBinder(i, j)"></el-button>
                                    </template>
                                    <template v-else>{{binder.comment}}</template>
                                </el-col>
                            </el-row>
                        </template>
                        <div class="add-binder">
                            <el-button type="primary" size="mini" @click="addBinder(i)">新增属性绑定</el-button>
                        </div>
                    </el-collapse-item>
                </template>
            </el-collapse>
        </template>
        <div v-else class="no-binder">
            无参数
        </div>
    </div>
</template>

<script>
    import CONST from '../../../config/const'

    export default {
        name: 'Interaction',
        props: ['component', 'state'],
        data() {
            return {}
        },
        created() {
        },
        methods: {
            /**
             * 添加一条绑定规则
             * @param index
             */
            addBinder(index) {
                this.component.config.event[index].binders.push({
                    name: null,
                    state: null
                })
            },
            /**
             * 删除一条绑定规则
             * @param index
             */
            deleteBinder(i, j) {
                this.component.config.event[i].binders.splice(j, 1)
            },
            /**
             * 获取变量类型
             */
            isBoolean(val) {
                return typeof val === "boolean"
            },
            /**
             * 注册全局状态
             */
            registerState(event) {
                this.$store.commit(CONST.MUTATIONS.REGISTER, {
                    store: event,
                })
                // 遍历所有组件，获取需要注册到全局的状态
                let components = this.$root.$children[0].$children[0].config.components
                let existState = {}
                for (let key in components) {
                    let config = components[key].config
                    if (!config || !config.event || !config.event.length) {
                        continue
                    }
                    for (let j = 0; j < config.event.length; j++) {
                        let event = config.event[j]
                        if (!event || !event.binders || !event.binders.length) {
                            continue
                        }
                        for (let k = 0; k < event.binders.length; k++) {
                            let binder = event.binders[k]
                            if (binder.state) {
                                existState[binder.state] = binder.state
                            }
                        }
                    }
                }
                // 对比组件的状态和全局状态，将全局状态中无效的删除
                let interaction = this.$store.state.screen.design.interaction
                for (let key in interaction) {
                    if (!existState[key]) {
                        this.$store.commit(CONST.MUTATIONS.DELETE, {
                            store: key
                        })
                    }
                }
            }
        },
        components: {}
    }
</script>

