import Vue from 'vue'
import Vuex from 'vuex'
import _ from 'lodash'
import CONST from '../config/const'
import Category from '../config/category'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        // 身份验证token
        token: "eyJ0eXBlIjoiSldUIiwiYWxnIjoiSFMyNTYifQ.eyJpZCI6IjEiLCJhY2NvdW50IjoiYWRtaW4iLCJuYW1lIjoi57O757uf566h55CG5ZGYIiwiZXhwIjo3NTQ1ODkyNzA0LCJuYmYiOjE1NDU4OTI3NjR9.PU5dQ_9w3Tfv0-Le22ptGsAZFt2qPuu5-X_e0bd8nJM",

        // 可视化功能
        screen: {
            design: {
                // 组件菜单内容
                menu: Category,
                // 储存大屏所有的联动状态
                interaction: {}
            }
        }
    },
    mutations: {
        // 组件注册和状态绑定
        [CONST.MUTATIONS.REGISTER](state, payload) {
            let storeProp = CONST.INTERACTION.VUEX_SPACE + payload.store
            let store = _.get(state, storeProp)
            if (store === undefined) {
                let item = {value: null, observer: []}
                if (payload.comp) {
                    item.observer.push({comp: payload.comp, prop: payload.prop})
                }
                _.set(state, storeProp, item)
            } else {
                if (!store.observer || !store.observer.length) {
                    store.observer = []
                }
                if (payload.comp && payload.prop) {
                    let exist = false
                    for (let i = 0; i < store.observer.length; i++) {
                        let ob = store.observer[i]
                        if (ob.comp === payload.comp && ob.prop === payload.prop) {
                            exist = true
                        }
                    }
                    if (!exist) {
                        store.observer.push({comp: payload.comp, prop: payload.prop})
                    }
                    _.set(payload.comp, payload.prop, store.value)
                }
            }
        },
        // 状态修改
        [CONST.MUTATIONS.MODIFY](state, payload) {
            let storeProp = CONST.INTERACTION.VUEX_SPACE + payload.store
            let store = _.get(state, storeProp)
            if (store === undefined) {
                return
            }
            _.set(state, storeProp + '.value', payload.value)
            if (!store.observer || !store.observer.length) {
                return
            }
            for (let i = 0; i < store.observer.length; i++) {
                let ob = store.observer[i]
                if (_.get(ob.comp, ob.prop) === payload.value) {
                    continue
                }
                _.set(ob.comp, ob.prop, payload.value)
            }
        },
        // 状态获取
        [CONST.MUTATIONS.GET](state, payload) {
            let storeProp = CONST.INTERACTION.VUEX_SPACE + payload.prop
            return _.get(state, storeProp + '.value')
        },
        // 状态保存
        [CONST.MUTATIONS.COMMIT](state, payload) {
            let storeProp = CONST.INTERACTION.VUEX_SPACE + payload.store
            let store = _.get(state, storeProp)
            if (store === undefined) {
                return
            }
            _.set(state, storeProp, payload.value)
        },
        // 状态删除
        [CONST.MUTATIONS.DELETE](state, payload) {
            let storeProp = CONST.INTERACTION.VUEX_SPACE + payload.store
            let store = _.get(state, storeProp)
            if (store === undefined) {
                return
            }
            if (!store.observer.length) {
                _.unset(state, storeProp)
            }
        },
        // 组件注销
        [CONST.MUTATIONS.UNREGISTER](state, payload) {
            let comp = payload.comp
            let store = comp.config.store
            for (let key in store) {
                let obj = _.get(state.screen.design.interaction, key)
                let observer = obj.observer
                for (let i = observer.length - 1; i >= 0; i--) {
                    let ob = observer[i]
                    if (ob.comp == comp) {
                        observer.splice(i, 1)
                    }
                }
            }
        },
        // TOKEN保存
        [CONST.MUTATIONS.SET_TOKEN](state, payload) {
            _.set(state, "token", payload)
        },
    }
})
