<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'RadiusBar',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'rgba(0,0,0,0)',
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        },
                        backgroundColor: '#2F3943',
                        borderColor: 'rgba(0,0,0,0)',
                        borderWidth: 1,
                        padding: [5, 10, 5, 10],
                        textStyle: {
                            fontSize: 14,
                            color: '#ffffff',
                        },
                        extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)'
                    },
                    grid: {
                        top: 50,
                        left: 50,
                        right: 50,
                        bottom: 50
                    },
                    legend: {
                        show: false,
                    },
                    polar: {},
                    angleAxis: {
                        min: function (value) {
                            return value.min >= 1 ? value.min - value.max / 3 : 0
                        },
                        max: function (value) {
                            return value.max * 4 / 3
                        },
                        axisLine: {
                            show: false,
                            lineStyle: {
                                color: "#737985",
                                width: 1,
                            }
                        },
                        axisTick: {
                            show: false,
                        },
                        axisLabel: {
                            show: false,
                            interval: 0,
                            textStyle: {
                                fontSize: 14,
                                color: "#dddddd",
                            }
                        },
                        splitLine: {
                            show: false,
                            lineStyle: {
                                width: 1,
                                color: '#737985',
                                type: 'dashed'
                            }
                        },
                    },
                    radiusAxis: {
                        type: 'category',
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: "#737985",
                                width: 1,
                            }
                        },
                        axisTick: {
                            show: false,
                            alignWithLabel: true
                        },
                        axisLabel: {
                            show: true,
                            interval: 0,
                            textStyle: {
                                fontSize: 14,
                                color: "#dddddd",
                            }
                        },
                        splitLine: {
                            show: false,
                            lineStyle: {
                                width: 1,
                                color: ['#737985'],
                                type: 'dashed'
                            }
                        },
                    },
                    series: [{
                        name: '默认',
                        type: 'bar',
                        barWidth: 5,
                        coordinateSystem: 'polar',
                        itemStyle: {
                            normal: {
                                color: {
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    colorStops: [{
                                        offset: 0,
                                        color: 'rgba(41, 121, 255, 1)'
                                    }, {
                                        offset: 1,
                                        color: 'rgba(41, 121, 255, 0.1)'
                                    }],
                                },
                                barBorderRadius: 5,
                                shadowBlur: 5,
                                shadowColor: "rgba(255, 255, 255, 0.8)",
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '一月', value: 22},
                    {name: '二月', value: 23},
                    {name: '三月', value: 51},
                    {name: '四月', value: 81},
                    {name: '五月', value: 67},
                    {name: '六月', value: 78},
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'value', comment: '数据值', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null,
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let nameProp = this.config.transform.mapper['name'] || 'name'
                let valueProp = this.config.transform.mapper['value'] || 'value'
                let axis = []
                let data = []
                for (let i = 0; i < this.config.data.length; i++) {
                    axis.push(this.config.data[i][nameProp])
                    data.push({
                        name: this.config.data[i][nameProp],
                        value: parseInt(this.config.data[i][valueProp])
                    })
                }
                this.config.option.radiusAxis.data = axis
                this.config.option.series[0].data = data
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
