<template>
    <el-collapse value="0" accordion>
        <!-- 基础属性 -->
        <el-collapse-item title="基础属性" name="0">
            <el-row>
                <el-col :span="8" class="tag">尺寸</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">位置</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">横坐标</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">纵坐标</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">其他</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description">透明度</div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!-- 样式 -->
        <el-collapse-item title="样式" name="1">
            <el-collapse value="0">
                <el-collapse-item title="图形" name="0">
                    <el-row>
                        <el-col :span="8" class="tag">排列</el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.series[0].sort"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in sort"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                            <div class="description">排序</div>
                        </el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.series[0].funnelAlign"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in funnelAlign"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                            <div class="description">位置</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">值域</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].min"
                                             size="mini"
                                             controls-position="right"
                                             :step="1">
                            </el-input-number>
                            <div class="description">最小值</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].max"
                                             size="mini"
                                             controls-position="right"
                                             :step="1">
                            </el-input-number>
                            <div class="description">最大值</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">最小宽度</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].minSize"
                                             size="mini"
                                             controls-position="right"
                                             :step="1">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">间隔</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].gap"
                                             size="mini"
                                             controls-position="right"
                                             :step="1">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">边框</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].itemStyle.normal.borderWidth"
                                             size="mini"
                                             controls-position="right"
                                             :min="0"
                                             :step="1">
                            </el-input-number>
                            <div class="description">线宽</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.series[0].itemStyle.normal.borderColor"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">颜色</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">阴影</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].itemStyle.normal.shadowOffsetX"
                                             size="mini"
                                             controls-position="right"
                                             :min="0"
                                             :step="1">
                            </el-input-number>
                            <div class="description">x偏移</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.series[0].itemStyle.normal.shadowOffsetY"
                                             size="mini"
                                             controls-position="right"
                                             :min="0"
                                             :step="1">
                            </el-input-number>
                            <div class="description">y偏移</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" :offset="8">
                            <el-input-number v-model="component.config.option.series[0].itemStyle.normal.shadowBlur"
                                             size="mini"
                                             controls-position="right"
                                             :min="0"
                                             :step="1">
                            </el-input-number>
                            <div class="description">范围</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.series[0].itemStyle.normal.shadowColor"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">颜色</div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="配色" name="1">
                    <el-row>
                        <el-col :span="4">
                            <el-button type="text"
                                       size="mini"
                                       @click="addColor">增加
                            </el-button>
                        </el-col>
                        <el-col :span="4">
                            <el-button type="text"
                                       size="mini"
                                       @click="minusColor">减少
                            </el-button>
                        </el-col>
                    </el-row>
                    <el-row>
                        <template v-for="(color, i) in component.config.option.color">
                            <el-col :key="i" :span="3">
                                <el-color-picker v-model="component.config.option.color[i]"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                            </el-col>
                        </template>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="文本" name="2">
                    <el-row>
                        <el-col :span="8" class="tag">文本</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.series[0].label.show"></el-checkbox>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseBar',
        props: ['component', 'state'],
        data() {
            return {
                funnelAlign: [
                    {label: '居左', value: 'left'},
                    {label: '居中', value: 'center'},
                    {label: '居右', value: 'right'},
                ],
                sort: [
                    {label: '顺序', value: 'ascending'},
                    {label: '倒序', value: 'descending'},
                ]
            }
        },
        methods: {
            /**
             * 增加主题颜色
             */
            addColor() {
                this.component.config.option.color.push(ColorUtils.randomHex())
            },
            /**
             * 减少主题颜色
             */
            minusColor() {
                this.component.config.option.color.pop()
            },
        },
        components: {},
    }
</script>
