<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'
    import echarts from "echarts"

    export default {
        name: 'MultipleIndicators',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    title: {
                        show: false,
                        text: '多指标并列图',
                        x: 'center',
                        y: 'top',
                        textStyle: {
                            color: '#ffffff',
                            fontSize: 16,
                            fontWeight: 'normal',
                        },

                    },
                    dataset: {
                        dimensions: null,
                        source: null,
                    },
                    backgroundColor: 'rgba(0,0,0,0)',
                    tooltip: {
                        show: true,
                        trigger: 'axis',
                        backgroundColor: 'rgba(255,255,255,0.1)',
                        axisPointer: {
                            type: 'shadow',
                            label: {
                                show: true,
                                backgroundColor: '#7B7DDC'
                            }
                        },
                        textStyle: {
                            color: '#ffffff',
                            fontSize: 16,
                            fontWeight: 'normal',
                        },
                    },
                    legend: {
                        show: true,
                        data: ['已贯通', '计划贯通', '贯通率',],
                        textStyle: {
                            fontSize: 12,
                            color: '#ffffff'
                        },
                        x: 'center',
                        y: 'bottom',
                    },
                    grid: {
                        top: 50,
                        left: 50,
                        right: 50,
                        bottom: 50
                    },
                    xAxis: {
                        type: 'category',
                        show: true,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff'
                            }
                        },
                        axisTick: {
                            show: false,
                        },
                        axisLabel: {
                            show: true,
                            textStyle: {
                                fontSize: 12,
                                color: '#ffffff'
                            }

                        }
                    },
                    yAxis: [{
                        show: true,
                        min: 0,
                        max: undefined,
                        splitLine: {
                            show: false,
                            lineStyle: {
                                color: '#ffffff'
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                            }
                        },
                        axisLabel: {
                            show: true,
                            textStyle: {
                                fontSize: 12,
                                color: '#ffffff'
                            },
                            formatter: '{value} ',
                        },
                        axisTick: {
                            show: false,
                        },
                    }, {
                        show: true,
                        min: 0,
                        max: undefined,
                        splitLine: {
                            show: false,
                            lineStyle: {
                                color: '#ffffff'
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                            }
                        },
                        axisLabel: {
                            show: true,
                            textStyle: {
                                fontSize: 12,
                                color: '#ffffff'
                            },
                            formatter: '{value} ',
                        },
                        axisTick: {
                            show: false,
                        },
                    }],
                    series: [{
                        name: '已贯通',
                        type: 'bar',
                        barWidth: '40%',
                        itemStyle: {
                            normal: {
                                isSingle: true,
                                color: '#0286ff'
                            }
                        }
                    }, {
                        name: '计划贯通',
                        type: 'bar',
                        barGap: '-100%',
                        barWidth: '40%',
                        itemStyle: {
                            normal: {
                                isSingle: true,
                                color: 'rgba(62, 158, 255, 1)'
                            }
                        },
                        z: -12
                    }, {
                        name: '贯通率',
                        type: 'line',
                        smooth: true,
                        showSymbol: true,
                        symbol: 'emptyCircle',
                        symbolSize: 8,
                        yAxisIndex: 1,
                        itemStyle: {
                            normal: {
                                isSingle: true,
                                color: '#F02FC2'
                            },
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {x: '市区', y: 4600, z: 18092, perc: 0.25},
                    {x: '万州', y: 5000, z: 20728, perc: 0.24},
                    {x: '江北', y: 5500, z: 24045, perc: 0.23},
                    {x: '南岸', y: 6500, z: 28348, perc: 0.23},
                    {x: '北碚', y: 7500, z: 32808, perc: 0.23},
                    {x: '綦南', y: 8500, z: 36097, perc: 0.24},
                    {x: '长寿', y: 9900, z: 39867, perc: 0.25}
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'value', comment: '系列值0', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        x: null,
                        y: null,
                        z: null,
                        perc: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let dimensions = []
                for (let key in this.config.transform.mapper) {
                    let dim = this.config.transform.mapper[key] || key
                    if (!this.config.data[0].hasOwnProperty(dim)) {
                        dim = null
                    }
                    dimensions.push(dim)
                }
                this.config.option.dataset = {
                    dimensions: dimensions,
                    source: this.config.data
                }
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
