<template>
    <el-collapse value="0" accordion>
        <!-- 基础属性 -->
        <el-collapse-item title="基础属性" name="0">
            <el-row>
                <el-col :span="8" class="tag">尺寸</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">位置</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">横坐标</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">纵坐标</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">其他</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description">透明度</div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!-- 样式 -->
        <el-collapse-item title="样式" name="1">
            <el-collapse value="0" accordion>
                <el-collapse-item title="基础" name="0" v-if="component.config.option.grid">
                    <el-row>
                        <el-col :span="8" class="tag">边距</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.grid.left"
                                             size="mini"
                                             controls-position="right" :min="0" :step="1">
                            </el-input-number>
                            <div class="description">左</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.grid.right"
                                             size="mini"
                                             controls-position="right" :min="0" :step="1">
                            </el-input-number>
                            <div class="description">右</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" :offset="8">
                            <el-input-number v-model="component.config.option.grid.top"
                                             size="mini"
                                             controls-position="right" :min="0" :step="1">
                            </el-input-number>
                            <div class="description">上</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.grid.bottom"
                                             size="mini"
                                             controls-position="right" :min="0" :step="1">
                            </el-input-number>
                            <div class="description">下</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">背景</el-col>
                        <el-col :span="8">
                            <el-color-picker
                                    v-model="component.config.option.backgroundColor"
                                    size="mini"
                                    show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="标题" name="1">
                    <el-row>
                        <el-col :span="7" class="tag">显示</el-col>
                        <el-col :span="8">
                            <el-switch
                                    v-model="component.config.option.title.show">
                            </el-switch>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">名称</el-col>
                        <el-col :span="17">
                            <el-input
                                v-model="component.config.option.title.text"
                                size="mini"
                                controls-position="right">
                            </el-input>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">颜色</el-col>
                        <el-col :span="17">
                            <el-color-picker
                                v-model="component.config.option.title.textStyle.color"
                                size="mini"
                                show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">字号</el-col>
                        <el-col :span="17">
                            <el-input-number
                                v-model="component.config.option.title.textStyle.fontSize"
                                size="mini"
                                controls-position="right"
                                :min="12">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">加粗</el-col>
                        <el-col :span="17">
                            <el-select v-model="component.config.option.title.textStyle.fontWeight"
                                       size="mini">
                                <el-option
                                        v-for="item in fontWeight"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">位置</el-col>
                        <el-col :span="17">
                            <el-row style="padding:0;">
                                <el-col :span="12">
                                    <el-select v-model="component.config.option.title.x"
                                               size="mini">
                                        <el-option
                                                v-for="item in positionType.alignHorizontal"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                    <div class="description">横坐标</div>
                                </el-col>
                                <el-col :span="12">
                                    <el-select v-model="component.config.option.title.y"
                                               size="mini">
                                        <el-option
                                                v-for="item in positionType.alignVertical"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                    <div class="description">横坐标</div>
                                </el-col>
                            </el-row>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="图例" name="2">
                    <el-row>
                        <el-col :span="7" class="tag">显示</el-col>
                        <el-col :span="8">
                            <el-switch
                                    v-model="component.config.option.legend.show">
                            </el-switch>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">颜色</el-col>
                        <el-col :span="17">
                            <el-color-picker
                                v-model="component.config.option.legend.textStyle.color"
                                size="mini"
                                show-alpha></el-color-picker>

                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">字号</el-col>
                        <el-col :span="17">
                            <el-input-number
                                v-model="component.config.option.legend.textStyle.fontSize"
                                size="mini"
                                controls-position="right"
                                :min="12">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">位置</el-col>
                        <el-col :span="17">
                            <el-row style="padding:0;">
                                <el-col :span="12">
                                    <el-select v-model="component.config.option.legend.x"
                                               size="mini">
                                        <el-option
                                                v-for="item in positionType.alignHorizontal"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                    <div class="description">横坐标</div>
                                </el-col>
                                <el-col :span="12">
                                    <el-select v-model="component.config.option.legend.y"
                                               size="mini">
                                        <el-option
                                                v-for="item in positionType.alignVertical"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                    <div class="description">横坐标</div>
                                </el-col>
                            </el-row>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="提示" name="3">
                    <el-row>
                        <el-col :span="7" class="tag">显示</el-col>
                        <el-col :span="8">
                            <el-switch
                                    v-model="component.config.option.tooltip.show">
                            </el-switch>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">背景</el-col>
                        <el-col :span="8">
                            <el-color-picker
                                v-model="component.config.option.tooltip.backgroundColor"
                                size="mini"
                                show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">触发类型</el-col>
                        <el-col :span="17">
                            <el-select
                                v-model="component.config.option.tooltip.trigger"
                                style="width:100%;"
                                @change="changeToolTip" >
                                <el-option
                                        v-for="item in tooltipType.trigger"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <template v-if="component.config.option.tooltip.trigger == 'axis'">
                        <el-row>
                            <el-col :span="7" class="tag">指示类型</el-col>
                            <el-col :span="17">
                                <el-select
                                    v-model="component.config.option.tooltip.axisPointer.type"
                                    style="width:100%;" >
                                    <el-option
                                            v-for="item in tooltipType.axisPointer"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="7" class="tag">坐标轴选中</el-col>
                            <el-col :span="17">
                                <el-row>
                                    <el-col :span="16">
                                        <el-switch
                                                v-model="component.config.option.tooltip.axisPointer.label.show"
                                                size="mini">
                                        </el-switch>
                                    </el-col>
                                    <el-col :span="8" class="tag">是否显示</el-col>
                                </el-row>
                                <el-row v-if="component.config.option.tooltip.axisPointer.label.show">
                                    <el-col :span="16">
                                        <el-color-picker
                                            v-model="component.config.option.tooltip.axisPointer.label.backgroundColor"
                                            size="mini"
                                            show-alpha></el-color-picker>
                                    </el-col>
                                    <el-col :span="8" class="tag">背景</el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                    </template>
                    <el-row>
                        <el-col :span="7" class="tag">颜色</el-col>
                        <el-col :span="17">
                            <el-color-picker
                                v-model="component.config.option.tooltip.textStyle.color"
                                size="mini"
                                show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">字号</el-col>
                        <el-col :span="17">
                            <el-input-number
                                v-model="component.config.option.tooltip.textStyle.fontSize"
                                size="mini"
                                controls-position="right"
                                :min="12">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">加粗</el-col>
                        <el-col :span="17">
                            <el-select v-model="component.config.option.tooltip.textStyle.fontWeight">
                                <el-option
                                        v-for="item in fontWeight"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>

        </el-collapse-item>

        <!-- x轴 -->
        <el-collapse-item title="x轴" name="2">
            <el-collapse value="0">
                <!-- 标签 -->
                <el-collapse-item title="标签" name="0">
                    <el-row>
                        <el-col :span="8" class="tag">文本</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.xAxis[0].axisLabel.show">
                                {{component.config.option.xAxis[0].axisLabel.show ? "显示" : "隐藏"}}
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">字号</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.xAxis[0].axisLabel.fontSize"
                                             size="mini"
                                             controls-position="right"
                                             :min="12">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">旋转</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.xAxis[0].axisLabel.rotate"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="4">
                            <el-color-picker v-model="component.config.option.xAxis[0].axisLabel.color"
                                             size="mini" show-alpha></el-color-picker>
                        </el-col>
                        <el-col :span="4">
                            <el-button
                                    @click="resetValue('component.config.option.xAxis[0].axisLabel.color', 'xAxisLabelColor')"
                                    type="text" size="mini">重置
                            </el-button>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- 轴线 -->
                <el-collapse-item title="轴线" name="1">
                    <el-row>
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="4">
                            <el-color-picker v-model="component.config.option.xAxis[0].axisLine.lineStyle.color"
                                             size="mini" show-alpha></el-color-picker>
                        </el-col>
                        <el-col :span="4">
                            <el-button
                                    @click="resetValue('component.config.option.xAxis[0].axisLine.lineStyle.color', 'xAxisLineColor')"
                                    type="text" size="mini">重置
                            </el-button>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>

        <!-- y轴 -->
        <el-collapse-item title="y轴" name="3">
            <el-row>
                <el-col :span="8" class="tag">标签字色</el-col>
                <el-col :span="4">
                    <el-color-picker v-model="component.config.option.yAxis[0].axisLabel.color"
                                     size="mini" show-alpha></el-color-picker>
                </el-col>
                <el-col :span="4">
                    <el-button
                            @click="resetValue('component.config.option.yAxis[0].axisLabel.color', 'yAxisLabelColor')"
                            type="text" size="mini">重置
                    </el-button>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">轴线色</el-col>
                <el-col :span="4">
                    <el-color-picker v-model="component.config.option.yAxis[0].axisLine.lineStyle.color"
                                     size="mini" show-alpha></el-color-picker>
                </el-col>
                <el-col :span="4">
                    <el-button
                            @click="resetValue('component.config.option.yAxis[0].axisLine.lineStyle.color', 'yAxisLineColor')"
                            type="text" size="mini">重置
                    </el-button>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">值范围</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.yAxis[0].min"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">最小值</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.yAxis[0].max"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">最大值</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">分隔线</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.yAxis[0].splitLine.show">
                        {{component.config.option.yAxis[0].splitLine.show ? "显示" : "隐藏"}}
                    </el-checkbox>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">刻度</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.yAxis[0].axisLabel.show">
                        {{component.config.option.yAxis[0].axisLabel.show ? "显示" : "隐藏"}}
                    </el-checkbox>
                </el-col>
            </el-row>
        </el-collapse-item>

        <!-- 系列 -->
        <el-collapse-item title="数据系列" name="4">
            <el-row>
                <el-col :span="4">
                    <el-button type="text" size="mini" @click="addSeries">增加</el-button>
                </el-col>
            </el-row>
            <el-collapse value="0">

                <el-collapse-item
                    v-for="(series,index) in component.config.option.series"
                    :title="series.name"
                    :name="index.toString()"
                    :key="index">

                    <el-row v-if="index > 0">
                        <el-col :span="4">
                            <el-button type="text"
                                       size="mini"
                                       @click="removeSeries(index)">删除
                            </el-button>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">名称</el-col>
                        <el-col :span="16">

                            <el-input
                                v-model="component.config.option.series[index].name"
                                size="mini"
                                controls-position="right"
                                @change="changeSeriesName">
                            </el-input>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">柱子宽度</el-col>
                        <el-col :span="8">
                            <el-input-number
                                :value="percentToNumber(component.config.option.series[index].barWidth)"
                                @change="setBarWidth($event,index)"
                                size="mini"
                                controls-position="right" :min="0" :max="1" :step="0.1">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">圆角</el-col>
                        <el-col :span="4">
                            <el-input-number
                                v-model="component.config.option.series[index].itemStyle.normal.barBorderRadius"
                                size="mini"
                                controls-position="right"
                                :min="0"></el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">背景色</el-col>
                        <el-col :span="16">
                            <el-row style="padding:3px 0px;">
                                <el-col :span="16" class="tag">
                                    <el-switch
                                            v-model="component.config.option.series[index].itemStyle.normal.isSingle"
                                            @change="changeBarFill($event,index)">
                                    </el-switch>
                                </el-col>
                                <el-col :span="8" class="tag">是否纯色</el-col>
                            </el-row>
                            <el-row style="padding:3px 0px;">
                                <el-col :span="24">
                                    <template v-if="component.config.option.series[index].itemStyle.normal.isSingle">
                                        <el-color-picker
                                                v-model="component.config.option.series[index].itemStyle.normal.color"
                                                size="mini" show-alpha></el-color-picker>
                                    </template>
                                    <template v-else>
                                        <el-row style="padding:3px 0px;">
                                            <el-col :span="16">
                                                <el-color-picker
                                                    v-model="barFill.gradient[index].colorStops[0].color"
                                                    size="mini" show-alpha
                                                    @change="changeBarFill($event,index)"></el-color-picker>

                                            </el-col>
                                            <el-col :span="8" class="tag">起始色</el-col>
                                        </el-row>
                                        <el-row style="padding:3px 0px;">
                                            <el-col :span="16">
                                                <el-color-picker
                                                    v-model="barFill.gradient[index].colorStops[1].color"
                                                    size="mini" show-alpha
                                                    @change="changeBarFill($event,index)"></el-color-picker>
                                            </el-col>
                                            <el-col :span="8" class="tag">终止色</el-col>
                                        </el-row>
                                    </template>
                                </el-col>
                            </el-row>
                            <el-col :span="4">
                                <el-button
                                        @click="resetBarColor(index)"
                                        type="text" size="mini">重置
                                </el-button>
                            </el-col>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">值标签</el-col>
                        <el-col :span="16">
                            <el-row style="padding:3px 0px;">
                                <el-col :span="16">
                                    <el-select v-model="component.config.option.series[index].label.normal.position"
                                               placeholder="请选择"
                                               size="mini">
                                        <el-option v-for="item in labelPositions"
                                                   :key="item.value"
                                                   :label="item.label"
                                                   :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <el-col :span="8" class="tag">位置</el-col>
                            </el-row>
                            <el-row style="padding:3px 0px;">
                                <el-col :span="16">
                                    <el-input-number
                                            v-model="component.config.option.series[index].label.normal.textStyle.fontSize"
                                            size="mini"
                                            controls-position="right"
                                            :min="12">
                                    </el-input-number>
                                </el-col>
                                <el-col :span="8" class="tag">字号</el-col>
                            </el-row>
                            <el-row style="padding:3px 0px;">
                                <el-col :span="8">
                                    <el-color-picker
                                            v-model="component.config.option.series[index].label.normal.textStyle.color"
                                            size="mini" show-alpha></el-color-picker>
                                </el-col>
                                <el-col :span="8">
                                    <el-button
                                            @click="resetValue(`component.config.option.series[${index}].label.normal.textStyle.color`, `labelColor[${index}]`)"
                                            type="text" size="mini">重置
                                    </el-button>
                                </el-col>
                                <el-col :span="8" class="tag">颜色</el-col>
                            </el-row>
                        </el-col>
                    </el-row>


                    <el-row>
                        <el-col :span="8" class="tag">图表标线</el-col>
                        <el-col :span="16">
                            <el-row style="padding:3px 0px;">
                                <el-col :span="16" class="tag">
                                    <el-switch
                                            v-model="component.config.option.markLines[index].show"
                                            @change="changeMarkLineFlag($event,index)">
                                    </el-switch>
                                </el-col>
                                <el-col :span="8" class="tag">是否显示</el-col>
                            </el-row>
                            <template v-if="component.config.option.markLines[index].show">
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-input-number
                                                v-model="component.config.option.markLines[index].markLine.data[0].yAxis"
                                                size="mini"
                                                controls-position="right"
                                                :min="0">
                                        </el-input-number>
                                    </el-col>
                                    <el-col :span="8" class="tag">标线值</el-col>
                                </el-row>
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-input-number
                                                v-model="component.config.option.markLines[index].markLine.lineStyle.opacity"
                                                size="mini"
                                                controls-position="right" :min="0" :max="1" :step="0.05">
                                        </el-input-number>
                                    </el-col>
                                    <el-col :span="8" class="tag">透明度</el-col>
                                </el-row>
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-input-number
                                                v-model="component.config.option.markLines[index].markLine.lineStyle.width"
                                                size="mini"
                                                controls-position="right"
                                                :min="0">
                                        </el-input-number>
                                    </el-col>
                                    <el-col :span="8" class="tag">标线宽</el-col>
                                </el-row>
                            </template>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'
    import _ from 'lodash'

    export default {
        name: "BaseBar",
        props: ["component"],
        data() {
            return {
                default: {
                    // 柱子颜色
                    barColor: null,
                    // 柱子上的标签字色
                    labelColor: null,
                    // x轴标签字色
                    xAxisLabelColor: null,
                    // x轴线颜色
                    xAxisLineColor: null,
                    // y轴标签字色
                    yAxisLabelColor: null,
                    // x轴线颜色
                    yAxisLineColor: null,
                },
                // 可见
                visibility: [
                    {label: '显示', value: 'visible'},
                    {label: '隐藏', value: 'hidden'},
                ],
                // 柱子填充状态和填充色
                barFill: {
                    single: [],
                    gradient: [],
                },
                // 标签位置选项
                labelPositions: [
                    {label: "顶部", value: "top"},
                    {label: "左侧", value: "left"},
                    {label: "右侧", value: "right"},
                    {label: "底部", value: "bottom"},
                    {label: "内部", value: "inside"},
                    {label: "左内侧", value: "insideLeft"},
                    {label: "右内侧", value: "insideRight"},
                    {label: "内侧顶部", value: "insideTop"},
                    {label: "内侧底部", value: "insideBottom"},
                    {label: "左内侧顶部", value: "insideTopLeft"},
                    {label: "左内侧底部", value: "insideBottomLeft"},
                    {label: "右内侧顶部", value: "insideTopRight"},
                    {label: "右内侧底部", value: "insideBottomRight"},
                ],
                //字体加粗选项
                fontWeight: [
                    {label: '正常', value: 'normal'},
                    {label: '加粗', value: 'bold'},
                    {label: '变细', value: 'lighter'},
                ],
                //tooltip相关分类
                tooltipType: {
                    trigger: [
                        {label: '数据项图形触发', value: 'item'},
                        {label: '坐标轴触发', value: 'axis'}
                    ],
                    axisPointer: [
                        {label: '直线指示器', value: 'line'},
                        {label: '阴影指示器', value: 'shadow'},
                        {label: '十字准星指示器', value: 'cross'},
                        {label: '无指示器', value: 'none'}
                    ]
                },
                //位置分类
                positionType: {
                    orient: [
                        {label: '水平', value: 'horizontal'},
                        {label: '垂直', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '左侧', value: 'left'},
                        {label: '居中', value: 'center'},
                        {label: '右侧', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '顶部', value: 'top'},
                        {label: '居中', value: 'middle'},
                        {label: '底部', value: 'bottom'},
                    ]
                },
            }
        },
        created() {
            this.init()
        },
        methods: {
            init() {
                // 缓存部分颜色的默认值，方便重置颜色时候恢复
                let barColor = this.component.config.option.series.map(ele => {
                    return {color: ele.itemStyle.normal.color, isSingle: ele.itemStyle.normal.isSingle}
                })
                let labelColor = this.component.config.option.series.map(ele => ele.label.normal.textStyle.color ? ele.label.normal.textStyle.color : '#ffc72b')
                this.default = {
                    barColor, labelColor,
                    xAxisLabelColor: this.component.config.option.xAxis[0].axisLabel.color,
                    xAxisLineColor: this.component.config.option.xAxis[0].axisLine.lineStyle.color,
                    yAxisLabelColor: this.component.config.option.yAxis[0].axisLabel.color,
                    yAxisLineColor: this.component.config.option.yAxis[0].axisLine.lineStyle.color,
                }
                let single = []
                let gradient = []
                barColor.forEach(ele => {
                    let color = ''
                    let gradientItem = {
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        type: "linear",
                        global: false,
                        colorStops: [
                            {offset: 0, color: "#199cff"},
                            {offset: 1, color: "#0259aa"}
                        ]
                    }
                    if (ele.isSingle) {
                        color = ele.color
                    } else {
                        gradientItem = ele.color
                    }
                    single.push(color)
                    gradient.push(gradientItem)
                })
                this.barFill = {
                    single, gradient
                }
            },
            /**
             * 修改柱子填充色，0-纯色 / 1-渐变
             */
            changeBarFill(value, index) {
                if (!this.component.config.option.series[index].itemStyle.normal.isSingle) {
                    this.$set(this.component.config.option.series[index].itemStyle.normal, 'color', this.barFill.gradient[index])
                } else {
                    this.$set(this.component.config.option.series[index].itemStyle.normal, 'color', this.barFill.single[index])
                }
            },
            /**
             * 重置柱子的颜色
             */
            resetBarColor(index) {
                this.$set(this.component.config.option.series[index].itemStyle.normal, 'color', this.default.barColor[index].color)
                this.$set(this.component.config.option.series[index].itemStyle.normal, 'isSingle', this.default.barColor[index].isSingle)
            },
            /**
             * 重置值
             */
            resetValue(prop, defaultProp) {
                _.set(this, prop, _.get(this.default, defaultProp))
            },
            /**
             * 设置柱子宽度
             */
            setBarWidth(val,index) {
                this.component.config.option.series[index].barWidth = this.numberToPercent(val)
            },
            /**
             * 百分比转数字
             * @param pct
             * @returns {number}
             */
            percentToNumber(pct) {
                if (!pct) {
                    return 0
                }
                return parseInt(pct.substring(0, pct.length - 1)) / 100
            },
            /**
             * 数字转百分比
             * @param val
             * @returns {string}
             */
            numberToPercent(val) {
                if (!val) {
                    val = 0
                }
                return val * 100 + "%"
            },
            /**
             * 修改series名
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            },
            // 改变触发类型
            changeToolTip(val) {
                if (val == 'item') {
                    this.$delete(this.component.config.option.tooltip, 'axisPointer')
                } else if (val == 'axis') {
                    let axisPointer = {
                        type: 'shadow',
                        label: {
                            show: true,
                            backgroundColor: '#0286ff'
                        }
                    }
                    this.$set(this.component.config.option.tooltip, 'axisPointer', axisPointer)
                }
            },
            /**
             * 增加一个系列
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper使用的是对象，此处遍历时，需要使用 this.$set() 方法赋值，
                // 否则无法调用mapper的setter，从而无法触发dom的重新渲染
                this.$set(this.component.config.transform.mapper, 'series' + index, null)
                let seriesName = `数据${index}`

                let barColor = ColorUtils.randomHex()
                let labelColor = ColorUtils.randomHex()
                this.barFill.single.push(barColor)
                this.barFill.gradient.push({
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    type: "linear",
                    global: false,
                    colorStops: [
                        {offset: 0, color: "#199cff"},
                        {offset: 1, color: "#0259aa"}
                    ]
                })
                this.default.barColor.push({
                    isSingle: true, color: barColor
                })
                this.default.labelColor.push(labelColor)
                this.component.config.option.markLines.push({
                    show:true,
                    markLine:{
                        data:[{yAxis:50}],
                        lineStyle:{
                            width:'1',
                            opacity:'1'
                        }
                    }
                });
                this.component.config.option.legend.data.push(seriesName)
                this.component.config.option.series.push({
                    name: seriesName,
                    type: 'bar',
                    barWidth: '20%',
                    barGap: '40%',
                    xAxisIndex: 0,
                    yAxisIndex: 0,
                    label: {
                        normal: {
                            show: true,
                            position: 'top',
                            textStyle: {
                                color: labelColor,
                                fontSize: 16
                            }
                        }
                    },
                    itemStyle: {
                        normal: {
                            barBorderRadius: 0,
                            isSingle: true,
                            color: barColor
                        }
                    }
                    // ,
                    // markLine:{
                    //     data:[{yAxis:50}]
                    // }
                })
            },
            /**
             * 删除一个系列
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                // 删除mapper的属性
                let mapper = _.cloneDeep(this.component.config.transform.mapper)
                for (let i = index; i < this.component.config.option.series.length; i++) {
                    mapper['series' + i] = mapper['series' + (i + 1)]
                }
                delete mapper['series' + (this.component.config.option.series.length - 1)]
                this.component.config.transform.mapper = mapper
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
                this.barFill.single.splice(index, 1)
                this.barFill.gradient.splice(index, 1)
                this.default.barColor.splice(index, 1)
                this.default.labelColor.splice(index, 1)
            },
            /**
             * 图标标线控制
             */
            changeMarkLineFlag(value, index){
                if(value){
                    this.component.config.option.series[index].markLine=this.component.config.option.markLines[index].markLine;
                }else{
                    this.component.config.option.series[index].markLine=null;
                }
            }
        },
        components: {},
    }
</script>
