<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import 'echarts-gl';
    import _ from 'lodash'

    export default {
        name: 'ThreeDimBar',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    dataset: {
                        dimensions: ['x', 'y', 'z'],
                        source: null,
                    },
                    visualMap: {
                        show: true,
                        max: 60,
                        min: 0,
                        calculable: true,
                        color: ['#ff3333', '#ffff00', '#00ffff'],
                        top: 'bottom',
                        left: 'left',
                        itemWidth: 20,
                        itemHeight: 100,
                        textStyle: {
                            fontSize: 12,
                            color: '#fff'
                        }
                    },
                    grid3D: {
                        boxWidth: 120,
                        boxDepth: 50,
                        boxHeight: 80,
                        viewControl: {
                            // projection: 'orthographic'
                        },
                        axisPointer: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            },
                            label: {
                                textStyle: {
                                    color: '#ffffff',
                                    fontSize: 16,
                                }
                            }
                        },
                        light: {
                            main: {
                                intensity: 1.2,
                                shadow: true
                            },
                            ambient: {
                                intensity: 0.3
                            }
                        }
                    },
                    xAxis3D: {
                        type: 'category',
                        nameTextStyle: {
                            color: 'rgba(0,0,0,0)'
                        },
                        axisTick: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLabel: {
                            show: true,
                            textStyle: {
                                color: '#ffffff',
                                fontSize: 12,
                                borderWidth: 1,
                                borderColor: 'rgba(0,0,0,0)'
                            }
                        }
                    },
                    yAxis3D: {
                        type: 'category',
                        nameTextStyle: {
                            color: 'rgba(0,0,0,0)'
                        },
                        axisTick: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLabel: {
                            show: true,
                            color: 'rgb(170,170,170)',
                            textStyle: {
                                color: '#ffffff',
                                fontSize: 12,
                                borderWidth: 1,
                                borderColor: 'rgba(0,0,0,0)'
                            }
                        }
                    },
                    zAxis3D: {
                        type: 'value',
                        nameTextStyle: {
                            color: 'rgba(0,0,0,0)'
                        },
                        axisTick: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLabel: {
                            show: true,
                            color: 'rgb(170,170,170)',
                            textStyle: {
                                color: '#ffffff',
                                fontSize: 12,
                                borderWidth: 1,
                                borderColor: 'rgba(0,0,0,0)'
                            }
                        }
                    },
                    series: [{
                        type: 'bar3D',
                        shading: 'lambert',
                        data: null,
                        label: {
                            show: false,
                            textStyle: {
                                color: '#ffffff',
                                fontSize: 12,
                                borderWidth: 1,
                                borderColor: 'rgba(0,0,0,0)'
                            }
                        },
                        emphasis: {
                            label: {
                                textStyle: {
                                    fontSize: 20,
                                    color: '#900',
                                    borderWidth: 1,
                                    borderColor: 'rgba(0,0,0,0)'
                                }
                            },
                            itemStyle: {
                                color: '#900'
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {x: '2013', y: '一季度', z: 20},
                    {x: '2013', y: '二季度', z: 22},
                    {x: '2013', y: '三季度', z: 24},
                    {x: '2013', y: '四季度', z: 30},
                    {x: '2014', y: '一季度', z: 28},
                    {x: '2014', y: '二季度', z: 32},
                    {x: '2014', y: '三季度', z: 37},
                    {x: '2014', y: '四季度', z: 40},
                    {x: '2015', y: '一季度', z: 32},
                    {x: '2015', y: '二季度', z: 35},
                    {x: '2015', y: '三季度', z: 42},
                    {x: '2015', y: '四季度', z: 47},
                    {x: '2016', y: '一季度', z: 42},
                    {x: '2016', y: '二季度', z: 45},
                    {x: '2016', y: '三季度', z: 42},
                    {x: '2016', y: '四季度', z: 59},
                    {x: '2017', y: '一季度', z: 46},
                    {x: '2017', y: '二季度', z: 46},
                    {x: '2017', y: '三季度', z: 50},
                    {x: '2017', y: '四季度', z: 52},
                    {x: '2018', y: '一季度', z: 45},
                    {x: '2018', y: '二季度', z: 47},
                    {x: '2018', y: '三季度', z: 44},
                    {x: '2018', y: '四季度', z: 50},
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'x', comment: 'x坐标', prop: 'x', state: null},
                            {name: 'y', comment: 'y坐标', prop: 'y', state: null},
                            {name: 'z', comment: 'z坐标', prop: 'z', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        x: null,
                        y: null,
                        z: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                this.config.option.dataset.source = this.config.data
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
