<template>
    <Plugin :config="config" :base="base">
        <div class="clock-ins"
             :style="calcStyle">
            <span class="date" :style="calcTextStyle('date')">{{ config.option.date.value }}</span>
            <span class="weekday" :style="calcTextStyle('weekday')">{{ config.option.weekday.value }}</span>
            <div class="time" :style="calcTextStyle('time')">{{ config.option.time.value }}</div>
        </div>
    </Plugin>
</template>

<script>
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'Clock',
        props: ['config', 'base'],
        data() {
            return {
                thread: null,
                weekday: ['周日', '周一', '周二', '周三', '周四', '周五', '周六'],
            }
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        mounted() {
            this.run()
        },
        computed: {
            /**
             * 计算容器样式
             */
            calcStyle() {
                return {
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                    color: this.config.style.color,
                    fontWeight: this.config.style.fontWeight,
                    fontSize: this.config.style.fontSize + 'px',
                    borderWidth: this.config.style.borderWidth + 'px',
                    borderStyle: this.config.style.borderStyle,
                    borderColor: this.config.style.borderColor,
                    backgroundColor: this.config.style.backgroundColor,
                    borderTopLeftRadius: this.config.style.borderTopLeftRadius + 'px',
                    borderTopRightRadius: this.config.style.borderTopRightRadius + 'px',
                    borderBottomLeftRadius: this.config.style.borderBottomLeftRadius + 'px',
                    borderBottomRightRadius: this.config.style.borderBottomRightRadius + 'px',
                }
            },

        },
        methods: {
            /**
             * 初始化样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    width: 200,
                    height: 70,
                    opacity: 1,
                    display: true,
                    fontSize: 24,
                    fontWeight: 400,
                    borderWidth: 1,
                    borderStyle: 'solid',
                    borderColor: 'rgba(0,0,0,0)',
                    backgroundColor: 'rgba(0,0,0,0)',
                    borderTopLeftRadius: 0,
                    borderTopRightRadius: 0,
                    borderBottomLeftRadius: 0,
                    borderBottomRightRadius: 0,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [{value: '一个标题'}]
            },
            /**
             * 声明默认option
             */
            initOption() {
                this.config.option = this.config.option || {
                    date: {
                        show: true,
                        fontSize: 16,
                        fontWeight: 400,
                        color: '#daf6ff',
                        value: ''
                    },
                    weekday: {
                        show: true,
                        fontSize: 16,
                        fontWeight: 400,
                        color: '#daf6ff',
                        value: ''
                    },
                    time: {
                        show: true,
                        fontSize: 28,
                        fontWeight: 400,
                        color: '#daf6ff',
                        value: ''
                    },
                }
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = null
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = {
                    type: null
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = null
            },
            /**
             * 计算文字样式
             */
            calcTextStyle(category) {
                return {
                    fontSize: this.config.option[category].fontSize + 'px',
                    fontWeight: this.config.option[category].fontWeight,
                    color: this.config.option[category].color,
                }
            },
            /**
             * 开始计时
             */
            run() {
                let me = this
                if (me.thread) {
                    clearInterval(me.thread)
                }
                me.formatTime()
                me.thread = setInterval(function () {
                    me.formatTime();
                }, 1000)
            },
            /**
             * 设置当前时间
             */
            formatTime() {
                let date = new Date()
                this.config.option.time.value = ''
                if (this.config.option.time.show) {
                    this.config.option.time.value += this.fillZero(date.getHours(), 2) + ':' +
                        this.fillZero(date.getMinutes(), 2) + ':' +
                        this.fillZero(date.getSeconds(), 2)
                }
                this.config.option.date.value = ''
                if (this.config.option.date.show) {
                    this.config.option.date.value += this.fillZero(date.getFullYear(), 4) + '-' +
                        this.fillZero(date.getMonth() + 1, 2) + '-' +
                        this.fillZero(date.getDate(), 2) + ' '
                }
                this.config.option.weekday.value = ''
                if (this.config.option.weekday.show) {
                    this.config.option.weekday.value += this.weekday[date.getDay()]
                }
            },
            /**
             * 填充数字0
             * @param num
             * @param digit
             * @returns {string}
             */
            fillZero(num, digit) {
                let zero = ''
                for (let i = 0; i < digit; i++) {
                    zero += '0'
                }
                return (zero + num).slice(-digit)
            }
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
