<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BaseFunnel',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    tooltip: {
                        trigger: 'item',
                        formatter: "{b} : {c} "
                    },
                    color: ["#FFDB5C", "#9FE6B8", "#67E0E3", "#32C5E9", "#37A2DA"],
                    series: [{
                        name: '漏斗图',
                        type: 'funnel',
                        x: '10%',
                        width: '80%',
                        min: 0,
                        max: 100,
                        minSize: 0,
                        maxSize: '100%',
                        sort: 'descending',
                        funnelAlign: 'center',
                        gap: 0,
                        data: null,
                        label: {
                            show: true,
                            formatter: '{b} {c}',
                            position: 'center'
                        },
                        itemStyle: {
                            normal: {
                                borderWidth: 0,
                                shadowBlur: 30,
                                shadowOffsetX: 0,
                                shadowOffsetY: 10,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {value: 60, name: '访问'},
                    {value: 40, name: '咨询'},
                    {value: 20, name: '订单'},
                    {value: 80, name: '点击'},
                    {value: 100, name: '展现'}
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'value', comment: '数据值', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propX = mapper.name || 'name'
                let propY = mapper.value || 'value'
                let series = []
                for (let i = 0; i < data.length; i++) {
                    series.push({
                        name: data[i][propX],
                        value: data[i][propY],
                    })
                }
                this.config.option.series[0].data = series.sort(function (a, b) {
                    return a.value - b.value
                })
                this.config.option.series[0].max = series[series.length - 1].value
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
