<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'RainfallLine',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    dataset: {
                        dimensions: null,
                        source: null,
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            lineStyle: {
                                color: 'rgba(62, 158, 255, 0.8)',
                                width: 1,
                            }
                        },
                        backgroundColor: '#2F3943',
                        borderColor: 'transparent',
                        borderWidth: 1,
                        padding: [5, 10, 5, 10],
                        textStyle: {
                            color: '#ffffff',
                        },
                        extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)'
                    },
                    grid: {
                        top: 50,
                        left: 50,
                        right: 50,
                        bottom: 50
                    },
                    legend: {
                        orient: 'horizontal',
                        top: 'top',
                        left: 'center',
                        textStyle: {
                            fontSize: 12,
                            color: ['#D4DFF5']
                        },
                        data: ['默认']
                    },
                    dataZoom: [{
                        show: true,
                        realtime: true,
                        start: 15,
                        end: 95,
                        bottom: 0,
                        textStyle: {
                            fontSize: 14,
                            color: '#dddddd'
                        }
                    }, {
                        type: 'inside',
                        realtime: true,
                        start: 15,
                        end: 95
                    }],
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        splitLine: {
                            show: true,
                            interval: 'auto',
                            lineStyle: {
                                color: ['rgba(212,223,245,0)']
                            }
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            onZero: false,
                            lineStyle: {
                                color: '#dddddd'
                            }
                        },
                        axisLabel: {
                            show: true,
                            interval:0,
                            rotate:0,
                            margin: 10,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        }
                    },
                    yAxis: [{
                        type: 'value',
                        min: 0,
                        max: 50,
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: ['rgba(110,117,132,0.50)']
                            }
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#dddddd'
                            }
                        },
                        axisLabel: {
                            show: true,
                            margin: 10,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        }
                    }, {
                        type: 'value',
                        inverse: true,
                        min: 0,
                        max: 250,
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: ['rgba(110,117,132,0.50)']
                            }
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#dddddd'
                            }
                        },
                        axisLabel: {
                            show: true,
                            margin: 10,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        }
                    }],
                    series: [{
                        name: '系列1',
                        type: 'line',
                        smooth: false,
                        showSymbol: false,
                        symbol: 'circle',
                        symbolSize: 2,
                        areaStyle: {
                            normal: {
                                color: {
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    type: 'linear',
                                    global: false,
                                    colorStops: [
                                        {offset: 0, color: 'rgba(255,118,99,0.7)'},
                                        {offset: 0.5, color: 'rgba(255,118,99,0.7)'},
                                        {offset: 1, color: 'rgba(255,118,99,0.7)'}
                                    ]
                                }
                            }
                        },
                        itemStyle: {
                            normal: {
                                color: 'rgba(62, 158, 255, 1)'
                            }
                        },
                        lineStyle: {
                            normal: {
                                width: 1
                            }
                        }
                    }, {
                        name: '系列2',
                        type: 'line',
                        smooth: false,
                        showSymbol: false,
                        symbol: 'circle',
                        symbolSize: 1,
                        yAxisIndex: 1,
                        areaStyle: {
                            normal: {
                                color: {
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    type: 'linear',
                                    global: false,
                                    colorStops: [
                                        {offset: 0, color: 'rgba(117,130,172,0.8)'},
                                        {offset: 0.5, color: 'rgba(117,130,172,0.8)'},
                                        {offset: 1, color: 'rgba(117,130,172,0.8)'}
                                    ]
                                }
                            }
                        },
                        itemStyle: {
                            normal: {
                                color: 'rgba(62, 158, 255, 1)'
                            }
                        },
                        lineStyle: {
                            normal: {
                                width: 1
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '1-1', series1: 5, series2: 80},
                    {name: '1-2', series1: 3, series2: 87},
                    {name: '1-3', series1: 2, series2: 51},
                    {name: '1-4', series1: 7, series2: 81},
                    {name: '1-4', series1: 4, series2: 23},
                    {name: '1-6', series1: 2, series2: 45},
                    {name: '1-7', series1: 5, series2: 80},
                    {name: '1-8', series1: 3, series2: 87},
                    {name: '1-9', series1: 2, series2: 51},
                    {name: '1-10', series1: 7, series2: 81},
                    {name: '1-11', series1: 4, series2: 23},
                    {name: '1-12', series1: 18, series2: 145},
                    {name: '1-13', series1: 10, series2: 100},
                    {name: '1-14', series1: 5, series2: 45},
                    {name: '1-15', series1: 3, series2: 33},
                    {name: '1-16', series1: 2, series2: 6},
                    {name: '1-17', series1: 10, series2: 95},
                    {name: '1-18', series1: 3, series2: 33},
                    {name: '1-19', series1: 2, series2: 22},
                    {name: '1-20', series1: 5, series2: 55},
                    {name: '1-21', series1: 7, series2: 66},
                    {name: '1-22', series1: 0, series2: 0},
                    {name: '1-23', series1: 0, series2: 0},
                    {name: '1-24', series1: 0, series2: 0},
                    {name: '1-25', series1: 0, series2: 8},
                    {name: '1-26', series1: 2, series2: 11},
                    {name: '1-27', series1: 20, series2: 200},
                    {name: '1-28', series1: 3, series2: 32},
                    {name: '1-29', series1: 1, series2: 5},
                    {name: '1-30', series1: 1, series2: 6}
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'series1', comment: '数据值1', prop: 'series1', state: null},
                            {name: 'series2', comment: '数据值2', prop: 'series2', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        series1: null,
                        series2: null,
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let dimensions = []
                for (let key in this.config.transform.mapper) {
                    let dim = this.config.transform.mapper[key] || key
                    if (!this.config.data[0].hasOwnProperty(dim)) {
                        dim = null
                    }
                    dimensions.push(dim)
                }
                this.config.option.dataset = {
                    dimensions: dimensions,
                    source: this.config.data
                }
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
