<template>
    <div :id="config.id"
         :style="calcStyle()"
         class="vi-modal">
        <div v-if="config.option.header.show"
             class="vi-modal-header">
            <div class="vi-modal-title">{{config.option.header.title}}</div>
        </div>
        <div class="vi-modal-close" @click="closeModal()">
            <i class="el-icon el-icon-close"></i>
        </div>
        <div class="vi-modal-body">
            <slot name="body"></slot>
        </div>
        <span v-if="config.option.footer.show" class="vi-modal-footer">
            <slot name="footer"></slot>
        </span>
    </div>
</template>

<script>
    import CONST from "../../../../config/const"
    import TriggerHandler from "../../../../utils/TriggerHandler"
    import _ from "lodash"
    import DataProxy from "../../../proxy/DataProxy";

    export default {
        name: "Modal",
        props: ['config', 'base'],
        data() {
            return {
                // 数据代理实例
                dataProxy: null,
                // 数据代理线程
                proxyThread: null
            }
        },
        created() {
            this.initStyle()
            this.initTrigger()
            this.initProxy()
            this.initTransform()
            // this.initEvent()
        },
        mounted() {
            this.init()
        },
        beforeDestroy() {
            // 销毁前注销组件的数据绑定
            this.$store.commit(CONST.MUTATIONS.SCREEN.UNREGISTER, {
                comp: this
            })
        },
        methods: {
            /**
             * 计算容器样式
             */
            calcStyle() {
                return {
                    width: this.config.style.width + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                    visibility: this.config.style.visibility,
                }
            },
            /**
             * 初始化组件
             */
            init() {
                this.instanceProxy()
            },
            /**
             * 初始化样式，对新组件使用默认样式
             */
            initStyle() {
                this.config.style = _.assignIn({
                    top: 30,
                    left: 30,
                    width: 200,
                    opacity: 1,
                    visibility: 'hidden'
                }, this.config.style)
            },
            /**
             * 初始化触发器
             */
            initTrigger() {
                this.config.trigger = this.config.trigger || [
                    {
                        show: true,
                        name: '可见性',
                        binder: null,
                        prop: "config.style.visibility",
                        defaultValue: true,
                        isScript: true,
                        mapper: [
                            {key: true, value: 'visible', comment: '返回 true 时显示'},
                            {key: false, value: 'hidden', comment: '返回 false 时隐藏'},
                        ]
                    }
                ]
                this.initTriggerStore()
                this.calcTrigger()
            },
            /**
             * 初始化触发器的Store存储
             */
            initTriggerStore() {
                if (!this.config.trigger || !this.config.trigger.length) {
                    return
                }
                let store = {}
                let vuex = this.$store
                for (let i = 0; i < this.config.trigger.length; i++) {
                    let trig = this.config.trigger[i]
                    let binder = trig.binder;
                    if (binder && binder.length) {
                        let keys = binder.match(CONST.REGEX.PLACEHOLDER_EXTERNAL);
                        if (!keys || !keys.length) {
                            continue;
                        }
                        for (let i = 0; i < keys.length; i++) {
                            let storeProp = keys[i].substring(2, keys[i].length - 1)
                            store[storeProp] = vuex.commit(CONST.MUTATIONS.GET, {prop: storeProp}) || null
                            vuex.commit(CONST.MUTATIONS.REGISTER, {
                                // 绑定哪个vuex状态
                                store: storeProp,
                                // 当前的绑定对象
                                comp: this,
                                // 当vuex状态变更时，应该修改绑定对象的哪个属性
                                prop: CONST.INTERACTION.COMPONENT_TRIGGER_STORE + storeProp
                            })
                        }
                    }
                }
                this.config.store.trigger = store
            },
            /**
             * 计算trigger结果，并赋值给相应的属性
             */
            calcTrigger() {
                if (!this.config.trigger || !this.config.trigger.length) {
                    return
                }
                for (let i = 0; i < this.config.trigger.length; i++) {
                    let trig = this.config.trigger[i];
                    let result = TriggerHandler.do(trig, this.config.data, this.config.store.trigger)
                    _.set(this, trig.prop, result);
                }
            },
            /**
             * 初始化样式，对新组件使用默认样式
             */
            initOption() {
                this.config.option = _.assignIn({
                    // 是否点击空白处关闭
                    clickToClose: true,
                    // 点击esc关闭
                    escToClose: true,
                    // 显示关闭按钮
                    showCloseBtn: true,
                    // body内容配置
                    body: {
                        // 静态html
                        html: '',
                        // 动态载入组件
                        components: []
                    },
                    // footer内容配置
                    footer: {
                        // 隐藏footer部
                        hidden: false,
                        // 静态html
                        html: '',
                        // 动态载入组件
                        components: []
                    }
                }, this.config.option)
            },
            /**
             * 初始化数据代理的配置参数
             */
            initProxy() {
                let proxy = _.assignIn({}, {
                    // 类型，0-API / 1-静态数据 / 2-数据库,
                    type: 1,
                    // 自动更新
                    autoRequest: false,
                    // 自动更新间隔
                    interval: 60,
                    // url
                    url: null,
                    // 参数
                    param: [],
                }, this.config.proxy)
                this.config.proxy = proxy
            },
            /**
             * 实例化proxy，并缓存为组件属性
             */
            instanceProxy() {
                this.dataProxy = new DataProxy(this)
            },
            /**
             * 初始化transform
             */
            initTransform() {
                let transform = _.assignIn({}, {
                    // 从源数据的什么属性获取展示用的数据集
                    prop: null,
                }, this.config.transform)
                this.config.transform = transform
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'close',
                    comment: '关闭事件',
                    binders: [
                        {name: 'visibility', comment: '可见性', prop: 'config.style.visibility', state: null},
                    ]
                }]
            },
            /**
             * 使用数据代理获取数据，并将数据载入配置项中，然后重新加载配置项，渲染组件
             */
            render() {
                clearInterval(this.proxyThread)
                this.proxyData()
                if (this.config.proxy.autoRequest && this.config.proxy.interval) {
                    this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
                }
            },
            /**
             * 代理数据
             */
            proxyData() {
                this.dataProxy.do()
                    .then((data) => {
                        this.config.data = data
                    })
                    .catch(() => {
                    })
                    .finally(() => {
                        this.loadData()
                    })
            },
            /**
             * 调用父组件的handler方法，handler定义了将数据载入option的过程，
             * 因为option配置项是根据具体的报表类型而不同的，所以handler必须定义在父组件中
             */
            loadData() {
                if (!this.config.data || !this.config.data.length) {
                    return
                }
                if (!this.$parent || !this.$parent.handler) {
                    return
                }
                this.$parent.handler()
            },
            /**
             * 向全局状态管理发起状态修改请求
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 重新加载报表
             */
            reload() {
                this.render()
            },

            /**
             * 触发事件代理
             */
            onEvent(eventName, item, index) {
                if (!this.config.event || !this.config.event.length) {
                    return
                }
                // 遍历组件的事件列表
                for (let i = 0; i < this.config.event.length; i++) {
                    let event = this.config.event[i]
                    // 事件名与当前触发的事件不匹配时，跳过
                    if (event.name != eventName) {
                        continue
                    }
                    // 触发的事件未定义绑定关系时，跳过
                    if (!event.binders || !event.binders.length) {
                        continue
                    }
                    // 遍历绑定规则
                    for (let j = 0; j < event.binders.length; j++) {
                        let binder = event.binders[j]
                        // 未与全局状态绑定的规则，跳过
                        if (!binder.state) {
                            continue
                        }
                        // 通过prop是否为空，判断是内置规则还是自定义规则
                        let value
                        // 如果是内置规则，则在option.data对应的对象中查找值
                        // 如果是自定义规则，则通过索引找到原始数据对象，在其中查找值
                        if (binder.prop) {
                            value = _.get(item, binder.prop)
                        } else {
                            let dataset = this.config.data
                            value = dataset[index][binder.name]
                        }
                        this.commit(binder.state, value)
                    }
                }
            },
            /**
             * 关闭窗口
             */
            closeModal() {
                this.config.style.visibility = 'hidden'
                this.onEvent("close", this)
            }
        },
        watch: {
            /**
             * 监听被外部触发修改的属性
             */
            "config.trigger": {
                deep: true,
                handler: function () {
                    this.initTriggerStore()
                    this.calcTrigger()
                }
            },
            /**
             * 监听proxy，发生变更时，重新实例化数据代理，并渲染组件
             */
            'config.proxy': {
                deep: true,
                handler: function () {
                    this.instanceProxy()
                    this.render()
                }
            },
            /**
             * 监听全局状态绑定的触发器，发生变更时重新计算触发器相关的属性
             */
            "config.store.trigger": {
                deep: true,
                handler: function () {
                    this.calcTrigger()
                }
            },
            /**
             * 监听全局状态绑定的数据代理，发生变更时重新渲染组件
             */
            "config.store.proxy": {
                deep: true,
                handler: function () {
                    this.render()
                }
            },
            /**
             * 监听静态数据
             */
            'config.data': {
                deep: true,
                handler: function () {
                    this.loadData()
                    this.calcTrigger()
                }
            },
            /**
             * 监听数据转换规则
             */
            'config.transform': {
                deep: true,
                handler: function () {
                    this.proxyData()
                }
            },
            /**
             * 监听从数据转为option的方法
             */
            'config.handler': {
                deep: true,
                handler: function () {
                    this.loadData()
                }
            },
        }
    }
</script>
