<template>
    <Map :config="config" :base="base"></Map>
</template>

<script>
    import Map from '@/components/screen/layer/base/Map'
    import ChinaCity from '../../../map/coordinate/china-city'
    import _ from 'lodash'

    export default {
        name: 'HeatMap',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    visualMap: {
                        show: true,
                        min: 0,
                        max: 100,
                        calculable: true,
                        color: ['#ff3333', '#ffff00', '#00ffff'],
                        left: 'left',
                        itemWidth: 20,
                        itemHeight: 100,
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    geo: {
                        map: 'china',
                        label: {
                            emphasis: {
                                show: false
                            }
                        },
                        roam: true,
                        layoutCenter: ['50%', '53%'],
                        layoutSize: '108%',
                        itemStyle: {
                            normal: {
                                color: 'rgba(51, 69, 89, .5)',
                                borderColor: 'rgba(100,149,237,1)'
                            },
                            emphasis: {
                                color: 'rgba(37, 43, 61, .5)'
                            }
                        }
                    },
                    series: [{
                        type: 'heatmap',
                        coordinateSystem: 'geo',
                        zlevel: 2,
                        data: null
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
				this.config.areaSource='';
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '北京市', value: 120},
                    {name: '上海市', value: 100},
                    {name: '广州市', value: 70},
                    {name: '哈尔滨市', value: 30},
                    {name: '青岛市', value: 50},
                    {name: '南昌市', value: 20},
                    {name: '银川市', value: 10},
                    {name: '拉萨市', value: 80},
                    {name: '西安市', value: 55},
                    {name: '乌鲁木齐市', value: 90}
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'click',
                    comment: '点击底图事件',
                    filter: function (params) {
                        if (params.componentType === "geo") {
                            return true
                        } else {
                            return false;
                        }
                    },
                    binders: [
                        {name: 'name', comment: '行政区划名', prop: 'name', state: null},
                    ]
                }]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    },
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let scatterData = []
                let propName = this.config.transform.mapper.name || 'name'
                let propValue = this.config.transform.mapper.value || 'value'
                for (let i = 0; i < this.config.data.length; i++) {
                    let item = this.config.data[i]
                    let name = item[propName]
                    let value = item[propValue]
                    let cityCoord = ChinaCity.get(name)
                    scatterData.push({
                        name: name,
                        value: cityCoord.concat(value)
                    })
                }
                this.config.option.series[0].data = scatterData
            }
        },
        components: {
            'Map': Map
        },
    }
</script>
