<template>
    <Plugin :config="config" :base="base">
        <div :style="calcButtonGroupStyle">
            <el-button v-for="(item, index) in config.option.data"
                       :style="calcButtonStyle(item, index)"
                       :key="index"
                       @click="onEvent('click', item, index)">{{item.name}}
            </el-button>
        </div>
    </Plugin>
</template>

<script>
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'Radio',
        props: ['config', 'base'],
        data() {
            return {
                // 当前选中的按钮索引
                currentIndex: 0
            }
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        computed: {
            /**
             * 计算容器样式
             */
            calcButtonGroupStyle() {
                return {
                    display: this.config.style.horizon ? 'inline-flex' : 'block',
                }
            },
        },
        methods: {
            /**
             * 计算按钮样式
             */
            calcButtonStyle(item, index) {
                return {
                    fontSize: this.config.style.fontSize + 'px',
                    width: this.config.style.btnWidth + 'px',
                    height: this.config.style.btnHeight + 'px',
                    lineHeight: this.config.style.lineHeight + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                    color: this.isActiveItem(index) ? this.config.style.activeColor : this.config.style.color,
                    borderColor: this.isActiveItem(index) ? this.config.style.activeBorderColor : this.config.style.borderColor,
                    backgroundColor: this.isActiveItem(index) ? this.config.style.activeBackgroundColor : this.config.style.backgroundColor,
                    marginLeft: this.config.style.marginLeft + 'px',
                    marginRight: this.config.style.marginRight + 'px',
                    marginTop: this.config.style.marginTop + 'px',
                    marginBottom: this.config.style.marginBottom + 'px',
                    borderRadius: this.config.style.borderRadius + 'px',

                }
            },
            /**
             * 初始化样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    width: 280,
                    height: 50,
                    fontSize: 12,
                    btnHeight: 40,
                    lineHeight: 12,
                    btnWidth: 84,
                    marginLeft: 5,
                    marginRight: 5,
                    marginTop: 5,
                    marginBottom: 5,
                    color: '#00BAFF',
                    borderColor: '#00BAFF',
                    backgroundColor: '#151a23',
                    activeColor: '#efefef',
                    activeBorderColor: '#00BAFF',
                    activeBackgroundColor: '#00BAFF',
                    borderRadius: 0,
                    horizon: true,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '按钮一', value: 0},
                    {name: '按钮二', value: 1},
                    {name: '按钮三', value: 2},
                ]
            },
            /**
             * 声明默认option
             */
            initOption() {
                this.config.option = this.config.option || {
                    value: 0,
                    data: []
                }
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '文本', prop: 'name', state: null},
                            {name: 'value', comment: '值', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null,
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propName = mapper.name || 'name'
                let propValue = mapper.value || 'value'
                let optionData = []
                for (let i = 0; i < data.length; i++) {
                    optionData.push({
                        name: data[i][propName],
                        value: data[i][propValue],
                    })
                }
                this.config.option.data = optionData
                this.config.option.name = optionData[0].name
            },
            /**
             * 触发事件代理
             */
            onEvent(eventName, item, index) {
                this.currentIndex = index
                this.$children[0].onEvent(eventName, item, index)
            },
            /**
             * 判断一个按钮是否激活
             */
            isActiveItem(index) {
                if (this.currentIndex == index) {
                    return true
                }
                return false
            },
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
