<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BaseBar',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    title: {
                        show: false,
                        text: '柱状图',
                        textStyle: {
                            color: '#fff',
                            fontSize: '22',
                            fontWeight: 'normal'
                        },
                    },
                    dataset: {
                        dimensions: null,
                        source: null,
                    },
                    backgroundColor: 'rgba(0,0,0,0)',
                    grid: {
                        top: 50,
                        left: 50,
                        right: 50,
                        bottom: 50
                    },
                    legend: {
                        show: true,
                        data: ['数据0'],
                        textStyle: {
                            fontSize: 12,
                            color: '#ffffff'
                        },
                        x: 'center',
                        y: 'bottom',
                    },
                    tooltip: {
                        show: true,
                        trigger: 'axis',
                        backgroundColor: 'rgba(0,0,0,0.6)',
                        axisPointer: {
                            type: 'shadow',
                            label: {
                                show: true,
                                backgroundColor: '#0286ff'
                            }
                        },
                        textStyle: {
                            color: '#ffffff',
                            fontSize: 16,
                            fontWeight: 'normal',
                        },
                    },
                    xAxis: [{
                        type: 'category',
                        gridIndex: 0,
                        data: null,
                        axisTick: {
                            alignWithLabel: true
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#0c3b71'
                            }
                        },
                        axisLabel: {
                            show: true,
                            interval: 0,
                            rotate: 0,
                            color: 'rgb(170,170,170)',
                            fontSize: 16
                        }
                    }],
                    yAxis: [{
                        type: 'value',
                        gridIndex: 0,
                        splitLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#0c3b71'
                            }
                        },
                        axisLabel: {
                            show: true,
                            color: 'rgb(170,170,170)',
                            formatter: '{value}'
                        },
                        min: 0,
                        max: undefined

                    }, {
                        type: 'value',
                        splitLine: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLabel: {
                            show: false
                        }
                    }],
                    series: [{
                        name: '数据0',
                        type: 'bar',
                        barWidth: '20%',
                        barGap: '40%',
                        xAxisIndex: 0,
                        yAxisIndex: 0,
                        label: {
                            normal: {
                                show: true,
                                position: 'top',
                                textStyle: {
                                    color: '#ffc72b',
                                    fontSize: 16
                                }
                            }
                        },
                        itemStyle: {
                            normal: {
                                barBorderRadius: 0,
                                isSingle: true,
                                color: '#0286ff'
                            }
                        }
                        // markLine:{
                        //     data:[{yAxis:50}]
                        // }
                    }],
                    markLines: [
                        {
                            show: true,
                            markLine: {
                                data: [{yAxis: 50}],
                                lineStyle: {
                                    width: '1',
                                    opacity: '1'
                                }
                            }
                        }
                    ]

                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '周一', series0: 80, series1: 60},
                    {name: '周二', series0: 87, series1: 80},
                    {name: '周三', series0: 51, series1: 50},
                    {name: '周四', series0: 81, series1: 89},
                    {name: '周五', series0: 23, series1: 56},
                    {name: '周六', series0: 45, series1: 32},
                    {name: '周日', series0: 33, series1: 77}
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'data', comment: '数据值', prop: 'data', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        series0: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let dimensions = []
                for (let key in this.config.transform.mapper) {
                    let dim = this.config.transform.mapper[key] || key
                    if (!this.config.data[0].hasOwnProperty(dim)) {
                        dim = null
                    }
                    dimensions.push(dim)
                }
                this.config.option.dataset = {
                    dimensions: dimensions,
                    source: this.config.data
                }
                for (let i in this.config.option.series) {
                    if (this.config.option.markLines[i].show)
                        this.config.option.series[i].markLine = this.config.option.markLines[i].markLine;
                }

            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
