<template>
    <div class="paint"
         @click.left="cancelSelect($event)"
         @click.right="stopPropagation($event)"
         @mousedown.left="drawMultiBox($event)"
         @mousedown.right="stopPropagation($event)"
         @contextmenu.prevent>
        <div class="bg-panel"
             :style="calcBgStyle()">
            <div :style="calcPaintStyle()"
                 style="background-repeat:no-repeat;transform-origin:0 0 0;"
                 id="screen-paint"
                 class="canvas-panel"
                 @dragover.prevent
                 @drop="dropComponent($event)">
                <div v-show="calcMultiBoxStyle()"
                     :style="calcMultiBox()"
                     class="multi-select-box"></div>
                <div v-show="calcMultiBoxStyle()"
                     :style="calcMultiTop()"
                     class="aux-line aux-top">
                    <div v-show="calcMultiBoxStyle()"
                         class="multi-aux-num">
                        {{calcMultiTopText()}}
                    </div>
                </div>
                <div v-show="calcMultiBoxStyle()"
                     :style="calcMultiLeft()"
                     class="aux-line aux-left">
                    <div v-show="calcMultiBoxStyle()"
                         class="multi-aux-num">
                        {{calcMultiLeftText()}}
                    </div>
                </div>
                <template v-if="config.base.enableGrid">
                    <Grid :config="config" :state="state" :loader="loader"></Grid>
                </template>
                <template v-if="config.sequence && config.sequence.length > 0">
                    <template v-for="(item,index) in config.components">
                        <!-- 当未启用栅格，或启用栅格且组件不处于栅格中时，在此处渲染，否则在Grid中渲染 -->
                        <div v-if="!config.base.enableGrid || !item.config.grid.id"
                             :key="index"
                             :style="calcStyle(item)"
                             :class="calcActiveStyle(item)"
                             class="component-box"
                             @click.exact.left="activeSingleBox($event, item)"
                             @click.exact.right="showContextMenu($event, item)"
                             @click.ctrl.left="activeMultiBox($event, item)"
                             @click.meta.left="activeMultiBox($event, item)"
                             @mousedown.left="dragBox($event, item)">
                            <component :is="loader[item.config.id].default"
                                       :base="config.base"
                                       :config="config.components[item.config.id].config"></component>
                            <div v-show="calcResizeStyle(item)"
                                 :style="calcAuxTop(item)"
                                 class="aux-line aux-top"></div>
                            <div v-show="calcResizeStyle(item)"
                                 :style="calcAuxLeft(item)"
                                 class="aux-line aux-left"></div>
                            <div v-show="calcResizeStyle(item)"
                                 class="aux-num aux-top">
                                {{item.config.style && item.config.style.top + 'px'}}
                            </div>
                            <div v-show="calcResizeStyle(item)"
                                 class="aux-num aux-left">
                                {{item.config.style && item.config.style.left + 'px'}}
                            </div>
                            <div v-show="calcResizeStyle(item)"
                                 class="resize resize-tl"
                                 @mousedown.left="dragCorner($event, item, 'tl')"></div>
                            <div v-show="calcResizeStyle(item)"
                                 class="resize resize-tr"
                                 @mousedown.left="dragCorner($event, item, 'tr')"></div>
                            <div v-show="calcResizeStyle(item)"
                                 class="resize resize-bl"
                                 @mousedown.left="dragCorner($event, item, 'bl')"></div>
                            <div v-show="calcResizeStyle(item)"
                                 class="resize resize-br"
                                 @mousedown.left="dragCorner($event, item, 'br')"></div>
                            <div v-show="calcResizeStyle(item)"
                                 class="resize resize-t"
                                 @mousedown.left="dragCorner($event, item, 't')"></div>
                            <div v-show="calcResizeStyle(item)"
                                 class="resize resize-l"
                                 @mousedown.left="dragCorner($event, item, 'l')"></div>
                            <div v-show="calcResizeStyle(item)"
                                 class="resize resize-r"
                                 @mousedown.left="dragCorner($event, item, 'r')"></div>
                            <div v-show="calcResizeStyle(item)"
                                 class="resize resize-b"
                                 @mousedown.left="dragCorner($event, item, 'b')"></div>
                        </div>
                    </template>
                </template>
            </div>
        </div>
        <div class="bottom-bar">
            <div class="scale-bar">
                <span class="icon iconfont icon-minus" @click="scale(-1)"></span>
                <el-slider :show-tooltip="paintScale.showTooltip"
                           v-model="paintScale.index"
                           :min="paintScale.min"
                           :max="paintScale.max"
                           :step="paintScale.step">
                </el-slider>
                <span class="icon iconfont icon-plus" @click="scale(1)"></span>
            </div>
        </div>
        <div class="select-box"
             :style="calcSelectBoxStyle()"></div>
    </div>
</template>
<script>
    import Grid from '@/components/screen/Grid'
    import _ from 'lodash'

    export default {
        name: "Paint",
        props: ["config", "state"],
        data() {
            return {
                // 多选组件框
                multiBox: {
                    x: 0,
                    y: 0,
                    scrollTop: 0,
                    scrollLeft: 0,
                    currentX: 0,
                    currentY: 0,
                    display: 'none',
                    top: 0,
                    left: 0,
                    height: 0,
                    width: 0,
                    start: null,
                    end: null
                },
                // 多选组件后的容器
                multiContainer: {
                    top: 0,
                    left: 0,
                    height: 0,
                    width: 0,
                },
                // 动态加载组件的存储
                loader: {},
                // 位移参照基准点
                originalPosition: {x: null, y: null},
                // 当前调整大小的组件id
                resizeId: null,
                // 调整大小的角所在位置
                cornerPosition: null,
                // 调整大小的角位置参照基准点
                cornerPoint: {x: null, y: null},
                // 画布缩放
                paintScale: {
                    showTooltip: true,
                    min: 0.125,
                    max: 2,
                    step: 0.125,
                    index: 0.375
                },
            }
        },
        created() {
            this.loadComponent()
        },
        methods: {
            /**
             * 计算框选样式和位置
             */
            calcSelectBoxStyle() {
                return {
                    display: this.multiBox.display,
                    top: this.multiBox.top + 'px',
                    left: this.multiBox.left + 'px',
                    height: this.multiBox.height + 'px',
                    width: this.multiBox.width + 'px',
                }
            },
            /**
             * 拖拽框选组件开始
             */
            cancelSelect(e) {
                if (this.isMultiSelected()) {
                    e.stopPropagation()
                    return
                }
                // 取消组件编辑
                this.$set(this.state, "select", {})
                // 取消栅格编辑模式
                this.state.grid.configMode = false
                this.state.grid.row = null
            },
            /**
             * 判断是否刚进行过框选，如果是的话则不触发点击事件，并禁止冒泡，避免触发取消选中组件的操作
             */
            isMultiSelected() {
                let flag = false
                let start = this.multiBox.start
                let end = this.multiBox.end
                if (start && end && end - start > 150) {
                    flag = true
                }
                this.multiBox.start = null
                this.multiBox.end = null
                return flag
            },
            /**
             * 禁止右键事件
             */
            stopPropagation(e) {
                e.stopPropagation()
            },
            /**
             * 拖拽框选组件开始
             */
            drawMultiBox(e) {
                e.stopPropagation()
                this.multiBox.start = new Date().getTime()
                this.$set(this.state, 'select', {})
                this.multiBox.top = 0
                this.multiBox.left = 0
                this.multiBox.height = 0
                this.multiBox.width = 0
                this.multiBox.x = e.pageX
                this.multiBox.y = e.pageY
                this.multiBox.scrollLeft = e.currentTarget.scrollLeft
                this.multiBox.scrollTop = e.currentTarget.scrollTop
                this.multiBox.display = 'block'
                // 增加鼠标事件
                window.addEventListener('mousemove', this.scaleMultiBox)
                window.addEventListener('mouseup', this.endMultiBox)
            },
            /**
             * 拖拽框选组件移动
             */
            scaleMultiBox(e) {
                e.stopPropagation()
                let offsetX = e.pageX - this.multiBox.x
                let offsetY = e.pageY - this.multiBox.y
                let left = offsetX > 0 ? this.multiBox.x : this.multiBox.x + offsetX
                left = left - 225 + this.multiBox.scrollLeft
                this.multiBox.left = left
                let top = offsetY > 0 ? this.multiBox.y : this.multiBox.y + offsetY
                top = top + this.multiBox.scrollTop
                this.multiBox.top = top
                this.multiBox.width = Math.abs(offsetX)
                this.multiBox.height = Math.abs(offsetY)
            },
            /**
             * 拖拽框选组件结束
             */
            endMultiBox(e) {
                e.stopPropagation()
                this.multiBox.display = 'none'
                this.multiBox.end = new Date().getTime()
                let box = this.multiBox
                for (let key in this.config.components) {
                    let item = this.config.components[key]
                    let scale = this.paintScale.index
                    let style = item.config.style
                    let anchorY = (style.top - 10) * scale + 30
                    let anchorX = (style.left - 10) * scale + 30
                    let bottom = (style.top + style.height + 20) * scale + 30
                    let right = (style.left + style.width + 20) * scale + 30
                    if (anchorY >= box.top && anchorX >= box.left &&
                        bottom <= box.top + box.height && right <= box.left + box.width) {
                        this.$set(this.state.select, key, item);
                    }
                }
                window.removeEventListener('mousemove', this.scaleMultiBox)
                window.removeEventListener('mouseup', this.endMultiBox)
            },
            /**
             * 放置组件
             */
            dropComponent(e) {
                e.preventDefault()
                // 当从菜单拖放组件时
                if (this.state.menu.dragMode) {
                    if (!this.config.components) {
                        this.config.components = {}
                    }
                    if (!this.config.sequence) {
                        this.config.sequence = []
                    }
                    let id = this.state.menu.component.config.id
                    this.config.sequence.unshift(id)
                    this.state.menu.component.config.style = _.assignIn(this.state.menu.component.config.style, {
                        left: e.offsetX,
                        top: e.offsetY
                    })
                    this.$set(this.config.components, id, this.state.menu.component)
                }
            },
            /**
             * 计算背景样式
             */
            calcBgStyle() {
                let scale = this.paintScale.index
                return {
                    width: (this.config.base.size.width * scale + 60) + 'px',
                    height: (this.config.base.size.height * scale + 60) + 'px'
                }
            },
            /**
             * 计算画布样式
             */
            calcPaintStyle() {
                return {
                    width: this.config.base.size.width + 'px',
                    height: this.config.base.size.height + 'px',
                    backgroundColor: this.config.base.backgroundColor,
                    backgroundImage: this.config.base.backgroundImage ?
                        'url(' + this.config.base.backgroundImage + ')' : 'none',
                    backgroundSize: this.calcBgImageSize(),
                    transform: 'scale(' + this.paintScale.index + ')'
                }
            },
            /**
             * 根据配置项调整背景图片尺寸
             */
            calcBgImageSize() {
                switch (this.config.base.scaleMode) {
                    case 1:
                        return 'auto 100%'
                    case 2:
                        return '100% 100%'
                    default:
                        return '100% auto'
                }
            },
            /**
             * 计算顶部辅助线样式
             */
            calcAuxTop(item) {
                if (!item || !item.config || !item.config.style || !item.config.style.top) {
                    return
                }
                return {
                    top: item.config.style.top * -1 + 'px',
                    height: item.config.style.top + 'px',
                }
            },
            /**
             * 计算左边辅助线样式
             */
            calcAuxLeft(item) {
                if (!item || !item.config || !item.config.style || !item.config.style.left) {
                    return
                }
                return {
                    left: item.config.style.left * -1 + 'px',
                    width: item.config.style.left + 'px',
                }
            },
            /**
             * 选中或者在框选范围内时，激活active样式
             */
            calcActiveStyle(item) {
                if (this.state.contextMenu.refId == item.config.id) {
                    return 'active current'
                }
                if (this.state.select[item.config.id]) {
                    return 'active';
                }
                if (this.multiBox.display == 'none') {
                    return ''
                }
                let scale = this.paintScale.index
                let style = item.config.style
                let anchorY = (style.top - 10) * scale + 30
                let anchorX = (style.left - 10) * scale + 30
                let bottom = (style.top + style.height + 20) * scale + 30
                let right = (style.left + style.width + 20) * scale + 30
                let box = this.multiBox
                if (anchorY >= box.top && anchorX >= box.left &&
                    bottom <= box.top + box.height && right <= box.left + box.width) {
                    return 'active';
                }
                return '';
            },
            /**
             * 判断当前组件是否选中
             */
            calcResizeStyle(item) {
                let select = this.state.select
                if (Object.keys(select).length === 1 && select.hasOwnProperty(item.config.id)) {
                    return true
                }
                return false
            },
            /**
             * 点击选中当前组件
             */
            activeSingleBox(e, item) {
                e.stopPropagation()
                if (this.isDragged()) {
                    return
                }
                let comps = {};
                comps[item.config.id] = item
                this.$set(this.state, "select", comps)
            },
            /**
             * 点击选中当前组件
             */
            activeMultiBox(e, item) {
                e.stopPropagation()
                if (this.isDragged()) {
                    return
                }
                let id = item.config.id
                if (this.state.select[id]) {
                    this.$delete(this.state.select, id)
                } else {
                    this.$set(this.state.select, id, item)
                }
            },
            /**
             * 判断是否刚进行过拖拽，如果是的话则不触发点击事件
             */
            isDragged() {
                let flag = false
                let start = this.state.drag.start
                let end = this.state.drag.end
                if (start && end && end - start > 150) {
                    flag = true
                }
                this.state.drag.start = null
                this.state.drag.end = null
                return flag
            },
            /**
             * 拖拽组件角落
             */
            dragCorner(e, item, position) {
                e.stopPropagation()
                this.cornerPosition = position
                this.resizeId = item.config.id
                // 获取位移的参照原点
                this.cornerPoint.x = e.screenX
                this.cornerPoint.y = e.screenY
                // 增加鼠标事件
                window.addEventListener('mousemove', this.resizeBox)
                window.addEventListener('mouseup', this.endResize)
            },
            /**
             * 拖拽组件角落
             */
            resizeBox(e, item) {
                // 获取偏移量
                let offsetX = parseInt((e.screenX - this.cornerPoint.x) / this.paintScale.index)
                let offsetY = parseInt((e.screenY - this.cornerPoint.y) / this.paintScale.index)
                // 将参照原点更新为移动后的坐标，作为下一次移动的参照点
                this.cornerPoint.x = e.screenX
                this.cornerPoint.y = e.screenY
                // 获取组件的样式
                let style = this.config.components[this.resizeId].config.style
                // 根据拖动的角位置，计算调整后的尺寸，并修改组件的属性
                switch (this.cornerPosition) {
                    case "tl":
                        style.top += offsetY
                        style.left += offsetX
                        style.width -= offsetX
                        style.height -= offsetY
                        break
                    case "tr":
                        style.top += offsetY
                        style.width += offsetX
                        style.height -= offsetY
                        break
                    case "bl":
                        style.left += offsetX
                        style.width -= offsetX
                        style.height += offsetY
                        break
                    case "br":
                        style.width += offsetX
                        style.height += offsetY
                        break
                    case "t":
                        style.top += offsetY
                        style.height -= offsetY
                        break
                    case "l":
                        style.left += offsetX
                        style.width -= offsetX
                        break
                    case "r":
                        style.width += offsetX
                        break
                    case "b":
                        style.height += offsetY
                        break
                    default:
                        return
                }
            },
            /**
             * 结束调整大小
             */
            endResize(e) {
                window.removeEventListener('mousemove', this.resizeBox)
                window.removeEventListener('mouseup', this.endResize)
                e.stopPropagation()
            },
            /**
             * 拖拽组件，必须先点击选中，然后才可以拖拽
             */
            dragBox(e, item) {
                e.stopPropagation()
                let select = this.state.select
                if (!select.hasOwnProperty(item.config.id)) {
                    return
                }
                // 当启用栅格模式时，打开拖动标志，初始化鼠标位置
                if (this.config.base.enableGrid) {
                    this.state.drag.isDragging = true
                    // 鼠标相对于画布的定位，需要减去画布相对背景的边距30，其中x减去左侧菜单和列表的宽度225
                    this.state.grid.dragMode.mouse.x = e.pageX - 225 - 30
                    this.state.grid.dragMode.mouse.y = e.pageY - 30
                }
                this.state.drag.start = new Date().getTime()
                this.state.drag.id = item.config.id
                // 获取位移的参照原点
                this.originalPosition.x = e.screenX
                this.originalPosition.y = e.screenY
                // 增加鼠标事件
                window.addEventListener('mousemove', this.moveBox)
                window.addEventListener('mouseup', this.endDrag)
            },
            /**
             * 移动组件位置
             */
            moveBox(e) {
                // 当启用栅格模式时，调整鼠标位置
                if (this.config.base.enableGrid) {
                    // 鼠标相对于画布的定位，需要减去画布相对背景的边距30，其中x减去左侧菜单和列表的宽度225
                    this.state.grid.dragMode.mouse.x = e.pageX - 225 - 30
                    this.state.grid.dragMode.mouse.y = e.pageY - 30
                }
                // 获取偏移量
                let offsetX = parseInt((e.screenX - this.originalPosition.x) / this.paintScale.index)
                let offsetY = parseInt((e.screenY - this.originalPosition.y) / this.paintScale.index)
                // 将参照原点更新为移动后的坐标，作为下一次移动的参照点
                this.originalPosition.x = e.screenX
                this.originalPosition.y = e.screenY
                let select = this.state.select
                for (let key in select) {
                    let style = select[key].config.style
                    style.left += offsetX
                    style.top += offsetY
                }
                e.stopPropagation()
            },
            /**
             * 停止拖拽
             */
            endDrag(e) {
                e.stopPropagation()
                this.state.drag.end = new Date().getTime()
                if (this.config.base.enableGrid && this.state.grid.dragMode.column && this.isDragged()) {
                    // 当启用栅格模式时，鼠标指向哪个栅格，就在哪个栅格中增加此组件
                    let select = this.state.select
                    for (let cmpId in select) {
                        let comp = this.config.components[cmpId]
                        if (comp.config.grid.id) {
                            continue
                        }
                        // 并将组件的gridId设置为列id，双向存储
                        comp.config.grid.id = this.state.grid.dragMode.column.id;
                        this.$set(this.state.grid.dragMode.column.components, cmpId, comp)
                    }
                }
                // 当启用栅格模式时，关闭拖动标志
                this.state.drag.isDragging = false
                this.state.grid.dragMode.mouse.x = null
                this.state.grid.dragMode.mouse.y = null
                // this.state.grid.dragMode.column = null
                window.removeEventListener('mousemove', this.moveBox)
                window.removeEventListener('mouseup', this.endDrag)
            },
            /**
             * 显示右键菜单
             */
            showContextMenu(e, item) {
                let select = this.state.select
                if (item) {
                    let id = item.config.id;
                    if (!select.hasOwnProperty(id)) {
                        let newSelect = {}
                        newSelect[id] = item
                        this.$set(this.state, 'select', newSelect)
                    }
                }
                this.state.contextMenu.show = true
                this.state.contextMenu.refId = item.config.id
                this.state.contextMenu.ids = Object.keys(select)
                this.state.contextMenu.style.left = e.pageX
                this.state.contextMenu.style.top = e.pageY
                window.addEventListener('click', this.hideContextMenu)
            },
            /**
             * 隐藏右键菜单
             */
            hideContextMenu() {
                this.state.contextMenu.refId = null
                this.state.contextMenu.ids = null
                this.state.contextMenu.show = false
                window.removeEventListener('click', this.hideContextMenu)
            },
            /**
             * 动态加载组件
             */
            loadComponent() {
                if (!this.config.sequence || !this.config.sequence.length) {
                    return
                }
                for (let i = 0; i < this.config.sequence.length; i++) {
                    let id = this.config.sequence[i]
                    let comp = this.config.components[id]
                    if (!comp || !comp.name || this.loader[comp.config.id]) {
                        continue
                    }
                    let path = "components/screen/layer/" + comp.category + "/" + comp.name
                    this.loader[comp.config.id] = require("../../" + path + ".vue")
                }
            },
            /**
             * 计算容器样式
             */
            calcStyle(item) {
                let id = item.config.id
                let index = this.getIndexById(id)
                let style = item.config.style
                let zIndex = this.state.select.hasOwnProperty(id) ? 999 : this.config.sequence.length - index
                let result = {
                    zIndex: style.zIndex || zIndex,
                    top: style && style.top ? style.top + 'px' : 0,
                    left: style && style.left ? style.left + 'px' : 0,
                }
                return result
            },
            /**
             * 根据id获取组件索引
             */
            getIndexById(itemId) {
                for (let i = 0; i < this.config.sequence.length; i++) {
                    let id = this.config.sequence[i]
                    if (id == itemId) {
                        return i
                    }
                }
            },
            /**
             * 缩放调整
             */
            scale(step) {
                this.paintScale.index += step * this.paintScale.step
            },
            /**
             * 计算多选框是否显示
             */
            calcMultiBoxStyle() {
                if (Object.keys(this.state.select).length > 1) {
                    return true
                }
                return false
            },
            /**
             * 计算多选框样式
             */
            calcMultiBox() {
                let select = this.state.select;
                let keyArr = Object.keys(select);
                if (!select || keyArr.length <= 1) {
                    return {}
                }
                let baseStyle = select[keyArr[0]].config.style
                let top = baseStyle.top;
                let left = baseStyle.left;
                let bottom = top + baseStyle.height;
                let right = left + baseStyle.width;

                for (let i = 1; i < keyArr.length; i++) {
                    let style = select[keyArr[i]].config.style
                    top = Math.min(top, style.top);
                    left = Math.min(left, style.left);
                    bottom = Math.max(bottom, style.top + style.height);
                    right = Math.max(right, style.left + style.width);
                }
                return {
                    top: top + 'px',
                    left: left + 'px',
                    height: bottom - top + 'px',
                    width: right - left + 'px',
                }
            },
            /**
             * 计算顶部辅助线样式
             */
            calcMultiTop() {
                let style = this.calcMultiLineStyle()
                return {
                    left: style.left + 'px',
                    height: style.top + 'px',
                }
            },
            /**
             * 计算左边辅助线样式
             */
            calcMultiLeft() {
                let style = this.calcMultiLineStyle()
                return {
                    top: style.top + 'px',
                    width: style.left + 'px',
                }
            },
            /**
             * 计算多选辅助线样式
             */
            calcMultiLineStyle() {
                let select = this.state.select;
                let keyArr = Object.keys(select);
                if (!select || keyArr.length <= 1) {
                    return {}
                }
                let baseStyle = select[keyArr[0]].config.style
                let top = baseStyle.top;
                let left = baseStyle.left;
                for (let i = 1; i < keyArr.length; i++) {
                    let style = select[keyArr[i]].config.style
                    top = Math.min(top, style.top);
                    left = Math.min(left, style.left);
                }
                return {
                    top: top,
                    left: left
                }
            },
            /**
             * 计算顶部辅助线文本
             */
            calcMultiTopText() {
                let style = this.calcMultiLineStyle()
                return style.top + 'px'
            },
            /**
             * 计算左边辅助线文本
             */
            calcMultiLeftText() {
                let style = this.calcMultiLineStyle()
                return style.left + 'px'
            },
        },
        watch: {
            /**
             * 监听组件清单，变化后重新载入组件
             */
            "config.components": {
                immediate: true,
                handler: function () {
                    this.loadComponent()
                }
            }
        },
        components: {
            'Grid': Grid
        }
    }
</script>

