                        <el-row v-if="i > 0">
                            <el-col :span="4">
                                <el-button type="text"
                                           size="mini"
                                           @click="removeSeries(i)">删除
                                </el-button>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">系列</el-col>
                            <el-col :span="11">
                                <el-input v-model="component.config.option.series[i].name"
                                          size="mini"
                                          controls-position="right"
                                          @change="changeSeriesName">
                                </el-input>
                                <div class="description">名称</div>
                            </el-col>
                            <el-col :span="4" :offset="1">
                                <el-color-picker v-model="component.config.option.color[i]"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description">颜色</div>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">数据集</el-col>
                            <el-col :span="16">
                                <el-select v-model="component.config.option.series[i].datasetIndex"
                                           placeholder="请选择"
                                           size="mini">
                                    <el-option v-for="item in seriesOption"
                                               :key="item.value"
                                               :label="item.label"
                                               :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-col>
                        </el-row>

                    </el-collapse-item>
                </template>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseScatter',
        props: ['component', 'state'],
        data() {
            return {
                //可见
                visibility: [
                    {label: '显示', value: 'visible'},
                    {label: '隐藏', value: 'hidden'},
                ],
                legend: {
                    orient: [
                        {label: '水平', value: 'horizontal'},
                        {label: '垂直', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '左侧', value: 'left'},
                        {label: '居中', value: 'center'},
                        {label: '右侧', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '顶部', value: 'top'},
                        {label: '居中', value: 'middle'},
                        {label: '底部', value: 'bottom'},
                    ],
                    legendX:0
                },
                seriesOption: []
            }
        },
        created() {
            this.initSeriesOption();
            this.init()
        },
        computed: {},
        methods: {
            init(){
                this.legend.legendX=parseInt(this.component.config.option.legend.left);
            },
            /**
             * 修改图例水平位置
             */
            changeLengXPositon(val){
                this.component.config.option.legend.left=val+'%';
            },
            /**
             * 初始化缓存各数据集对应的值
             */
            initSeriesOption() {
                let opt = []
                if (this.component.config.option.dataset && this.component.config.option.dataset.length) {
                    for (let i = 0; i < this.component.config.option.dataset.length; i++) {
                        let model = this.component.config.option.dataset[i]['source']
                            && this.component.config.option.dataset[i]['source'][0]
                        if (model[4]) {
                            opt.push({label: model[4], value: i})
                        }
                    }
                }
                this.seriesOption = opt
            },
            /**
             * 设置隐藏范围参数
             */
            setOutOfRangeSize() {
                this.component.config.option.visualMap[0].outOfRange.symbolSize =
                    this.component.config.option.visualMap[0].inRange.symbolSize
            },
            /**
             * 增加一个系列
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper使用的是对象，此处遍历时，需要使用 this.$set() 方法赋值，
                // 否则无法调用mapper的setter，从而无法触发dom的重新渲染
                let seriesName = '系列' + index
                this.component.config.option.legend.data.push(seriesName)
                this.component.config.option.color.push(ColorUtils.randomHex())
                this.component.config.option.series.push({
                    name: seriesName,
                    type: 'scatter',
                    itemStyle: {
                        normal: {
                            opacity: 0.8,
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowOffsetY: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    },
                    datasetIndex: 0
                })
            },
            /**
             * 删除一个系列
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                this.component.config.option.color.splice(index, 1)
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
            },
            /**
             * 修改series名
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            }
        },
        components: {},
    }
</script>
