<template>
    <div class="config-screen">
        <el-tabs type="border-card"
                 :value="calcActiveTab()"
                 stretch>
            <el-tab-pane label="页面设置" name="0">
                <el-row>
                    <el-col :span="8" class="tag">名称</el-col>
                    <el-col :span="16">
                        <el-input v-model="config.name" size="mini"></el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">封面图</el-col>
                    <el-col :span="16">
                        <el-input v-model="config.cover"
                                  placeholder="请输入图片网址"
                                  size="mini" clearable></el-input>
                        <el-upload drag
                                   class="img-upload"
                                   :headers="getToken()"
                                   :action="getUploadUrl()"
                                   :before-upload="beforeUpload"
                                   :on-success="onSuccessCover"
                                   :on-Error="onError"
                                   :show-file-list="false">
                            <img v-if="config.cover"
                                 :src="config.cover"
                                 class="img-preview">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                        </el-upload>
                        <div v-if="config.cover"
                             class="img-cover">
                            <i class="icon iconfont icon-delete" @click="removeCoverImage()"></i>
                        </div>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">屏幕大小</el-col>
                    <el-col :span="8">
                        <el-input-number v-model="config.base.size.width"
                                         size="mini"
                                         controls-position="right"
                                         :min="0">
                        </el-input-number>
                        <div class="description">宽度</div>
                    </el-col>
                    <el-col :span="8">
                        <el-input-number v-model="config.base.size.height"
                                         size="mini"
                                         controls-position="right"
                                         :min="0">
                        </el-input-number>
                        <div class="description">高度</div>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">自适应栅格</el-col>
                    <el-col :span="8">
                        <el-checkbox v-model="config.base.enableGrid"></el-checkbox>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">背景色</el-col>
                    <el-col :span="4">
                        <el-color-picker v-model="config.base.backgroundColor"
                                         size="mini" show-alpha></el-color-picker>
                    </el-col>
                    <el-col :span="4">
                        <el-button type="text" size="mini" @click="resetBgColor">重置</el-button>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">背景图</el-col>
                    <el-col :span="16">
                        <el-input v-model="config.base.backgroundImage"
                                  placeholder="请输入图片网址"
                                  size="mini" clearable></el-input>
                        <el-upload drag
                                   class="img-upload"
                                   :headers="getToken()"
                                   :action="getUploadUrl()"
                                   :before-upload="beforeUpload"
                                   :on-success="onSuccess"
                                   :on-Error="onError"
                                   :show-file-list="false">
                            <img v-if="config.base.backgroundImage"
                                 :src="config.base.backgroundImage"
                                 class="img-preview">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                        </el-upload>
                        <div v-if="config.base.backgroundImage"
                             class="img-cover">
                            <i class="icon iconfont icon-delete" @click="removeBgImage()"></i>
                        </div>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">背景缩放方式</el-col>
                    <el-col :span="16">
                        <el-row>
                            <el-radio v-model="config.base.scaleMode" label="0">等比缩放宽度铺满</el-radio>
                        </el-row>
                        <el-row>
                            <el-radio v-model="config.base.scaleMode" label="1">等比缩放高度铺满</el-radio>
                        </el-row>
                        <el-row>
                            <el-radio v-model="config.base.scaleMode" label="2">全屏铺满</el-radio>
                        </el-row>
                    </el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane v-if="config.base.enableGrid && state.grid.configMode" label="自适应栅格" name="1">
                <el-collapse value="0" accordion>
                    <el-collapse-item title="行属性" name="0">
                        <el-row>
                            <el-col :span="8" class="tag">高度</el-col>
                            <el-col :span="8">
                                <el-input-number v-model="state.grid.row.style.height"
                                                 size="mini"
                                                 controls-position="right"
                                                 :min="0">
                                </el-input-number>
                                <div class="description">百分比</div>
                            </el-col>
                        </el-row>
                    </el-collapse-item>
                    <el-collapse-item title="列" name="1">
                        <el-row>
                            <el-col :span="4">
                                <el-button type="text" size="mini" @click="addColumn">增加列</el-button>
                            </el-col>
                        </el-row>
                        <el-collapse value="0">
                            <template v-for="(column, i) in state.grid.row.columns">
                                <el-collapse-item :key="i" :title="'列' + i" :name="i + ''">
                                    <el-row v-if="i > 0">
                                        <el-col :span="4">
                                            <el-button type="text" size="mini" @click="removeColumn(i)">{{'删除列' + i}}
                                            </el-button>
                                        </el-col>
                                    </el-row>
                                    <el-row>
                                        <el-col :span="8" class="tag">列宽</el-col>
                                        <el-col :span="8">
                                            <el-input-number v-model="column.style.xl"
                                                             size="mini"
                                                             controls-position="right"
                                                             :max="24"
                                                             :min="0">
                                            </el-input-number>
                                            <div class="description">≥1920px</div>
                                        </el-col>
                                        <el-col :span="8">
                                            <el-input-number v-model="column.style.lg"
                                                             size="mini"
                                                             controls-position="right"
                                                             :max="24"
                                                             :min="0">
                                            </el-input-number>
                                            <div class="description">≥1200px</div>
                                        </el-col>
                                        <el-col :span="8">
                                            <el-input-number v-model="column.style.md"
                                                             size="mini"
                                                             controls-position="right"
                                                             :max="24"
                                                             :min="0">
                                            </el-input-number>
                                            <div class="description">≥992px</div>
                                        </el-col>
                                        <el-col :span="8">
                                            <el-input-number v-model="column.style.sm"
                                                             size="mini"
                                                             controls-position="right"
                                                             :max="24"
                                                             :min="0">
                                            </el-input-number>
                                            <div class="description">≥768px</div>
                                        </el-col>
                                        <el-col :span="8">
                                            <el-input-number v-model="column.style.xs"
                                                             size="mini"
                                                             controls-position="right"
                                                             :max="24"
                                                             :min="0">
                                            </el-input-number>
                                            <div class="description">768px</div>
                                        </el-col>
                                    </el-row>
                                    <el-row>
                                        <template v-if="state.grid.row.columns[i].rows">
                                            <el-tabs @tab-remove="removeRow($event, i)"
                                                     @tab-add="addRow(i)"
                                                     editable>
                                                <el-tab-pane v-for="(row, index) in state.grid.row.columns[i].rows"
                                                             :key="index"
                                                             :label="'行' + index"
                                                             :name="index + ''">
                                                    <el-row>
                                                        <el-col :span="8" class="tag">高度</el-col>
                                                        <el-col :span="8">
                                                            <el-input-number v-model="row.style.height"
                                                                             size="mini"
                                                                             controls-position="right"
                                                                             :min="0">
                                                            </el-input-number>
                                                            <div class="description">百分比</div>
                                                        </el-col>
                                                    </el-row>
                                                </el-tab-pane>
                                            </el-tabs>
                                        </template>
                                    </el-row>
                                </el-collapse-item>
                            </template>
                        </el-collapse>
                    </el-collapse-item>
                </el-collapse>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
    import API from '../../../config/api'
    // import CONST from '../../../config/const'

    export default {
        name: "Screen",
        props: ["config", "state"],
        data() {
            return {
                default: {
                    // 屏幕背景色
                    bgColor: null
                }
            }
        },
        created() {
            this.default = {
                bgColor: this.config.base.backgroundColor
            }
        },
        methods: {
            calcActiveTab() {
                return this.state.grid.configMode === true ? '1' : '0'
            },
            resetBgColor() {
                this.config.base.backgroundColor = this.default.bgColor
            },
            /**
             * 上传之前
             */
            beforeUpload(file) {
                let name = file.name
                let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
                if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg' && ext !== 'gif') {
                    this.$message({
                        message: "只支持jpg、png、bmp文件",
                        type: "error"
                    })
                    return false
                }
            },
            /**
             * 获取上传地址
             */
            getUploadUrl() {
                return API.ATTACHMENT.UPLOAD
            },
            /**
             * 获取token作文参数上传
             */
            getToken() {
                return {token: this.$store.state.token}
            },
            /**
             * 上传成功
             */
            onSuccess(res) {
                this.config.base.backgroundImage = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 上传封面成功
             */
            onSuccessCover(res) {
                this.config.cover = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 上传失败
             */
            onError(err) {
                this.$message({
                    message: err.data.message,
                    type: "error"
                })
            },
            /**
             * 移除背景图
             */
            removeBgImage() {
                this.config.base.backgroundImage = null
            },
            /**
             * 移除封面图
             */
            removeCoverImage() {
                this.config.cover = null
            },
            /**
             * 添加列
             */
            addColumn() {
                this.state.grid.row.columns.push({
                    id: "col-" + new Date().getTime(),
                    style: {xs: 1, sm: 1, md: 1, lg: 1, xl: 1},
                    rows: [],
                    components: {}
                })
            },
            /**
             * 删除列
             */
            removeColumn(index) {
                // 删除列时，使所有组件恢复自由状态
                let column = this.state.grid.row.columns[index]
                this.shiftComponentInColumn(column)
                this.state.grid.row.columns.splice(index, 1)
            },
            /**
             * 弹出栅格列中的组件，使其回归自由状态
             */
            shiftComponentInColumn(column) {
                let components = column.components
                if (components) {
                    for (let key in components) {
                        // 首先要移除组件所在的dom，否则容易出现此处还未销毁，就在Paint上渲染，
                        // 导致echarts.init找到的dom还是此处的dom，从而无法正确加载的问题
                        let boxId = this.getCmpBoxIdInGrid(column.id, key)
                        let dom = document.getElementById(boxId)
                        let parent = dom.parentElement
                        parent.removeChild(dom)
                        // 组件和column互相取消关联
                        this.config.components[key].config.grid.id = null
                    }
                }
            },
            /**
             * 计算组件在栅格中时的box id
             */
            getCmpBoxIdInGrid(columnId, cmpId) {
                return columnId + '-' + cmpId
            },
            /**
             * 增加行
             * @param colIndex
             */
            addRow(colIndex) {
                // 增加行时，如果此列已包含组件，则将所有的组件置入新增行的唯一列中
                let components = this.state.grid.row.columns[colIndex].components
                let subComps = {}
                if (components) {
                    for (let key in components) {
                        subComps[key] = components[key]
                    }
                }
                this.state.grid.row.columns[colIndex].components = {}
                this.state.grid.row.columns[colIndex].rows.push({
                    style: {height: 20},
                    columns: [{
                        id: "col-" + new Date().getTime(),
                        style: {xs: 24, sm: 24, md: 24, lg: 24, xl: 24},
                        rows: [],
                        components: subComps
                    }]
                })

            },
            /**
             * 移除行
             * @param rowIndex
             * @param colIndex
             */
            removeRow(rowIndex, colIndex) {
                // 删除行时，递归其中所有列的组件，使所有组件恢复自由状态
                let row = this.state.grid.row.columns[colIndex].rows[rowIndex]
                let columns = row.columns
                for (let i = 0; i < columns.length; i++) {
                    this.shiftComponentRecursion(columns[i])
                }
                this.state.grid.row.columns[colIndex].rows.splice(rowIndex, 1);
            },
            /**
             * 递归弹出组件
             */
            shiftComponentRecursion(column) {
                // 当列中有组件时，弹出所有组件
                let compSize = Object.getOwnPropertyNames(column.components).length
                if (compSize > 0) {
                    this.shiftComponentInColumn(column)
                    return
                }
                let rowSize = Object.getOwnPropertyNames(column.rows).length
                if (rowSize > 0) {
                    for (let i = 0; i < column.rows.length; i++) {
                        let row = column.rows[i]
                        if (!row.columns.length) {
                            continue
                        }
                        for (let j = 0; j < row.columns.length; j++) {
                            this.shiftComponentRecursion(row.column[j])
                        }
                    }
                }
            }
        }
    }
</script>

<style scoped>

</style>
