<template>
    <el-dialog width="60%"
               top="20px"
               title="自定义组件"
               :visible.sync="state.editMode.visible"
               :close-on-click-modal="false">
        <el-form v-if="state.editMode.item" :model="state.editMode.item" :rules="rules" size="small" label-width="80px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="名称" prop="text">
                        <el-input v-model="state.editMode.item.text"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="类型" prop="category">
                        <el-select v-model="state.editMode.item.category"
                                   @change="changeCategory($event)">
                            <template v-for="(item, i) in dynamicComponents">
                                <el-option :key="i" :label="item.text" :value="item.value"></el-option>
                            </template>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-form-item label="图片" prop="image">
                    <el-input v-model="state.editMode.item.image"
                              placeholder="请输入图片网址"
                              size="mini" clearable></el-input>
                    <el-upload drag
                               class="img-upload"
                               :headers="getToken()"
                               :action="getUploadUrl()"
                               :before-upload="beforeUpload"
                               :on-success="onSuccessCover"
                               :on-Error="onError"
                               :show-file-list="false">
                        <img v-if="state.editMode.item.image"
                             :src="state.editMode.item.image"
                             class="img-preview">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                    </el-upload>
                    <div v-if="state.editMode.item.image"
                         class="img-cover">
                        <i class="icon iconfont icon-delete" @click="removeCoverImage()"></i>
                    </div>
                </el-form-item>
            </el-row>
            <el-form-item label="配置" prop="config">
                <el-input v-model="state.editMode.item.config"
                          type="textarea"
                          :rows="16"
                          size="mini"
                          resize="none">
                </el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" type="primary" @click="save">保 存</el-button>
            <el-button size="mini" @click="state.editMode.visible = false">取 消</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import CONST from '../../config/const'
    import API from "../../config/api";

    export default {
        name: "DynamicComponentEdit",
        props: ['config', 'state'],
        data() {
            return {
                dynamicComponents: CONST.DYNAMIC_COMPONENTS,
                rules: {
                    text: [
                        {required: true, message: '名称不可为空', trigger: 'blur'}
                    ],
                    category: [
                        {required: true, message: '类型不可为空', trigger: 'blur'}
                    ],
                    name: [
                        {required: true, message: '不可为空', trigger: 'blur'}
                    ],
                    image: [
                        {required: true, message: '配置不可为空', trigger: 'blur'}
                    ],
                    config: [
                        {required: true, message: '配置不可为空', trigger: 'blur'}
                    ],
                },
            }
        },
        methods: {
            /**
             * 加载配置
             */
            save() {
                this.ajax({
                    url: API.DYNAMIC_COMPONENT.SAVE,
                    method: CONST.AJAX.POST,
                    data: this.state.editMode.item,
                }).then(response => {
                    if (response.success) {
                        this.$message(CONST.MESSAGE.SAVE_SUCCESS)
                        this.state.editMode.visible = false
                    }
                })
            },
            /**
             * 修改组件类型
             */
            changeCategory(val) {
                for (let i = 0; i < this.dynamicComponents.length; i++) {
                    let opt = this.dynamicComponents[i]
                    if (opt.value !== val) {
                        continue
                    }
                    this.state.editMode.item.name = opt.name
                    return
                }
            },
            /**
             * 上传之前
             */
            beforeUpload(file) {
                let name = file.name
                let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
                if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg' && ext !== 'gif') {
                    this.$message({
                        message: "只支持jpg、png、bmp文件",
                        type: "error"
                    })
                    return false
                }
            },
            /**
             * 获取上传地址
             */
            getUploadUrl() {
                return API.ATTACHMENT.UPLOAD
            },
            /**
             * 获取token作文参数上传
             */
            getToken() {
                return {token: this.$store.state.token}
            },
            /**
             * 上传封面成功
             */
            onSuccessCover(res) {
                this.state.editMode.item.image = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 上传失败
             */
            onError(err) {
                this.$message({
                    message: err.data.message,
                    type: "error"
                })
            },
            /**
             * 移除封面图
             */
            removeCoverImage() {
                this.state.editMode.item.image = null
            },
        }
    }
</script>

