const DataSourceType = {
    list: [
        {name: 'API', value: 0, script: 'ApiData', optAdd: false},
        {name: '静态数据', value: 1, script: 'StaticData', optAdd: false},
        {name: '数据库', value: 2, script: 'DatabaseData', optAdd: true},
        {name: 'Excel文件', value: 3, script: 'FileData', optAdd: true},
    ],
    getScript: function (value) {
        return this.list[value].script
    },
    get: function (value) {
        return this.list[value]
    }
}

export default DataSourceType
