                            lineStyle: {
                                width: 1,
                                color: ['#737985']
                            }
                        },
                    },
                    yAxis: [{
                        type: 'value',
                        axisLabel: {
                            show: true,
                            margin: 10,
                            textStyle: {
                                fontSize: 14,
                                color: '#ffffff'
                            }
                        },
                        min: 0,
                        max: undefined,
                        axisTick: {
                            show: false,
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: "#ffffff",
                                width: 1,
                            },
                        },
                        splitLine: {
                            show: false,
                            lineStyle: {
                                color: ['#737985']
                            }
                        },
                    }],
                    dataZoom: [{
                        type: 'slider',
                        show: true,
                        height: 30,
                        xAxisIndex: [
                            0
                        ],
                        bottom: 30,
                        start: 0,
                        end: 100,
                        handleIcon: 'path://M306.1,413c0,2.2-1.8,4-4,4h-59.8c-2.2,0-4-1.8-4-4V200.8c0-2.2,1.8-4,4-4h59.8c2.2,0,4,1.8,4,4V413z',
                        handleSize: '110%',
                        handleStyle: {
                            color: "#d3dee5",
                        },
                        textStyle: {
                            color: "#fff",
                            fontSize: 12
                        },
                        borderColor: "#90979c",
                        backgroundColor: 'rgba(47,69,84,0)'
                    }, {
                        type: "inside",
                        disabled: false,
                        start: 0,
                        end: 100
                    }],
                    series: [{
                        name: '女',
                        type: 'bar',
                        barWidth: 20,
                        barGap: 1,
                        stack: '0',
                        itemStyle: {
                            normal: {
                                color: {
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    type: "linear",
                                    global: false,
                                    colorStops: [{
