                            }
                        }
                    }, {
                        name: '男',
                        type: 'bar',
                        barWidth: 20,
                        barGap: 1,
                        stack: '0',
                        itemStyle: {
                            normal: {
                                color: {
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    type: "linear",
                                    global: false,
                                    colorStops: [{
                                        offset: 0,
                                        color: "#3CE7C9"
                                    }, {
                                        offset: 1,
                                        color: "#3CE7C9"
                                    }]
                                },
                                label: {
                                    show: false,
                                    position: 'inside',
                                    color: '#ffffff',
                                    fontSize: 12
                                }
                            }
                        }
                    }, {
                        name: "总数",
                        type: "line",
                        smooth: false,
                        showSymbol: true,
                        symbolSize: 10,
                        symbol: 'circle',
                        itemStyle: {
                            normal: {
                                color: "rgba(252,230,48,1)",
                                label: {
                                    show: true,
                                    position: "top",
                                    color: 'rgba(252,230,48,1)',
                                    fontSize: 12
                                }
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '一月', series0: 327, series1: 709, series2: 1036},
                    {name: '二月', series0: 1776, series1: 1917, series2: 3693},
                    {name: '三月', series0: 507, series1: 2455, series2: 2962},
                    {name: '四月', series0: 1200, series1: 2610, series2: 3810},
                    {name: '五月', series0: 800, series1: 1719, series2: 2519},
                    {name: '六月', series0: 482, series1: 1433, series2: 1915},
                    {name: '七月', series0: 204, series1: 1544, series2: 1748},
                    {name: '八月', series0: 1390, series1: 3285, series2: 4675},
                    {name: '九月', series0: 1001, series1: 5208, series2: 6209},
                    {name: '十月', series0: 951, series1: 3372, series2: 4323},
                    {name: '十一月', series0: 381, series1: 2484, series2: 2865},
                    {name: '十二月', series0: 220, series1: 4078, series2: 4298},
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'value', comment: '系列值0', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        series0: null,
                        series1: null,
                        series2: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let dimensions = []
                for (let key in this.config.transform.mapper) {
                    let dim = this.config.transform.mapper[key] || key
                    if (!this.config.data[0].hasOwnProperty(dim)) {
                        dim = null
                    }
                    dimensions.push(dim)
                }
                this.config.option.dataset = {
                    dimensions: dimensions,
                    source: this.config.data
                }
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
