<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BaseScatter',
        props: ['config', 'base'],
        data() {
            return {
                seriesOption: []
            }
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    color: ['rgba(62, 158, 255, 0.8)'],
                    grid: {
                        top: 50,
                        left: 50,
                        right: 50,
                        bottom: 50
                    },
                    legend: {
                        show: false,
                        orient: 'horizontal',
                        top: 'top',
                        left: '0%',
                        textStyle: {
                            fontSize: 12,
                            color: ['#D4DFF5']
                        },
                        data: ['默认']
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            lineStyle: {
                                color: 'rgba(62, 158, 255, 0.8)',
                                width: 1,
                            }
                        },
                        backgroundColor: '#2F3943',
                        borderColor: 'transparent',
                        borderWidth: 1,
                        padding: [5, 10, 5, 10],
                        textStyle: {
                            color: '#ffffff',
                        },
                        extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)',
                        formatter: '系列: {a}<br/>数据: {c0}'
                    },
                    xAxis: {
                        type: 'category',
                        splitLine: {
                            show: false,
                            interval: 'auto',
                            lineStyle: {
                                color: ['#D4DFF5']
                            }
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#609ee9'
                            }
                        },
                        axisLabel: {
                            show: true,
                            interval:0,
                            rotate:0,
                            margin: 10,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        }
                    },
                    yAxis: {
                        type: 'value',
                        splitLine: {
                            show: false,
                            lineStyle: {
                                color: ['#D4DFF5']
                            }
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#609ee9'
                            }
                        },
                        axisLabel: {
                            show: true,
                            margin: 10,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        },
                        min: 0,
                        max: undefined
                    },
                    visualMap: [{
                        show: true,
                        left: 'right',
                        top: '10%',
                        dimension: 2,
                        min: 0,
                        max: 100,
                        itemWidth: 15,
                        itemHeight: 60,
                        calculable: true,
                        precision: 0.1,
                        text: ['尺寸'],
                        textGap: 15,
                        textStyle: {
                            color: '#fff'
                        },
                        inRange: {
                            symbolSize: [10, 70]
                        },
                        outOfRange: {
                            symbolSize: [10, 70],
                            color: ['rgba(255,255,255,.2)']
                        },
                        controller: {
                            inRange: {
                                color: ['rgba(62, 158, 255, 0.8)']
                            },
                            outOfRange: {
                                color: ['#444']
                            }
                        }
                    }, {
                        show: true,
                        left: 'right',
                        bottom: '10%',
                        dimension: 3,
                        min: 0,
                        max: 100,
                        padding: 0,
                        itemWidth: 15,
                        itemHeight: 60,
                        calculable: true,
                        precision: 0.1,
                        text: ['颜色'],
                        textGap: 15,
                        textStyle: {
                            color: '#fff'
                        },
                        inRange: {
                            colorLightness: [1, 0.5]
                        },
                        outOfRange: {
                            color: ['rgba(255,255,255,.2)']
                        },
                        controller: {
                            inRange: {
                                color: ['rgba(62, 158, 255, 0.8)']
                            },
                            outOfRange: {
                                color: ['#444']
                            }
                        }
                    }],
                    series: [{
                        name: '默认',
                        type: 'scatter',
                        itemStyle: {
                            normal: {
                                opacity: 0.8,
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowOffsetY: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        },
                        datasetIndex: 0
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {x: '1月', y: 26, size: 37, color: 27, series: '北京'},
                    {x: '2月', y: 85, size: 62, color: 71, series: '北京'},
                    {x: '3月', y: 78, size: 38, color: 74, series: '北京'},
                    {x: '4月', y: 21, size: 21, color: 36, series: '北京'},
                    {x: '5月', y: 41, size: 42, color: 46, series: '北京'},
                    {x: '6月', y: 56, size: 52, color: 69, series: '北京'},
                    {x: '7月', y: 64, size: 30, color: 28, series: '北京'},
                    {x: '8月', y: 55, size: 48, color: 74, series: '北京'},
                    {x: '9月', y: 76, size: 85, color: 113, series: '北京'},
                    {x: '10月', y: 91, size: 81, color: 104, series: '北京'},
                    {x: '11月', y: 84, size: 39, color: 60, series: '北京'},
                    {x: '12月', y: 64, size: 51, color: 101, series: '北京'},
                    {x: '1月', y: 55, size: 13, color: 26, series: '上海'},
                    {x: '2月', y: 45, size: 68, color: 48, series: '上海'},
                    {x: '3月', y: 25, size: 43, color: 96, series: '上海'},
                    {x: '4月', y: 78, size: 34, color: 86, series: '上海'},
                    {x: '5月', y: 35, size: 47, color: 43, series: '上海'},
                    {x: '6月', y: 75, size: 68, color: 25, series: '上海'},
                    {x: '7月', y: 43, size: 95, color: 99, series: '上海'},
                    {x: '8月', y: 78, size: 54, color: 55, series: '上海'},
                    {x: '9月', y: 44, size: 57, color: 42, series: '上海'},
                    {x: '10月', y: 45, size: 74, color: 52, series: '上海'},
                    {x: '11月', y: 78, size: 77, color: 79, series: '上海'},
                    {x: '12月', y: 42, size: 90, color: 55, series: '上海'}
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'x', comment: 'x维度值', prop: 'x', state: null},
                            {name: 'y', comment: 'y维度值', prop: 'y', state: null},
                            {name: 'size', comment: '尺寸值', prop: 'size', state: null},
                            {name: 'color', comment: '颜色值', prop: 'color', state: null},
                            {name: 'series', comment: '系列名', prop: 'series', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        x: null,
                        y: null,
                        size: null,
                        color: null,
                        series: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                if (!this.config.data || !this.config.data.length) {
                    return
                }
                let sizeMax = 0
                let colorMax = 0
                let xProp = this.config.transform.mapper.x || 'x'
                let yProp = this.config.transform.mapper.y || 'y'
                let sizeProp = this.config.transform.mapper.size || 'size'
                let colorProp = this.config.transform.mapper.color || 'color'
                let seriesProp = this.config.transform.mapper.series || 'series'
                let dataset
                // 判断是否有分series的属性，如果存在，则分成多个dataset
                if (this.config.data[0][seriesProp]) {
                    this.seriesOption = []
                    dataset = []
                    for (let i = 0; i < this.config.data.length; i++) {
                        let item = this.config.data[i]
                        let seriesValue = item[seriesProp]
                        let datasetIndex = this.seriesOption.length
                        let exist = false
                        for (let j = 0; j < this.seriesOption.length; j++) {
                            if (this.seriesOption[j] == seriesValue) {
                                exist = true
                                datasetIndex = j
                                break
                            }
                        }
                        if (!exist) {
                            this.seriesOption.push(seriesValue)
                            dataset.push({source: []})
                        }
                        let dataModel = []
                        dataModel.push(item[xProp])
                        dataModel.push(item[yProp])
                        dataModel.push(item[sizeProp])
                        dataModel.push(item[colorProp])
                        dataModel.push(item[seriesProp])
                        dataset[datasetIndex]['source'].push(dataModel)
                        if (item[sizeProp] > sizeMax) {
                            sizeMax = item[sizeProp]
                        }
                        if (item[colorProp] > colorMax) {
                            colorMax = item[colorProp]
                        }
                    }
                } else {
                    dataset = [{source: []}]
                    for (let i = 0; i < this.config.data.length; i++) {
                        let item = this.config.data[i]
                        let dataModel = []
                        dataModel.push(item[xProp])
                        dataModel.push(item[yProp])
                        dataModel.push(item[sizeProp])
                        dataModel.push(item[colorProp])
                        if (item[seriesProp]) {
                            dataModel.push(item[seriesProp])
                        }
                        dataset[0]['source'].push(dataModel)
                        if (item[sizeProp] > sizeMax) {
                            sizeMax = item[sizeProp]
                        }
                        if (item[colorProp] > colorMax) {
                            colorMax = item[colorProp]
                        }
                    }
                }
                this.config.option.dataset = dataset
                this.config.option.visualMap[0].max = _.ceil(sizeMax, ((sizeMax + '').length - 1) * -1)
                this.config.option.visualMap[1].max = _.ceil(colorMax, ((colorMax + '').length - 1) * -1)
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
