<template>
    <el-dialog width="90%"
               :visible.sync="templateConfig.visible"
               :close-on-click-modal="false">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="自定义模板" name="first">
                <template v-if="template.list && template.list.length" style="height: 500px;">
                    <template v-for="(item, i) in template.list">
                        <div class="card" :key="i">
                            <template v-if="item.cover">
                                <div class="img-cover"></div>
                            </template>
                            <template v-else>
                                <img :src="item.cover" class="img-cover">
                            </template>
                            <div class="title-bar">
                                <el-row>
                                    <el-col :span="24" class="name">{{item.name || '未命名'}}</el-col>
                                </el-row>
                            </div>
                            <div class="toolbar">
                                <div class="delete">
                                    <el-tooltip class="item" effect="dark" content="删除" placement="top">
                                        <i class="icon iconfont icon-delete" @click="deleteTemplate(i)"></i>
                                    </el-tooltip>
                                </div>
                                <div class="edit">
                                    <el-button type="primary" size="mini" round @click="seletTemplate(i)"> 选 择
                                    </el-button>
                                </div>
                            </div>
                        </div>
                    </template>
                </template>
            </el-tab-pane>
            <el-tab-pane label="应用模板" name="second">
                <template v-if="template.list && template.list.length">
                    <template v-for="(item, i) in template.list">
                        <div class="card" :key="i">
                            <template v-if="item.cover">
                                <div class="img-cover"></div>
                            </template>
                            <template v-else>
                                <img :src="item.cover" class="img-cover">
                            </template>
                            <div class="title-bar">
                                <el-row>
                                    <el-col :span="24" class="name">{{item.name || '未命名'}}</el-col>
                                </el-row>
                            </div>
                            <div class="toolbar">
                                <div class="edit">
                                    <el-button type="primary" size="mini" round @click="seletTemplate(i)"> 选 择
                                    </el-button>
                                </div>
                            </div>
                        </div>
                    </template>
                </template>
            </el-tab-pane>
        </el-tabs>
        <div v-show="activeName==='first'" slot="footer" class="dialog-footer">
            <el-button v-show="templateConfig.isNew === false " size="mini" type="primary" @click="open('1')">更新当前模板
            </el-button>
            <el-button size="mini" type="primary" @click="open('0')">保存为新模板</el-button>
        </div>
    </el-dialog>

</template>

<script>
    import '../../assets/styles/overview/main.scss'
    import API from '../../config/api'
    import CONST from '../../config/const'
    import _ from 'lodash'

    export default {
        name: "ModelList",
        // props: ['templateConfig','config'],
        props: {
            templateConfig: {type: Object},
            config: {type: Object}
        },
        data() {
            return {
                //tab页签编码
                activeName: 'first',
                //模板请求接口参数配置
                type: "[[0,type,0,0]]",
                //列表
                template: {
                    index: null,
                    list: null
                },
                subConfig: this.config
            };
        },
        mounted() {
            this.listTemplate()
        },
        methods: {
            handleClick(tab, event) {
                if ('first' === this.activeName) {
                    this.type = "[[0,type,0,0]]";
                } else if ('second' === this.activeName) {
                    this.type = "[[0,type,0,1]]";
                }
                this.listTemplate();
            },
            /**
             * 自定义模板保存
             */
            save() {
                let data = _.cloneDeep(this.templateConfig.dataSource);
                data.type = 1;//设置自定义类型
                data.id = '';
                data.category = '模板风格类型预留';
                this.ajax({
                    url: API.TEMPLATE.SAVE,
                    method: CONST.AJAX.POST,
                    data: data,
                }).then(response => {
                    if (!data.id) {
                        data.id = response.data.id
                    }
                    this.$message(CONST.MESSAGE.SAVE_SUCCESS)
                    this.listTemplate();
                })
            },
            /**
             * 自定义模板更新
             */
            update() {
                let data = _.cloneDeep(this.templateConfig.dataSource);
                data.type = 1;//设置自定义类型
                this.ajax({
                    url: API.TEMPLATE.SAVE,
                    method: CONST.AJAX.POST,
                    data: data,
                }).then(response => {
                    if (!data.id) {
                        data.id = response.data.id
                    }
                    this.$message(CONST.MESSAGE.SAVE_SUCCESS)
                    this.listTemplate();
                })
            },
            /**
             * 自定义模板列表
             */
            listTemplate() {
                this.ajax({
                    url: API.TEMPLATE.LIST,
                    method: CONST.AJAX.POST,
                    data: {"filters": this.type}
                }).then(response => {
                    this.template.list = response.data
                })
            },
            /**
             * 选择模板
             */
            seletTemplate(index) {
                //反向修改父组件config和templateConfig
                let _id = this.template.list[index].id;
                if (_id) {
                    if (this.activeName === 'second') {
                        _id = '';
                        this.$set(this.templateConfig, 'isNew', true);
                    } else {
                        this.$set(this.templateConfig, 'isNew', false);
                    }
                }
                this.$set(this.config, 'id', _id);
                this.$set(this.config, 'name', this.template.list[index].name);
                this.$set(this.config, 'base', JSON.parse(this.template.list[index].base));
                this.$set(this.config, 'sequence', JSON.parse(this.template.list[index].sequence));
                this.$set(this.config, 'components', JSON.parse(this.template.list[index].components));
                this.templateConfig.visible = false;
            },
            /**
             * 删除
             */
            deleteTemplate(index) {
                this.$confirm('模板删除后无法恢复, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.ajax({
                        url: API.TEMPLATE.DELETE,
                        params: {id: this.template.list[index].id},
                    }).then(response => {
                        this.template.list.splice(index, 1)
                        this.$message({
                            message: response['message'],
                            type: 'success'
                        })
                    })
                }).catch(() => {
                })
            },
            /**
             * 修改模板名称，并确认保存或者更新模板操作
             */
            open(v) {
                this.$prompt('请输入模板名称', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValue: this.templateConfig.dataSource.name
                }).then(({value}) => {
                    this.templateConfig.dataSource.name = value;
                    if ('0' === v) {
                        this.save();
                    } else if ('1' === v) {
                        this.update();
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '取消保存'
                    });
                });
            }
        }
    }
</script>

<style scoped>
    .card {
        position: relative;
        width: 240px;
        height: 160px;
        text-align: center;
        border: 1px solid #2d3e55;
        background-color: rgba(45, 62, 85, 0.3);
        margin: 20px 0 0 20px;
        float: left;
    }

    .delete {
        position: absolute;
        top: 5px;
        right: 5px;
    }
</style>
