<template>
    <el-collapse value="0" accordion>
        <!-- 基础属性 -->
        <el-collapse-item title="基础属性" name="0">
            <el-row>
                <el-col :span="8" class="tag">尺寸</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">位置</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">横坐标</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">纵坐标</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">其他</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description">透明度</div>
                </el-col>
            </el-row>
        </el-collapse-item>

        <el-collapse-item title="配置" name="1">
            <el-row>
                <el-col :span="8" class="tag">背景色</el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.option.backgroundColor"
                                     size="mini"
                                     show-alpha></el-color-picker>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">布局</el-col>
                <el-col :span="7">
                    <el-select v-model="component.config.option.series[0].layout"
                               placeholder="请选择"
                               size="mini">
                        <el-option v-for="item in layout"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="8" :offset="1">
                    <template v-if="component.config.option.series[0].layout=='force'">
                        <el-input-number v-model="component.config.option.series[0].force.repulsion"
                                         size="mini"
                                         controls-position="right"
                                         :min="0">
                        </el-input-number>
                        <div class="description">斥力</div>
                    </template>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">尺寸</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.series[0].height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.series[0].width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">突出焦点关系</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.series[0].focusNodeAdjacency">
                    </el-checkbox>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">对象尺寸</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.transform.variable.symbolSize"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">文本</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.series[0].label.normal.show">
                    </el-checkbox>
                    <div class="description">显示</div>
                </el-col>
                <el-col :span="8" v-if="component.config.option.series[0].label.normal.show">
                    <el-input-number v-model="component.config.option.series[0].label.normal.fontSize"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">字号</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">连线</el-col>
                <el-col :span="8" v-if="component.config.option.series[0].lineStyle.normal.show">
                    <el-input-number v-model="component.config.option.series[0].lineStyle.normal.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">线宽</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">主题配色</el-col>
                <el-col :span="4">
                    <el-button type="text"
                               size="mini"
                               @click="addThemeColor">增加
                    </el-button>
                </el-col>
                <el-col :span="4">
                    <el-button type="text"
                               size="mini"
                               @click="minusThemeColor">减少
                    </el-button>
                </el-col>
            </el-row>
            <el-row>
                <template v-for="(color, i) in component.config.option.color">
                    <el-col :key="i" :span="3">
                        <el-color-picker v-model="component.config.option.color[i]"
                                         size="mini"
                                         show-alpha></el-color-picker>
                    </el-col>
                </template>
            </el-row>
        </el-collapse-item>

        <el-collapse-item title="提示" name="2">
            <el-row>
                <el-col :span="8" class="tag">显示</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.tooltip.show">
                    </el-checkbox>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.tooltip.show">
                <el-col :span="8" class="tag">文本</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.tooltip.textStyle.fontSize"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">字号</div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.option.tooltip.textStyle.color"
                                     size="mini"
                                     show-alpha></el-color-picker>
                    <div class="description">颜色</div>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.tooltip.show">
                <el-col :span="8" class="tag">边框</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.tooltip.borderWidth"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">线条</div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.option.tooltip.borderColor"
                                     size="mini"
                                     show-alpha></el-color-picker>
                    <div class="description">颜色</div>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.tooltip.show">
                <el-col :span="8" class="tag">内边距</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.tooltip.padding[0]"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">上</div>
                    <el-input-number v-model="component.config.option.tooltip.padding[3]"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">左</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.tooltip.padding[2]"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">下</div>
                    <el-input-number v-model="component.config.option.tooltip.padding[1]"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">右</div>
                </el-col>
            </el-row>
        </el-collapse-item>

        <el-collapse-item title="图例" name="3">
            <el-row>
                <el-col :span="8" class="tag">排列方式</el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.option.legend[0].orient"
                               placeholder="请选择"
                               size="mini">
                        <el-option v-for="item in legend.orient"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">水平对齐</el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.option.legend[0].left"
                               placeholder="请选择"
                               size="mini">
                        <el-option v-for="item in legend.alignHorizontal"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">垂直对齐</el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.option.legend[0].top"
                               placeholder="请选择"
                               size="mini">
                        <el-option v-for="item in legend.alignVertical"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">字号</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.option.legend[0].textStyle.fontSize"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                </el-col>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseBar',
        props: ['component', 'state'],
        data() {
            return {
                layout: [
                    {label: '环形', value: 'circular'},
                    {label: '力引导', value: 'force'},
                ],
                legend: {
                    orient: [
                        {label: '水平', value: 'horizontal'},
                        {label: '垂直', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '左侧', value: 'left'},
                        {label: '居中', value: 'center'},
                        {label: '右侧', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '顶部', value: 'top'},
                        {label: '居中', value: 'middle'},
                        {label: '底部', value: 'bottom'},
                    ]
                },
            }
        },
        methods: {
            /**
             * 增加主题颜色
             */
            addThemeColor() {
                this.component.config.option.color.push(ColorUtils.randomHex())
            },
            /**
             * 减少主题颜色
             */
            minusThemeColor() {
                this.component.config.option.color.pop()
            },
        },
        components: {},
    }
</script>
