<template>
    <el-dialog width="90%"
               top="20px"
               title="自定义组件"
               :visible.sync="state.visible"
               :close-on-click-modal="false">
        <el-row>
            <div class="dynamic-component-card create"
                 @click="create()">
                <div class="content">
                    <i class="icon iconfont icon-plus"></i>
                    <div>新增组件</div>
                </div>
            </div>
            <template v-if="state.list && state.list.length">
                <template v-for="(item, i) in state.list">
                    <div class="dynamic-component-card" :key="i">
                        <img :src="item.image">
                        <div class="title">{{item.text}}</div>
                        <div class="edit" @click="edit(item)">
                            <i class="icon iconfont icon-edit"></i>
                        </div>
                        <div class="close" @click="deleteItem(item)">
                            <i class="icon iconfont icon-cross"></i>
                        </div>
                    </div>
                </template>
            </template>
        </el-row>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" @click="state.visible = false">关 闭</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import API from '../../config/api'
    import CONST from "../../config/const";

    export default {
        name: "DynamicComponentList",
        props: ['config', 'state'],
        data() {
            return {}
        },
        mounted() {
        },
        methods: {
            /**
             * 新建
             */
            create() {
                this.$set(this.state.editMode, 'item', {
                    id: null,
                    text: "",
                    name: "DynamicChart",
                    category: "chart",
                    image: "",
                    config: ""
                })
                this.state.editMode.visible = true
            },
            /**
             * 编辑
             */
            edit(item) {
                this.$set(this.state.editMode, 'item', _.clone(item))
                this.state.editMode.visible = true
            },
            /**
             * 刷新列表
             */
            refresh() {
                this.ajax({
                    url: API.DYNAMIC_COMPONENT.LIST,
                    method: CONST.AJAX.POST,
                    data: {},
                }).then(response => {
                    this.state.list = response.data
                })
            },
            /**
             * 删除组件
             */
            deleteItem(item) {
                this.$confirm('组件删除后无法恢复, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.ajax({
                        url: API.DYNAMIC_COMPONENT.DELETE + '?id=' + item.id,
                        method: CONST.AJAX.GET,
                    }).then(response => {
                        this.refresh()
                        if (response.success) {
                            this.$message("删除成功");
                        } else {
                            this.$message.error(response.message)
                        }
                    })
                }).catch(() => {
                })
            }
        }
    }
</script>

