<script>
    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseGauge',
        props: ['component', 'state'],
        data() {
            return {
            }
        },
        created() {
        },
        computed: {},
        methods: {
            /**
             * 增加系列颜色
             */
            addAxisLineColor(seriesIndex) {
                this.component.config.option.series[seriesIndex].axisLine.lineStyle.color
                    .push([0, ColorUtils.randomHex()])
            },
            /**
             * 删除系列颜色
             */
            removeAxisLineColor(seriesIndex, i) {
                this.component.config.option.series[seriesIndex].axisLine.lineStyle.color.splice(i, 1)
            },
        },
        components: {},
    }
</script>
