<template>
    <div class="config" @click="stopPropagation($event)">
        <Screen v-show="!isActiveSingleComp()"
                :config="config" :state="state"/>
        <Base v-show="isActiveSingleComp()"
              :config="config"
              :state="state"/>
    </div>
</template>

<script>
    import Screen from "./config/Screen"
    import Base from "./config/Base"

    export default {
        name: "Config",
        props: ["config", "state"],
        data() {
            return {}
        },
        methods: {
            /**
             * 是否仅选中了一个组件
             */
            isActiveSingleComp() {
                return Object.keys(this.state.select).length === 1
            },
            /**
             * config面板阻止点击冒泡，防止点击后组件被取消选中
             * @param e
             */
            stopPropagation(e) {
                e.stopPropagation()
            }
        },
        components: {
            'Screen': Screen,
            'Base': Base
        }
    }
</script>

