                                </el-input>
                            </el-col>
                        </el-row>
                        <template v-if="series.type == 'bar'">
                            <el-row>
                                <el-col :span="8" class="tag">柱体</el-col>
                                <el-col :span="8">
                                    <el-input-number v-model="component.config.option.series[i].barWidth"
                                                     size="mini"
                                                     controls-position="right"
                                                     :min="0">
                                    </el-input-number>
                                    <div class="description">宽度</div>
                                </el-col>
                                <el-col :span="8">
                                    <el-input-number v-model="component.config.option.series[i].barGap"
                                                     size="mini"
                                                     controls-position="right"
                                                     :min="0">
                                    </el-input-number>
                                    <div class="description">间隔</div>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="8" class="tag">颜色</el-col>
                                <el-col :span="8">
                                    <el-color-picker
                                            v-model="component.config.option.series[i].itemStyle.normal.color.colorStops[0].color"
                                            size="mini"
                                            show-alpha></el-color-picker>
                                    <div class="description">顶部</div>
                                </el-col>
                                <el-col :span="8">
                                    <el-color-picker
                                            v-model="component.config.option.series[i].itemStyle.normal.color.colorStops[1].color"
                                            size="mini"
                                            show-alpha></el-color-picker>
                                    <div class="description">底部</div>
                                </el-col>
                            </el-row>
                        </template>
                        <template v-if="series.type == 'line'">
                            <el-row>
                                <el-col :span="8" class="tag">线条</el-col>
                                <el-col :span="8">
                                    <el-checkbox v-model="component.config.option.series[i].smooth">
                                    </el-checkbox>
                                    <div class="description">平滑</div>
                                </el-col>
                                <el-col :span="8">
                                    <el-color-picker v-model="component.config.option.series[i].itemStyle.normal.color"
                                                     size="mini"
                                                     show-alpha></el-color-picker>
                                    <div class="description">颜色</div>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="8" class="tag">标点</el-col>
                                <el-col :span="16">
                                    <el-checkbox v-model="component.config.option.series[i].showSymbol">
                                    </el-checkbox>
                                </el-col>
                            </el-row>
                            <template v-if="component.config.option.series[i].showSymbol">
                                <el-row>
                                    <el-col :span="8" class="tag">样式</el-col>
                                    <el-col :span="8">
                                        <el-select v-model="component.config.option.series[i].symbol"
                                                   size="mini">
                                            <el-option
                                                    v-for="item in symbolType"
                                                    :key="item.value"
                                                    :label="item.label"
                                                    :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </el-col>
                                    <el-col :span="8" class="tag">标点类型</el-col>
                                </el-row>
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-input-number
                                                v-model="component.config.option.series[i].symbolSize"
                                                size="mini"
                                                controls-position="right"
                                                :min="0"></el-input-number>
                                    </el-col>
                                    <el-col :span="8" class="tag">标点大小</el-col>
                                </el-row>
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16">
                                        <el-switch
                                                v-model="component.config.option.series[i].smooth">
                                        </el-switch>
                                    </el-col>
                                    <el-col :span="8" class="tag">是否平滑</el-col>
                                </el-row>
                                <el-row>
                                    <el-col :span="8" class="tag">样式</el-col>
                                    <el-col :span="8">
                                        <el-input-number
                                                v-model="component.config.option.series[i].itemStyle.normal.label.fontSize"
                                                size="mini"
                                                controls-position="right"
                                                :min="12">
                                        </el-input-number>
                                        <div class="description">字号</div>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-color-picker
                                                v-model="component.config.option.series[i].itemStyle.normal.label.color"
                                                size="mini"
                                                show-alpha></el-color-picker>
                                        <div class="description">颜色</div>
                                    </el-col>
                                </el-row>
                                <el-row>
                                    <el-col :span="8" class="tag">位置</el-col>
                                    <el-col :span="16">
                                        <el-select
                                                v-model="component.config.option.series[i].itemStyle.normal.label.position"
                                                placeholder="请选择"
                                                size="mini">
                                            <el-option v-for="item in labelPosition"
                                                       :key="item.value"
                                                       :label="item.label"
                                                       :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </el-col>
                                </el-row>
                            </template>
                        </template>
                    </el-collapse-item>
                </template>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'
    import _ from 'lodash'

    export default {
        name: 'StackSumBar',
        props: ['component', 'state'],
        data() {
            return {
                // 可见性
                visibility: [
                    {label: '显示', value: 'visible'},
                    {label: '隐藏', value: 'hidden'},
                ],
                legend: {
                    orient: [
                        {label: '水平', value: 'horizontal'},
                        {label: '垂直', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '左侧', value: 'left'},
                        {label: '居中', value: 'center'},
                        {label: '右侧', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '顶部', value: 'top'},
                        {label: '居中', value: 'middle'},
                        {label: '底部', value: 'bottom'},
                    ],
                    legendX: 0
                },
                //标点类型
                symbolType: [{
                    value: 'emptyCircle',
                    label: '空心圆'
                }, {
                    value: 'circle',
                    label: '实心园'
                }, {
                    value: 'rect',
                    label: '矩形'
                }, {
                    value: 'roundRect',
                    label: '正方形'
                }, {
                    value: 'triangle',
                    label: '三角形'
                }, {
                    value: 'diamond',
                    label: '菱形'
                }, {
                    value: 'pin',
                    label: '标注'
                }, {
                    value: 'arrow',
                    label: '箭头'
                }],
                //标签位置
                labelPosition: [
                    {label: "顶部", value: "top"},
                    {label: "左侧", value: "left"},
                    {label: "右侧", value: "right"},
                    {label: "底部", value: "bottom"},
                    {label: "内部", value: "inside"},
                    {label: "左内侧", value: "insideLeft"},
                    {label: "右内侧", value: "insideRight"},
                    {label: "内侧顶部", value: "insideTop"},
                    {label: "内侧底部", value: "insideBottom"},
                    {label: "左内侧顶部", value: "insideTopLeft"},
                    {label: "左内侧底部", value: "insideBottomLeft"},
                    {label: "右内侧顶部", value: "insideTopRight"},
                    {label: "右内侧底部", value: "insideBottomRight"},
                ],
                //位置分类
                positionType: {
                    orient: [
                        {label: '水平', value: 'horizontal'},
                        {label: '垂直', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '左侧', value: 'left'},
                        {label: '居中', value: 'center'},
                        {label: '右侧', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '顶部', value: 'top'},
                        {label: '居中', value: 'middle'},
                        {label: '底部', value: 'bottom'},
                    ]
                },
                //字体加粗选项
                fontWeight: [
                    {label: '正常', value: 'normal'},
                    {label: '加粗', value: 'bold'},
                    {label: '变细', value: 'lighter'},
                ],
                areaColor: []
            }
        },
        created() {
            this.init()
        },
        computed: {},
        methods: {
            init() {
                this.legend.legendX = parseInt(this.component.config.option.legend.x);
            },
            /**
             * 修改图例水平位置
             */
            changeLengXPositon(val) {
                this.component.config.option.legend.x = val + '%';
            },
            /**
             * 增加一个系列
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper使用的是对象，此处遍历时，需要使用 this.$set() 方法赋值，
                // 否则无法调用mapper的setter，从而无法触发dom的重新渲染
                this.$set(this.component.config.transform.mapper, 'series' + index, null)
                let seriesName = '系列' + index
                this.component.config.option.legend.data.push(seriesName)
                let color = ColorUtils.randomHex()
                this.component.config.option.series.push({
                    name: seriesName,
                    type: 'bar',
                    barWidth: 20,
                    barGap: 1,
                    stack: index + '',
                    itemStyle: {
                        normal: {
                            color: {
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                type: "linear",
                                global: false,
                                colorStops: [{
                                    offset: 0,
                                    color: color
                                }, {
                                    offset: 1,
                                    color: color
                                }]
                            },
                        }
                    }
                })
            },
            /**
             * 删除一个系列
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                // 删除mapper的属性
                let mapper = _.cloneDeep(this.component.config.transform.mapper)
                for (let i = index; i < this.component.config.option.series.length; i++) {
                    mapper['series' + i] = mapper['series' + (i + 1)]
                }
                delete mapper['series' + (this.component.config.option.series.length - 1)]
                this.component.config.transform.mapper = mapper
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
                this.areaColor.splice(index, 1)
            },
            /**
             * 修改series名
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            }
        },
        components: {},
    }
</script>
