<template>
    <div v-if="state.contextMenu.show"
         :style="calcStyle"
         class="context-menu">
        <div v-show="showMoveButton()" class="item" @click="moveCard($event, 'top')">
            <i class="icon iconfont icon-top"></i> 置顶
        </div>
        <div v-show="showMoveButton()" class="item" @click="moveCard($event, 'up')">
            <i class="icon iconfont icon-up"></i> 上移一层
        </div>
        <div v-show="showMoveButton()" class="item" @click="moveCard($event, 'down')">
            <i class="icon iconfont icon-down"></i> 下移一层
        </div>
        <div v-show="showMoveButton()" class="item" @click="moveCard($event, 'bottom')">
            <i class="icon iconfont icon-bottom"></i> 置底
        </div>
        <div class="separate-line"></div>
        <div v-show="showAlignButton()" class="item" @click="align($event, 'top')">
            <i class="icon iconfont icon-align-top"></i> 顶部对齐
        </div>
        <div v-show="showAlignButton()" class="item" @click="align($event, 'bottom')">
            <i class="icon iconfont icon-align-bottom"></i> 底部对齐
        </div>
        <div v-show="showAlignButton()" class="item" @click="align($event, 'left')">
            <i class="icon iconfont icon-align-left"></i> 靠左对齐
        </div>
        <div v-show="showAlignButton()" class="item" @click="align($event, 'right')">
            <i class="icon iconfont icon-align-right"></i> 靠右对齐
        </div>
        <div v-show="showAlignButton()" class="item" @click="align($event, 'horizon')">
            <i class="icon iconfont icon-align-horizon"></i> 水平对齐
        </div>
        <div v-show="showAlignButton()" class="item" @click="align($event, 'vertical')">
            <i class="icon iconfont icon-align-vertical"></i> 垂直对齐
        </div>
        <div class="separate-line"></div>
        <div v-show="showExportPic()"
             class="item"
             @click="exportPic">
            <i class="icon iconfont icon-copy"></i> 导出图片
        </div>
        <div class="item" @click="duplicateCmp($event)">
            <i class="icon iconfont icon-copy"></i> 复制
        </div>
        <div class="item" @click="copyToClipboard($event)">
            <i class="icon iconfont icon-transplant"></i> 移植
        </div>
        <div class="item" @click="deleteCmp">
            <i class="icon iconfont icon-delete"></i> 删除
        </div>
    </div>
</template>

<script>
    import echarts from 'echarts'
    import IdGenerator from "../../utils/IdGenerator";
    import _ from 'lodash'

    export default {
        name: 'ContextMenu',
        props: ['config', 'state'],
        data() {
            return {}
        },
        computed: {
            calcStyle() {
                return {
                    top: this.state.contextMenu.style.top + 'px',
                    left: this.state.contextMenu.style.left + 'px',
                }
            },
        },
        methods: {
            /**
             * 是否移动按钮
             */
            showMoveButton() {
                if (Object.keys(this.state.select).length > 1) {
                    return false
                }
                return true
            },
            /**
             * 是否显示对齐功能
             */
            showAlignButton() {
                if (Object.keys(this.state.select).length > 1) {
                    return true
                }
                return false
            },
            /**
             * 是否显示导出图片按钮
             */
            showExportPic() {
                if (Object.keys(this.state.select).length > 1) {
                    return false
                }
                let id = this.state.contextMenu.ids[0]
                if (!id || !this.config.components[id] || !this.config.components[id].category) {
                    return false
                }
                return this.config.components[id].category &&
                    (this.config.components[id].category == 'map' || this.config.components[id].category == 'chart');
            },
            /**
             * 导出图片
             */
            exportPic() {
                let id = this.state.contextMenu.ids[0]
                let chart = echarts.getInstanceByDom(document.getElementById(id))
                let pic = chart.getDataURL()
                let aTag = document.createElement('a')
                aTag.download = "pic-" + new Date().getTime() + ".png"
                aTag.href = pic
                aTag.click()
            },
            /**
             * 复制一个组件的副本
             */
            duplicateCmp(e) {
                e.stopPropagation()
                let arr = Object.keys(this.state.select)
                let select = {}
                for (let i = arr.length - 1; i >= 0; i--) {
                    let id = arr[i]
                    let component = _.cloneDeep(this.config.components[id])
                    let cmpId = IdGenerator.randomCmpId()
                    component.config.id = cmpId
                    component.config.style.top += 150
                    component.config.style.left += 150
                    this.config.sequence.unshift(cmpId)
                    this.$set(this.config.components, cmpId, component)
                    select[cmpId] = component
                    console.log("id:" + id + " cmpid:" + cmpId)
                }
                this.$set(this.state, 'select', select)
                this.state.contextMenu.ids = null
            },
            /**
             * 复制到剪贴板
             */
            copyToClipboard(e) {
                e.stopPropagation()
                let arr = Object.keys(this.state.select)
                let temp = {}
                for (let i = 0; i < arr.length; i++) {
                    let id = arr[i]
                    temp[id] = _.cloneDeep(this.config.components[id])
                }
                let config = JSON.stringify(temp)
                this.$copyText(config)
                    .then(res => {
                            this.$message({
                                message: '移植成功，可在其他可视化编辑界面导入。',
                                type: 'success'
                            });
                        },
                        err => {
                            this.$message({
                                message: '移植失败！',
                                type: 'error'
                            });
                        }
                    )
            },
            /**
             * 删除组件
             */
            deleteCmp() {
                let select = this.state.select
                let sequence = this.config.sequence
                for (let i = sequence.length - 1; i >= 0; i--) {
                    let id = sequence[i]
                    if (select.hasOwnProperty(id)) {
                        sequence.splice(i, 1)
                    }
                }
                for (let i = 0; i < this.state.contextMenu.ids.length; i++) {
                    let id = this.state.contextMenu.ids[i]
                    this.$delete(this.config.components, id)
                }
                this.$set(this.state, 'select', {})
                this.state.contextMenu.ids = null
            },
            /**
             * 移动选中的组件
             * @param index
             */
            moveCard(e, action) {
                e.stopPropagation()
                let idArr = Object.keys(this.state.select)
                if (idArr.length > 1) {
                    return
                }
                let id = idArr[0]
                let sequence = this.config.sequence
                let seqIndex
                let arr
                for (let i = 0; i < sequence.length; i++) {
                    if (sequence[i] === id) {
                        seqIndex = i
                        break
                    }
                }
                switch (action) {
                    case 'top':
                        if (seqIndex === 0) {
                            return
                        }
                        this.config.sequence.splice(seqIndex, 1)
                        this.config.sequence.unshift(id)
                        break
                    case 'up':
                        if (seqIndex === 0) {
                            return
                        }
                        arr = this.config.sequence.slice(seqIndex - 1, seqIndex + 1).reverse()
                        this.config.sequence.splice(seqIndex - 1, 2, arr[0], arr[1])
                        break
                    case 'down':
                        if (seqIndex === sequence.length) {
                            return
                        }
                        arr = this.config.sequence.slice(seqIndex, seqIndex + 2).reverse()
                        this.config.sequence.splice(seqIndex, 2, arr[0], arr[1])
                        break
                    case 'bottom':
                        if (seqIndex === sequence.length) {
                            return
                        }
                        this.config.sequence.splice(seqIndex, 1)
                        this.config.sequence.push(id)
                        break
                    default:
                        break
                }
            },
            /**
             * 移动选中的组件
             * @param index
             */
            align(e, action) {
                e.stopPropagation()
                let current = this.state.contextMenu.refId
                let idArr = Object.keys(this.state.select)
                let baseStyle = this.config.components[current].config.style
                for (let i = 0; i < idArr.length; i++) {
                    this.moveComponent(action, baseStyle, idArr[i])
                }
            },
            moveComponent(action, baseStyle, cmpId) {
                let style = this.config.components[cmpId].config.style
                switch (action) {
                    case 'top':
                        this.$set(style, 'top', baseStyle.top)
                        break
                    case 'bottom':
                        this.$set(style, 'top', baseStyle.top + baseStyle.height - style.height)
                        break
                    case 'left':
                        this.$set(style, 'left', baseStyle.left)
                        break
                    case 'right':
                        this.$set(style, 'left', baseStyle.left + baseStyle.width - style.width)
                        break
                    case 'horizon':
                        this.$set(style, 'top', baseStyle.top + baseStyle.height / 2 - style.height / 2)
                        break
                    case 'vertical':
                        this.$set(style, 'left', baseStyle.left + baseStyle.width / 2 - style.width / 2)
                        break
                    default:
                        break

                }
            }
        }
    }
</script>

