            </el-row>
            <el-row>
                <el-col :span="8" class="tag">字体</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.header.fontSize"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.style.header.color"
                                     size="mini" show-alpha></el-color-picker>
                    <div class="description">颜色</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">边框</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.header.borderWidth"
                                     size="mini"
