                    </el-input-number>
                    <div class="description">间隔</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">样式</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right" :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
                <el-col :span="8">
