                                     size="mini" show-alpha></el-color-picker>
                    <div class="description">背景色</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">字体</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.body.fontSize"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">字号</div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.style.body.color"
