                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                </el-col>
                <div class="description">上下</div>
            </el-row>
        </el-collapse-item>
        <!-- 行 -->
        <el-collapse-item title="行" name="4">
            <el-row>
                <el-col :span="8" class="tag">样式</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.body.height"
