            </el-row>
            <!--            <el-row>
                            <el-col :span="8" class="tag">斑马纹</el-col>
                            <el-col :span="4">
                                <el-checkbox v-model="component.config.option.stripe"></el-checkbox>
                            </el-col>
                            <el-col :span="6" v-if="component.config.option.stripe">
                                <el-color-picker v-model="component.config.style.oddRowBgColor"
