                                <div class="description">奇数行</div>
                            </el-col>
                            <el-col :span="6" v-if="component.config.option.stripe">
                                <el-color-picker v-model="component.config.style.evenRowBgColor"
                                                 size="mini" show-alpha></el-color-picker>
                                <div class="description">偶数行</div>
                            </el-col>
                        </el-row>-->
        </el-collapse-item>
        <!-- 列 -->
        <el-collapse-item title="列" name="5">
            <el-tabs v-model="currentColumn" editable @tab-remove="removeColumn" @tab-add="addColumn">
                <el-tab-pane v-for="(item, index) in component.config.option.columns"
                             :key="index"
                             :label="item.text + '列'"
                             :name="index + ''">
                    <el-row>
                        <el-col :span="8" class="tag">文本</el-col>
                        <el-col :span="16">
                            <el-input v-model="item.text" size="mini"></el-input>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">属性</el-col>
                        <el-col :span="16">
                            <el-input v-model="item.prop" size="mini"></el-input>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">宽度</el-col>
                        <el-col :span="16">
