<script>
    export default {
        name: 'ScrollTable',
        props: ['component', 'state'],
        data() {
            return {
                //可见
                visibility: [
                    {label: '显示', value: 'visible'},
                    {label: '隐藏', value: 'hidden'},
                ],
                tableSize: [
                    {label: '大', value: 'medium'},
                    {label: '中', value: 'small'},
                    {label: '小', value: 'mini'},
                ],
                columnType: [
                    {label: '文本', value: 'text'},
                    {label: '序号', value: 'index'},
                ],
                // 当前选中列
                currentColumn: '0'
            }
        },
        created() {
        },
        computed: {},
        methods: {
            /**
             * 添加列
             */
            addColumn(e) {
                this.component.config.option.columns.push({
                    type: "text",
                    text: "",
                    prop: "",
                    width: 100,
                    fixed: false,
                    sortable: false
                })
                this.currentColumn = this.component.config.option.columns.length - 1 + '';
            },
            /**
             * 删除列
             */
            removeColumn(name) {
                this.component.config.option.columns.splice(name, 1)
                if (this.currentColumn !== name) {
                    return;
                }
                if (this.currentColumn * 1 > 0) {
                    this.currentColumn = this.currentColumn - 1 + ''
                }
            }
        },
        components: {}
    }
</script>
