<template>
    <Modal :config="config" :base="base">
        <iframe slot="body"
                :style="calcStyle()"
                :src="calcUrl()"
                frameborder="0"></iframe>
    </Modal>
</template>

<script>
    import Modal from '@/components/screen/layer/base/Modal'
    import _ from "lodash"
    import CONST from "../../../../config/const";

    export default {
        name: "BaseModal",
        props: ['config', 'base'],
        created() {
            this.initStyle()
            this.initTrigger()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        methods: {
            /**
             * 计算样式
             */
            calcStyle() {
                return {
                    width: "100%",
                    height: this.config.style.iframeHeight + 'px',
                    opacity: this.config.style.opacity,
                }
            },
            /**
             * 计算URL
             */
            calcUrl() {
                let url = this.config.option.url || ""
                return url;
            },
            /**
             * 初始化样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    iframeHeight: 120,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 初始化样式
             */
            initTrigger() {
                this.config.trigger = this.config.trigger || [
                    {
                        show: true,
                        name: '可见性',
                        binder: null,
                        prop: "config.style.visibility",
                        defaultValue: true,
                        isScript: true,
                        mapper: [
                            {key: true, value: 'visible', comment: '返回 true 时显示'},
                            {key: false, value: 'hidden', comment: '返回 false 时隐藏'},
                        ]
                    },
                    {
                        show: false,
                        name: 'URL',
                        binder: null,
                        prop: "config.option.url",
                        defaultValue: "",
                        isScript: false,
                        mapper: null
                    }
                ]
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = null
            },
            /**
             * 声明默认option
             */
            initOption() {
                this.config.option = this.config.option || {
                    url: {
                        value: "",
                        binder: "",
                        store: {}
                    },
                    param: {
                        value: "",
                        binder: [],
                        store: {}
                    },
                    header: {
                        show: false,
                        title: '',
                    },
                    footer: {
                        show: false,
                    }
                }
            },
            /**
             * 初始化事件
             */
            initEvent() {
                this.config.event = null
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = {type: null}
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = null
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
            },
        },
        components: {
            'Modal': Modal
        }
    }
</script>
