<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'HorizontalStackBar',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'rgba(0,0,0,0)',
                    legend: {
                        show: true,
                        data: ['数据0', '数据1'],
                        orient: 'horizontal',
                        top: 'bottom',
                        left: '0%',
                        itemWidth: 18,
                        itemHeight: 8,
                        textStyle: {
                            fontSize: 12,
                            color: ['#D4DFF5']
                        },
                    },
                    grid: {
                        top: 50,
                        left: 50,
                        right: 50,
                        bottom: 50
                    },
                    tooltip: {
                        show: true,
                        backgroundColor: '#fff',
                        borderColor: '#ddd',
                        borderWidth: 1,
                        padding: [5, 10, 5, 10],
                        textStyle: {
                            color: '#3c3c3c',
                            fontSize: 16
                        },
                        extraCssText: 'box-shadow: 0 0 5px rgba(0, 0, 0, 0.1)'
                    },
                    xAxis: {
                        min: 0,
                        max: undefined,
                        axisLine: {
                            show: false,
                            lineStyle: {
                                color: "#737985",
                                width: 1,
                            }
                        },
                        axisTick: {
                            show: false,
                        },
                        axisLabel: {
                            show: false,
                            textStyle: {
                                fontSize: 14,
                                color: "#dddddd",
                            }
                        },
                        splitLine: {
                            show: false,
                            lineStyle: {
                                width: 1,
                                color: '#737985'
                            }
                        },
                    },
                    yAxis: [{
                        data: null,
                        axisLabel: {
                            show: true,
                            interval:0,
                            rotate:0,
                            margin: 10,
                            textStyle: {
                                fontSize: 14,
                                color: '#dddddd'
                            }
                        },
                        axisTick: {
                            show: false,
                        },
                        axisLine: {
                            show: false,
                            lineStyle: {
                                color: "#737985",
                                width: 1,
                            },
                        },
                        splitLine: {
                            show: false,
                            lineStyle: {
                                color: '#737985'
                            }
                        },
                    }],
                    series: [{
                        type: 'bar',
                        name: '数据0',
                        stack: 'oneLine',
                        label: {
                            normal: {
                                show: true,
                                position: 'inside',
                                textStyle: {
                                    color: '#fff',
                                    fontSize: 16
                                }
                            }
                        },
                        legendHoverLink: false,
                        barWidth: 20,
                        barGap: 1,
                        itemStyle: {
                            normal: {
                                color: "#00BBFF"
                            }
                        },
                        data: null
                    }, {
                        type: 'bar',
                        name: '数据1',
                        stack: 'oneLine',
                        barGap: 1,
                        label: {
                            normal: {
                                show: true,
                                position: 'inside',
                                textStyle: {
                                    color: '#fff',
                                    fontSize: 16
                                }
                            }
                        },
                        legendHoverLink: false,
                        barWidth: 20,
                        itemStyle: {
                            normal: {
                                color: "#3CE7C9"
                            }
                        },
                        data: null
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '一月', series0: 60, series1: 60},
                    {name: '二月', series0: 55, series1: 60},
                    {name: '三月', series0: 35, series1: 60},
                    {name: '四月', series0: 68, series1: 60},
                    {name: '五月', series0: 84, series1: 60},
                    {name: '六月', series0: 26, series1: 60},
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'value', comment: '系列值0', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        series0: null,
                        series1: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                Object.keys(mapper).forEach((ele, index) => {
                    let propName = mapper[ele] ? mapper[ele] : ele
                    let tempData = []
                    for (let i = 0; i < data.length; i++) {
                        if (ele == 'name') {
                            tempData.push(data[i][propName])
                        } else {
                            let name = mapper.name ? mapper.name : 'name'
                            tempData.push({
                                name: data[i][name],
                                value: data[i][propName],
                            })
                        }
                    }
                    if (ele == 'name') {
                        this.config.option.yAxis[0].data = tempData
                    } else {
                        this.config.option.series[index - 1].data = tempData
                    }
                })
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
