import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'
import Viewport from '@/views/Viewport'
import Login from '@/views/Login'
import Overview from '@/views/Overview'
import Design from '@/views/Design'
import Presentation from '@/views/Presentation'
import Integration from '@/views/Integration'
import IntegrationPresentation from '@/views/IntegrationPresentation'

Vue.use(Router)

const router = new Router({
    /*mode: 'history',*/
    routes: [{
        path: '/',
        name: 'Viewport',
        component: Viewport
    }, {
        path: '/login',
        name: 'Login',
        component: Login
    }, {
        path: '/overview',
        name: 'Overview',
        component: Overview
    }, {
        path: '/design',
        name: 'Design',
        component: Design
    }, {
        path: '/presentation',
        name: 'Presentation',
        component: Presentation
    }, {
        path: '/integration',
        name: 'Integration',
        component: Integration
    }, {
        path: '/integration_presentation',
        name: 'IntegrationPresentation',
        component: IntegrationPresentation
    }]
})

/**
 * todo 部署前删除
 * 全局路由守护，当token不存在时，跳转到登陆页
 */
router.beforeEach((to, from, next) => {
	if (to.path !== "/login" && !store.state.token) {
		next("/login")
	} else {
		next()
	}
})

export default router
