<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import _ from 'lodash'

    export default {
        name: 'BasePie',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    tooltip: {
                        trigger: 'item',
                        formatter: "{b} : {c} "
                    },
                    legend: {
                        orient: 'horizontal',
                        icon: 'circle',
                        top: 'bottom',
                        left: 'center',
                        textStyle: {
                            color: '#fff',
                            fontSize: 12
                        },
                    },
                    series: [{
                        type: 'pie',
                        roseType: false,
                        radius: ['40%', '50%'],
                        center: ['50%', '50%'],
                        color: ['#0E7CE2', '#FF8352', '#E271DE', '#F8456B', '#00FFFF', '#4AEAB0'],
                        data: null,
                        labelLine: {
                            normal: {
                                show: true,
                                length: 20,
                                length2: 20,
                                lineStyle: {
                                    color: '#12EABE',
                                    width: 2
                                }
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                formatter: '{c|{c}}\n{hr|}\n{d|{d}%}',
                                rich: {
                                    b: {
                                        fontSize: 14,
                                        color: '#12EABE',
                                        align: 'left',
                                        padding: 4
                                    },
                                    hr: {
                                        borderColor: '#12EABE',
                                        width: '100%',
                                        borderWidth: 2,
                                        height: 0
                                    },
                                    d: {
                                        fontSize: 14,
                                        color: '#fff',
                                        align: 'left',
                                        padding: 4
                                    },
                                    c: {
                                        fontSize: 14,
                                        color: '#fff',
                                        align: 'left',
                                        padding: 4
                                    }
                                }
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: '一月', value: 80},
                    {name: '二月', value: 87},
                    {name: '三月', value: 51},
                    {name: '四月', value: 81},
                    {name: '五月', value: 23},
                    {name: '六月', value: 45},
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'value', comment: '数据值', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propX = mapper.name || 'name'
                let propY = mapper.value || 'value'
                let series = []
                for (let i = 0; i < data.length; i++) {
                    series.push({
                        name: data[i][propX],
                        value: data[i][propY],
                    })
                }
                this.config.option.series[0].data = series
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
