<template>
    <el-col :id="column.id"
            :span="column.style.span"
            :xs="column.style.xs"
            :sm="column.style.sm"
            :md="column.style.md"
            :lg="column.style.lg"
            :xl="column.style.xl"
            :class="isDropping() ? 'is-dropping': ''">
        <!-- 当此栅格中的行数大于1时，渲染子组件 -->
        <template v-if="column.rows && rowCmp && column.rows.length">
            <template v-for="(row, i) in column.rows">
                <component :is="rowCmp.default"
                           :config="config"
                           :row="row"
                           :state="state"
                           :loader="loader"
                           :key="i"></component>
            </template>
        </template>
        <!-- 在此栅格列中，按照左侧组件列表的顺序从上到下进行渲染 -->
        <template v-else>
            <template v-if="config.sequence && config.sequence.length > 0">
                <template v-for="(id, index) in config.sequence">
                    <template v-if="column.components[id] && loader[id]">
                        <div :id="getCmpBoxId(id)"
                             :key="index"
                             :class="calcBoxActive(id) ? 'active' : ''"
                             class="column-box"
                             @click="activeBox($event, id)"
                             @mousedown="showContextMenu($event, id)">
                            <component :is="loader[id].default"
                                       :base="config.base"
                                       :config="config.components[id].config"></component>
                            <div v-show="toggleShiftButton(id)"
                                 class="shift-cmp icon iconfont icon-disconnect"
                                 @mousedown.left=shiftComp($event,id)></div>
                        </div>
                    </template>
                </template>
            </template>
        </template>
    </el-col>
</template>

<script>
    export default {
        name: "Column",
        props: ['config', 'column', 'state', 'loader'],
        data() {
            return {
                rowCmp: null
            }
        },
        mounted() {
            this.dynamicLoadRow()
        },
        methods: {
            /**
             * 计算box激活样式
             */
            calcBoxActive(id) {
                if (!this.state) {
                    return false
                }
                return this.state.select.hasOwnProperty(id)
            },
            /**
             * 由于Column和Row是交叉引用，此处需要通过动态引入Row
             */
            dynamicLoadRow() {
                this.rowCmp = require("./Row.vue")
            },
            /**
             * 计算组件容器id
             */
            getCmpBoxId(id) {
                return this.column.id + '-' + id
            },
            /**
             * 点击选中当前组件
             */
            activeBox(e, id) {
                if (!this.state) {
                    return
                }
                let comps = {}
                comps[id] = this.config.components[id]
                this.$set(this.state, "select", comps)
                e.stopPropagation()
            },
            /**
             * 显示右键菜单
             */
            showContextMenu(e, id) {
                if (!this.state) {
                    return
                }
                if (e.button === 2) {
                    let comps = {}
                    comps[id] = this.config.components[id]
                    this.$set(this.state, "select", comps)
                    this.state.contextMenu.ids = Object.keys(comps)
                    this.state.contextMenu.style.left = e.pageX
                    this.state.contextMenu.style.top = e.pageY
                    window.addEventListener('click', this.hideContextMenu)
                }
            },
            /**
             * 隐藏右键菜单
             */
            hideContextMenu() {
                if (!this.state) {
                    return
                }
                this.state.contextMenu.index = null
                window.removeEventListener('click', this.hideContextMenu)
            },
            /**
             * 计算当前的组件移入的激活状态
             */
            isDropping() {
                if (!this.state || !this.state.grid.dragMode.column || !this.column) {
                    return false
                }
                return this.state.grid.dragMode.column.id === this.column.id
            },
            /**
             * 显示/隐藏弹出按钮
             */
            toggleShiftButton(id) {
                if (!this.state) {
                    return false
                }
                return this.state.select.hasOwnProperty(id)
            },
            /**
             * 从栅格中弹出组件，使其恢复绝对定位的模式
             * @param id
             */
            shiftComp(e, id) {
                e.stopPropagation()
                if (!this.state) {
                    return
                }
                // 首先要移除组件所在的dom，否则容易出现此处还未销毁，就在Paint上渲染，
                // 导致echarts.init找到的dom还是此处的dom，从而无法正确加载的问题
                let boxId = this.getCmpBoxId(id)
                let dom = document.getElementById(boxId)
                let parent = dom.parentElement
                parent.removeChild(dom)
                // 组件和column互相取消关联
                this.config.components[id].config.grid.id = null
                delete this.column.components[id]
            },
            /**
             * 判断是否刚进行过拖拽，如果是的话则不触发点击事件
             */
            isDragging() {
                let flag = false
                let start = this.state.drag.start
                let end = new Date().getTime()
                if (start && end && end - start > 150) {
                    flag = true
                }
                return flag
            },
        },
        watch: {
            /**
             * 根据鼠标位置激活列
             */
            "state.grid.dragMode.mouse": {
                deep: true,
                handler: function () {
                    if (!this.state) {
                        return
                    }
                    if (!this.state.drag.isDragging || !this.isDragging()) {
                        return
                    }
                    // 当鼠标移入时，如果此列的 rows.length <= 1，则可处于激活状态，即当此栅格为父容器时，无法被激活
                    // 激活时将此栅格的配置存入全局state，使组件drop时，可以加入此栅格
                    if (this.column.rows.length > 0) {
                        return;
                    }
                    let dom = document.getElementById(this.column.id)
                    let rect = dom.getBoundingClientRect()
                    // 鼠标相对于画布的定位，需要减去画布相对背景的边距30，其中x减去左侧菜单和列表的宽度225
                    let x = rect.x - 225 - 30
                    let y = rect.y - 30
                    let width = rect.width
                    let height = rect.height
                    let mouseX = this.state.grid.dragMode.mouse.x
                    let mouseY = this.state.grid.dragMode.mouse.y
                    if (mouseX >= x && mouseX <= x + width && mouseY >= y && mouseY <= y + height) {
                        this.state.grid.dragMode.column = this.column
                    }
                    let gridDom = document.getElementById(this.state.grid.id)
                    let gridRect = gridDom.getBoundingClientRect()
                    x = gridRect.x - 225 - 30
                    y = gridRect.y - 30
                    width = gridRect.width
                    height = gridRect.height
                    if (!(mouseX >= x && mouseX <= x + width && mouseY >= y && mouseY <= y + height)) {
                        this.state.grid.dragMode.column = null
                    }
                }
            }
        }
    }
</script>

