<template>
    <el-collapse value="0" accordion>
        <!-- 基础属性 -->
        <el-collapse-item title="基础属性" name="0">
            <el-row>
                <el-col :span="8" class="tag">尺寸</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">位置</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">横坐标</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">纵坐标</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">其他</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description">透明度</div>
                </el-col>
            </el-row>
        </el-collapse-item>

        <!-- 配置 -->
        <el-collapse-item title="配置" name="1">
            <el-collapse value="0">
                <el-collapse-item title="功能" name="0">
                    <el-row>
                        <el-col :span="8" class="tag">坐标系</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.radar.startAngle"
                                             size="mini"
                                             controls-position="right" :min="0" :max="360" :step="1">
                            </el-input-number>
                            <div class="description">起始角</div>
                        </el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.radar.shape"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in radarShape"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                            <div class="description">形状</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">坐标轴</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.radar.radius"
                                             size="mini"
                                             controls-position="right" :min="0">
                            </el-input-number>
                            <div class="description">半径</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.radar.splitNumber"
                                             size="mini"
                                             controls-position="right" :min="0">
                            </el-input-number>
                            <div class="description">段数</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">坐标文本</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.radar.name.show">
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row v-if="component.config.option.radar.name.show">
                        <el-col :span="8" class="tag">字体</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.radar.name.fontSize"
                                             size="mini"
                                             controls-position="right" :min="12">
                            </el-input-number>
                            <div class="description">字号</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.radar.name.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">分隔区</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.radar.splitArea.show">
                            </el-checkbox>
                            <div class="description">显示</div>
                        </el-col>
                        <el-col :span="8" v-if="component.config.option.radar.splitArea.show">
                            <el-color-picker v-model="component.config.option.radar.splitArea.areaStyle.color[0]"
                                             size="mini"
                                             show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">分隔线</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.radar.splitLine.show">
                            </el-checkbox>
                            <div class="description">显示</div>
                        </el-col>
                        <el-col :span="8" v-if="component.config.option.radar.splitLine.show">
                            <el-color-picker v-model="component.config.option.radar.splitLine.lineStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">坐标轴</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.radar.axisLine.show">
                            </el-checkbox>
                            <div class="description">显示</div>
                        </el-col>
                        <el-col :span="8" v-if="component.config.option.radar.axisLine.show">
                            <el-color-picker v-model="component.config.option.radar.axisLine.lineStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="图例" name="1">
                    <el-row>
                        <el-col :span="8" class="tag">显示</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.legend.show">
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">排列方式</el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.legend.orient"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in legend.orient"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">水平对齐</el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.legend.left"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in legend.alignHorizontal"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">垂直对齐</el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.legend.top"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in legend.alignVertical"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">文本</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.legend.textStyle.fontSize"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">字号</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.legend.textStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">颜色</div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="提示" name="2">
                    <el-row>
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.tooltip.backgroundColor"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">背景</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.tooltip.textStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">文本</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">内边距</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.tooltip.padding[0]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">上</div>
                            <el-input-number v-model="component.config.option.tooltip.padding[3]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">左</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.tooltip.padding[2]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">下</div>
                            <el-input-number v-model="component.config.option.tooltip.padding[1]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">右</div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>

        <!-- 系列 -->
        <el-collapse-item title="数据系列" name="2">
            <el-row>
                <el-col :span="4">
                    <el-button type="text"
                               size="mini"
                               @click="addSeries">增加
                    </el-button>
                </el-col>
            </el-row>
            <el-collapse value="0">
                <template v-for="(series, i) in component.config.option.series">
                    <el-collapse-item :key="i" :title="series.name" :name="i + ''">
                        <el-row v-if="i > 0">
                            <el-col :span="4">
                                <el-button type="text"
                                           size="mini"
                                           @click="removeSeries(i)">删除
                                </el-button>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">名称</el-col>
                            <el-col :span="11">
                                <el-input v-model="component.config.option.series[i].name"
                                          size="mini"
                                          controls-position="right"
                                          @change="changeSeriesName">
                                </el-input>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">连线</el-col>
                            <el-col :span="8">
                                <el-select v-model="component.config.option.series[i].lineStyle.normal.type"
                                           placeholder="请选择"
                                           size="mini">
                                    <el-option v-for="item in lineType"
                                               :key="item.value"
                                               :label="item.label"
                                               :value="item.value">
                                    </el-option>
                                </el-select>
                                <div class="description">线型</div>
                            </el-col>
                            <el-col :span="8">
                                <el-color-picker v-model="component.config.option.series[i].lineStyle.normal.color"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description">线色</div>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" :offset="8">
                                <el-input-number v-model="component.config.option.series[i].lineStyle.normal.width"
                                                 size="mini"
                                                 controls-position="right" :min="0">
                                </el-input-number>
                                <div class="description">线宽</div>
                            </el-col>
                            <el-col :span="8">
                                <el-color-picker v-model="component.config.option.series[i].areaStyle.normal.color"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description">背景</div>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">节点</el-col>
                            <el-col :span="6">
                                <el-input-number v-model="component.config.option.series[i].itemStyle.borderWidth"
                                                 size="mini"
                                                 controls-position="right" :min="0">
                                </el-input-number>
                                <div class="description">边宽</div>
                            </el-col>
                            <el-col :span="4" :offset="2">
                                <el-color-picker v-model="component.config.option.series[i].itemStyle.borderColor"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description">边框</div>
                            </el-col>
                            <el-col :span="4">
                                <el-color-picker v-model="component.config.option.series[i].itemStyle.color"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description">背景</div>
                            </el-col>
                        </el-row>
                    </el-collapse-item>
                </template>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'

    export default {
        name: 'BaseScatter',
        props: ['component', 'state'],
        data() {
            return {
                legend: {
                    orient: [
                        {label: '水平', value: 'horizontal'},
                        {label: '垂直', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '左侧', value: 'left'},
                        {label: '居中', value: 'center'},
                        {label: '右侧', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '顶部', value: 'top'},
                        {label: '居中', value: 'middle'},
                        {label: '底部', value: 'bottom'},
                    ]
                },
                radarShape: [
                    {label: '多边形', value: 'polygon'},
                    {label: '圆形', value: 'circle'},
                ],
                lineType: [
                    {label: '实线', value: 'solid'},
                    {label: '虚线', value: 'dashed'},
                    {label: '点', value: 'dotted'},
                ],
            }
        },
        methods: {
            /**
             * 增加一个系列
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper使用的是对象，此处遍历时，需要使用 this.$set() 方法赋值，
                // 否则无法调用mapper的setter，从而无法触发dom的重新渲染
                let seriesName = '系列' + index
                this.component.config.option.legend.data.push(seriesName)
                let color = ColorUtils.randomHex()
                this.component.config.option.series.push({
                    name: seriesName,
                    type: "radar",
                    symbol: "circle",
                    symbolSize: 10,
                    areaStyle: {
                        normal: {
                            color: color
                        }
                    },
                    itemStyle: {
                        color: color,
                        borderColor: color,
                        borderWidth: 10,
                    },
                    lineStyle: {
                        normal: {
                            type: "dashed",

                            color: color,
                            width: 2
                        }
                    },
                    data: null
                })
            },
            /**
             * 删除一个系列
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
            },
            /**
             * 修改series名
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            }
        },
        components: {},
    }
</script>
