<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import 'echarts-wordcloud'
    import _ from 'lodash'

    export default {
        name: 'WordCloud',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    series: [{
                        type: 'wordCloud',
                        data: null,
                        shape: 'circle',
                        left: 'center',
                        top: 'center',
                        width: 380,
                        height: 280,
                        right: null,
                        bottom: null,
                        sizeRange: [10, 30],
                        rotationRange: [-45, 90],
                        drawOutOfBound: false,
                        textStyle: {
                            normal: {
                                fontFamily: 'sans-serif',
                                fontWeight: 'bold',
                                color: function () {
                                    return 'rgb(' + [
                                        Math.floor(Math.random() * 255),
                                        Math.floor(Math.random() * 255),
                                        Math.floor(Math.random() * 255)
                                    ].join(',') + ')';
                                }
                            },
                            emphasis: {
                                shadowBlur: 10,
                                shadowColor: '#333'
                            }
                        },
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: "生活资源", value: 999},
                    {name: "供热管理", value: 888},
                    {name: "供气质量", value: 777},
                    {name: "生活用水管理", value: 688},
                    {name: "一次供水问题", value: 588},
                    {name: "交通运输", value: 516},
                    {name: "城市交通", value: 515},
                    {name: "环境保护", value: 483},
                    {name: "房地产管理", value: 462},
                    {name: "城乡建设", value: 449},
                    {name: "社会保障与福利", value: 429},
                    {name: "社会保障", value: 407},
                    {name: "文体与教育管理", value: 406},
                    {name: "公共安全", value: 406},
                    {name: "公交运输管理", value: 386},
                    {name: "出租车运营管理", value: 385},
                    {name: "供热管理", value: 375},
                    {name: "市容环卫", value: 355},
                    {name: "自然资源管理", value: 355},
                    {name: "粉尘污染", value: 335},
                    {name: "噪声污染", value: 324},
                    {name: "土地资源管理", value: 304},
                    {name: "物业服务与管理", value: 304},
                    {name: "医疗卫生", value: 284},
                    {name: "粉煤灰污染", value: 284},
                    {name: "占道", value: 284},
                    {name: "供热发展", value: 254},
                    {name: "农村土地规划管理", value: 254},
                    {name: "生活噪音", value: 253},
                    {name: "供热单位影响", value: 253},
                    {name: "城市供电", value: 223},
                    {name: "房屋质量与安全", value: 223},
                    {name: "大气污染", value: 223},
                    {name: "房屋安全", value: 223},
                    {name: "文化活动", value: 223},
                    {name: "拆迁管理", value: 223},
                    {name: "公共设施", value: 223},
                    {name: "供气质量", value: 223},
                    {name: "供电管理", value: 223},
                    {name: "燃气管理", value: 152},
                    {name: "教育管理", value: 152},
                    {name: "医疗纠纷", value: 152},
                    {name: "执法监督", value: 152},
                    {name: "设备安全", value: 152},
                    {name: "政务建设", value: 152},
                    {name: "县区、开发区", value: 152},
                    {name: "宏观经济", value: 152},
                    {name: "教育管理", value: 112},
                    {name: "社会保障", value: 112},
                    {name: "生活用水管理", value: 112},
                    {name: "物业服务与管理", value: 112},
                    {name: "分类列表", value: 112},
                    {name: "农业生产", value: 112},
                    {name: "二次供水问题", value: 112},
                    {name: "城市公共设施", value: 92}
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'name', comment: '数据名', prop: 'name', state: null},
                            {name: 'value', comment: '数据值', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        name: null,
                        value: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let mapper = this.config.transform.mapper
                let propName = mapper.name || 'name'
                let propValue = mapper.value || 'value'
                let data = []
                for (let i = 0; i < this.config.data.length; i++) {
                    data.push({
                        name: this.config.data[i][propName],
                        value: this.config.data[i][propValue]
                    })
                }
                this.config.option.series[0].data = data
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
