<template>
    <el-collapse value="0" accordion>
        <!-- 基础属性 -->
        <el-collapse-item title="基础属性" name="0">
            <el-row>
                <el-col :span="8" class="tag">按钮尺寸</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">位置</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">横坐标</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">纵坐标</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">其他</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description">透明度</div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!-- 属性 -->
        <el-collapse-item title="功能" name="1">
            <el-row>
                <el-col :span="8">
                    <div class="tag">URL</div>
                </el-col>
                <el-col :span="16">
                    <el-input v-model="component.config.option.url" size="mini" clearable></el-input>
                    <el-upload drag
                               class="img-upload"
                               :headers="getToken()"
                               :action="getUploadUrl()"
                               :before-upload="beforeUpload"
                               :on-success="onSuccessCover"
                               :on-Error="onError"
                               :show-file-list="false">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                    </el-upload>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">循环播放</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.loop"></el-checkbox>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">自动播放</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.autoplay"></el-checkbox>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">显示控制器</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.controls"></el-checkbox>
                </el-col>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import API from '../../../../config/api'

    export default {
        name: 'Video',
        props: ['component', 'state'],
        data() {
            return {
            }
        },
        methods: {
            /**
             * 获取token作文参数上传
             */
            getToken() {
                return {token: this.$store.state.token}
            },
            /**
             * 上传之前
             */
            beforeUpload(file) {
                let name = file.name
                let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
                if (ext !== "ogg" && ext !== "mp4" && ext !== 'webm') {
                    this.$message({
                        message: "只支持mp4、ogg、webm文件",
                        type: "error"
                    })
                    return false
                }
            },
            /**
             * 获取上传地址
             */
            getUploadUrl() {
                return API.ATTACHMENT.UPLOAD
            },
            /**
             * 上传成功
             */
            onSuccessCover(res) {
                this.component.config.option.url = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 上传失败
             */
            onError(err) {
                this.$message({
                    message: err.data.message,
                    type: "error"
                })
            },
        }
    }
</script>
