<template>
    <el-collapse value="0" accordion>
        <!-- 基础属性 -->
        <el-collapse-item title="基础属性" name="0">
            <el-row>
                <el-col :span="8" class="tag">尺寸</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.height"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">位置</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">横坐标</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">纵坐标</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">其他</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description">透明度</div>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!-- 功能 -->
        <el-collapse-item title="功能" name="1">
            <el-row>
                <el-col :span="8" class="tag">文本</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.fontSize"
                                     size="mini"
                                     controls-position="right"
                                     :min="12">
                    </el-input-number>
                    <div class="description">字号</div>
                </el-col>
                <el-col :span="8">
                    <el-color-picker v-model="component.config.style.color"
                                     size="mini" show-alpha></el-color-picker>
                    <div class="description">颜色</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">效果</el-col>
                <el-col :span="8">
                    <el-select v-model="component.config.option.type"
                               placeholder="请选择"
                               size="mini">
                        <el-option v-for="item in scrollType"
                                   :key="item.value"
                                   :label="item.name"
                                   :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    export default {
        name: 'ScrollText',
        props: ['component', 'state'],
        data() {
            return {
                scrollType: [
                    {name: '向上', value: 'scroll-up'},
                    {name: '匀速向上', value: 'scroll-up-linear'},
                    {name: '向左', value: 'scroll-left'},
                    {name: '匀速向左', value: 'scroll-left-linear'},
                ]
            }
        },
    }
</script>
