<template>
    <div class="screen-view"
         :style="calcViewStyle"
         v-loading="loading"
         element-loading-background="rgba(0, 0, 0, 0.7)"
         v-if="config && config.sequence && config.sequence.length">
        <Grid :config="config" :state="state" :loader="loader"></Grid>
        <template v-for="(id, index) in config.sequence">
            <template v-if="config.components[id].config">
                <div v-if="!config.base.enableGrid || !config.components[id].config.grid.id"
                     class="view-box"
                     :key="index"
                     :style="calcCompBoxStyle(id)">
                    <component :is="loader[id].default"
                               :base="config.base"
                               :config="config.components[id].config"></component>
                </div>
            </template>
        </template>
        <div v-if="config.base.fullScreen.id"
             class="full-screen-box"
             :style="calcFullScreenSize()">
            <component :is="loader[config.base.fullScreen.id].default"
                       :base="config.base"
                       :config="config.base.fullScreen.component"></component>
            <div class="cancel-full-screen icon iconfont icon-cancel-full-screen"
                 @click=cancelFullScreen></div>
        </div>
    </div>
</template>

<script>
    import '../assets/styles/presentation/main.scss'
    import Grid from '@/components/screen/Grid'
    import API from '../config/api'
    import _ from "lodash";

    export default {
        name: 'Presentation',
        data() {
            return {
                // 加载动画
                loading: true,
                loader: {},
                state: null,
                config: null
            }
        },
        created() {
            this.init()
        },
        computed: {
            /**
             * 计算画布样式
             * @returns
             */
            calcViewStyle() {
                if (!this.config) {
                    return
                }
                if (this.config.base.enableGrid) {
                    return {
                        width: '100%',
                        backgroundColor: this.config.base.backgroundColor,
                        backgroundImage: this.config.base.backgroundImage ?
                            'url(' + this.config.base.backgroundImage + ')' : 'none',
                        backgroundSize: '100% 100%'
                    }
                } else {
                    return {
                        width: this.config.base.size.width + 'px',
                        height: this.config.base.size.height + 'px',
                        backgroundColor: this.config.base.backgroundColor,
                        backgroundImage: this.config.base.backgroundImage ?
                            'url(' + this.config.base.backgroundImage + ')' : 'none',
                        backgroundSize: this.calcBgImageSize
                    }
                }
            },
            /**
             * 根据配置项调整背景图片尺寸
             */
            calcBgImageSize() {
                switch (this.config.base.scaleMode) {
                    case 1:
                        return 'auto 100%'
                    case 2:
                        return '100% 100%'
                    default:
                        return '100% auto'
                }
            },
        },
        methods: {
            init() {
                let id = this.$route.query.id
                if (!id) {
                    this.loading = false
                    this.$message.error('错误：未找到可视化。')
                    return
                }
                this.ajax({
                    url: API.SCREEN.GET_BY_ID,
                    params: {id: id},
                }).then(response => {
                    let config = response.data
                    if (!config) {
                        this.$message.error('错误：未找到可视化。')
                    }
                    config.base = JSON.parse(config.base)
                    config.components = JSON.parse(config.components)
                    config.sequence = JSON.parse(config.sequence)
                    this.config = config
                }).finally(() => {
                    this.loading = false
                })
            },
            /**
             * 计算组件外框样式
             */
            calcCompBoxStyle(id) {
                if (!this.config) {
                    return
                }
                let style = this.config.components[id].config.style
                return {
                    zIndex: style.zIndex || this.config.sequence.length - this.getIndexById(id),
                    top: style && style.top ? style.top + 'px' : 0,
                    left: style && style.left ? style.left + 'px' : 0,
                    visibility: style.visibility === 'hidden' ? 'hidden' : 'visible'
                }
            },
            /**
             * 动态加载组件
             */
            loadComponent() {
                if (!this.config || !this.config.sequence || !this.config.sequence.length) {
                    return
                }
                for (let i = 0; i < this.config.sequence.length; i++) {
                    let id = this.config.sequence[i]
                    let comp = this.config.components[id]
                    if (!comp || !comp.name || this.loader[comp.config.id]) {
                        continue
                    }
                    let path = 'components/screen/layer/' + comp.category + '/' + comp.name
                    this.loader[comp.config.id] = (require('../' + path + '.vue'))
                }
            },
            /**
             * 根据id获取组件索引
             */
            getIndexById(itemId) {
                for (let i = 0; i < this.config.sequence.length; i++) {
                    let id = this.config.sequence[i]
                    if (id == itemId) {
                        return i
                    }
                }
            },
            /**
             * 计算全屏显示的宽度和高度
             */
            calcFullScreenSize() {
                // 当组件全屏显示时，使用屏幕宽高
                if (this.config.base.enableGrid) {
                    return {
                        width: '100%',
                        height: '100%'
                    }
                } else {
                    return {
                        width: this.config.base.size.width + 'px',
                        height: this.config.base.size.height + 'px'
                    };
                }
            },
            /**
             * 取消全屏图表
             */
            cancelFullScreen() {
                this.config.base.fullScreen.id = null
                this.config.base.fullScreen.component = null
            }
        },
        watch: {
            config: {
                deep: true,
                immediate: true,
                handler: function () {
                    this.loadComponent()
                }
            },
            $route: function () {
                this.init()
            }
        },
        components: {
            'Grid': Grid
        }
    }
</script>

<style scoped>

</style>
