<template>
    <el-dialog width="50%"
               title="导入配置"
               :visible.sync="state.visible"
               :close-on-click-modal="false">
        <el-row>
            <el-col :span="24">
                <el-input v-model="screen"
                          type="textarea"
                          :rows="12"
                          size="mini"
                          resize="none">
                </el-input>
            </el-col>
        </el-row>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" type="primary" @click="save">载 入</el-button>
            <el-button size="mini" @click="state.visible = false">取 消</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import CONST from '../../config/const'
    import IdGenerator from '../../utils/IdGenerator'

    export default {
        name: "Loader",
        props: ['config', 'state'],
        data() {
            return {
                rules: {
                    screen: [
                        {required: true, message: '配置不可为空', trigger: 'blur'}
                    ],
                },
                screen: null
            }
        },
        methods: {
            /**
             * 加载配置
             */
            save() {
                try {
                    let data = JSON.parse(this.screen)
                    if (data.hasOwnProperty("id")) {
                        this.importConfig(data)
                    } else {
                        this.importComponents(data)
                    }
                    this.state.visible = false
                    this.screen = null
                } catch (e) {
                    this.$message({message: CONST.MESSAGE.JSON_PARSE_ERROR, type: 'error'})
                }
            },
            importConfig(data) {
                let id = this.config.id
                Object.assign(this.config, data)
                this.config.id = id
                if (typeof this.config.base == "string") {
                    this.config.base = JSON.parse(this.config.base)
                }
                if (typeof this.config.sequence == "string") {
                    this.config.sequence = JSON.parse(this.config.sequence)
                }
                if (typeof this.config.components == "string") {
                    this.config.components = JSON.parse(this.config.components)
                }
                if ("ON" === this.config.state) {
                    this.config.state = 1;
                } else {
                    this.config.state = 0;
                }
            },
            importComponents(data) {
                let idArr = Object.keys(data)
                if (!idArr || !idArr.length) {
                    return
                }
                let seq = []
                for (let i = 0; i < idArr.length; i++) {
                    let id = idArr[i];
                    if (!IdGenerator.validateCmpId(id)) {
                        continue;
                    }
                    let cmp = data[id];
                    let newId = IdGenerator.randomCmpId();
                    cmp.config.id = newId;
                    this.$set(this.config.components, newId, cmp);
                    seq.push(newId);
                }
                seq = seq.concat(this.config.sequence);
                this.$set(this.config, "sequence", seq);
            },
        }
    }
</script>

