<template>
    <el-row :style=calcStyle()
            :class="calcActive() ? 'is-active' : ''"
            @mouseenter.native=toggleConfig(true)
            @mouseleave.native=toggleConfig(false)>
        <template v-if="row.columns">
            <template v-for="(column, i) in row.columns">
                <Column :config="config" :column="column" :state="state" :loader="loader" :key="i"/>
            </template>
        </template>
        <div v-show="state && isMouseover"
             class="row-config is-top icon iconfont icon-grid-top"
             @mouseenter=activeBorder(true)
             @mouseleave=activeBorder(false)
             @click=chooseRow($event)></div>
        <div v-show="state && isMouseover"
             class="row-config is-left icon iconfont icon-grid-left"
             @mouseenter=activeBorder(true)
             @mouseleave=activeBorder(false)
             @click=chooseRow($event)></div>
        <div v-show="state && isMouseover"
             class="row-config is-right icon iconfont icon-grid-right"
             @mouseenter=activeBorder(true)
             @mouseleave=activeBorder(false)
             @click=chooseRow($event)></div>
        <div v-show="state && isMouseover"
             class="row-config is-bottom icon iconfont icon-grid-bottom"
             @mouseenter=activeBorder(true)
             @mouseleave=activeBorder(false)
             @click=chooseRow($event)></div>
    </el-row>
</template>

<script>
    import Column from '@/components/screen/layer/base/Column'
    import _ from 'lodash'

    export default {
        name: "Row",
        props: ['config', 'row', 'state', 'loader'],
        data() {
            return {
                isMouseover: false,
                isActive: false
            }
        },
        mounted() {
        },
        methods: {
            calcStyle() {
                return {
                    height: this.row.style.height + '%'
                }
            },
            calcActive() {
                if (!this.state) {
                    return false
                }
                if (this.state.grid.row === this.row) {
                    return true;
                }
                if (this.isActive) {
                    return true;
                }
                return false
            },
            toggleConfig(val) {
                this.isMouseover = val
            },
            activeBorder(val) {
                this.isActive = val
            },
            chooseRow(e) {
                if (!this.state) {
                    return
                }
                // 取消选择当前编辑的组件
                this.$set(this.state, "select", {})
                this.state.grid.configMode = true
                this.$set(this.state.grid, 'row', this.row)
                e.stopPropagation()
            }
        },
        components: {
            'Column': Column
        }
    }
</script>

