                    ],
                    legend: [{
                        show: true,
                        icon: "circle",
                        top: 'top',
                        left: 'left',
                        orient: "vertical",
                        itemWidth: 14,
                        itemHeight: 14,
                        textStyle: {
                            fontSize: 14,
                            color: "#fff"
                        },
                        data: null
                    }],
                    animationDurationUpdate: 1500,
                    animationEasingUpdate: 'quinticInOut',
                    series: [{
                        type: 'graph',
                        // 布局 circular-环形 / force-力引导
                        layout: 'circular',
                        circular: {
                            rotateLabel: true
                        },
                        force: {
                            // 斥力
                            repulsion: 100,
                        },
                        data: null,
                        links: null,
                        categories: null,
                        roam: true,
                        // 突出焦点关系
                        focusNodeAdjacency: true,
                        height: 200,
                        width: 200,
                        label: {
                            normal: {
                                show: true,
                                fontSize: 12,
                                position: 'right',
                                formatter: '{b}'
                            }
                        },
                        lineStyle: {
                            normal: {
                                color: 'source',
                                curveness: 0.3,
                                width: 1,
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [{
                    "source": "三国演义",
                    "target": "刘备",
                    "sourceValue": 15,
                    "targetValue": 24,
                    "sourceCategory": "三国演义",
                    "targetCategory": "刘备"
                }, {
                    "source": "刘备",
                    "target": "使君",
                    "sourceValue": 24,
                    "targetValue": 9,
                    "sourceCategory": "刘备",
                    "targetCategory": "刘备"
                }, {
                    "source": "刘备",
                    "target": "玄德",
                    "sourceValue": 24,
                    "targetValue": 18,
                    "sourceCategory": "刘备",
                    "targetCategory": "刘备"
                }, {
                    "source": "刘备",
                    "target": "皇叔",
                    "sourceValue": 24,
                    "targetValue": 24,
                    "sourceCategory": "刘备",
                    "targetCategory": "刘备"
                }, {
                    "source": "三国演义",
                    "target": "诸葛亮",
                    "sourceValue": 15,
                    "targetValue": 18,
                    "sourceCategory": "三国演义",
                    "targetCategory": "诸葛亮"
                }, {
                    "source": "诸葛亮",
                    "target": "孔明",
                    "sourceValue": 18,
                    "targetValue": 15,
                    "sourceCategory": "诸葛亮",
                    "targetCategory": "诸葛亮"
                }, {
                    "source": "诸葛亮",
                    "target": "卧龙",
                    "sourceValue": 18,
                    "targetValue": 15,
                    "sourceCategory": "诸葛亮",
                    "targetCategory": "诸葛亮"
                }, {
                    "source": "三国演义",
                    "target": "曹操",
                    "sourceValue": 15,
                    "targetValue": 12,
                    "sourceCategory": "三国演义",
                    "targetCategory": "曹操"
                }, {
                    "source": "曹操",
                    "target": "孟德",
                    "sourceValue": 12,
                    "targetValue": 9,
                    "sourceCategory": "曹操",
                    "targetCategory": "曹操"
                }, {
                    "source": "曹操",
                    "target": "曹公",
                    "sourceValue": 12,
                    "targetValue": 21,
                    "sourceCategory": "曹操",
                    "targetCategory": "曹操"
                }, {
                    "source": "三国演义",
                    "target": "关羽",
                    "sourceValue": 15,
                    "targetValue": 12,
                    "sourceCategory": "三国演义",
                    "targetCategory": "关羽"
                }, {
                    "source": "关羽",
                    "target": "云长",
                    "sourceValue": 12,
                    "targetValue": 6,
                    "sourceCategory": "关羽",
                    "targetCategory": "关羽"
                }, {
                    "source": "关羽",
                    "target": "关公",
                    "sourceValue": 12,
                    "targetValue": 6,
                    "sourceCategory": "关羽",
                    "targetCategory": "关羽"
                }, {
                    "source": "三国演义",
                    "target": "张飞",
                    "sourceValue": 15,
                    "targetValue": 6,
                    "sourceCategory": "三国演义",
                    "targetCategory": "张飞"
                }, {
                    "source": "张飞",
                    "target": "翼德",
                    "sourceValue": 6,
                    "targetValue": 21,
                    "sourceCategory": "张飞",
                    "targetCategory": "张飞"
                }, {
                    "source": "三国演义",
                    "target": "赵云",
                    "sourceValue": 15,
                    "targetValue": 9,
                    "sourceCategory": "三国演义",
                    "targetCategory": "赵云"
                }, {
                    "source": "赵云",
                    "target": "子龙",
                    "sourceValue": 9,
                    "targetValue": 15,
                    "sourceCategory": "赵云",
                    "targetCategory": "赵云"
                }, {
                    "source": "三国演义",
                    "target": "孙权",
                    "sourceValue": 15,
                    "targetValue": 9,
                    "sourceCategory": "三国演义",
                    "targetCategory": "孙权"
                }, {
                    "source": "孙权",
                    "target": "仲谋",
                    "sourceValue": 9,
                    "targetValue": 12,
                    "sourceCategory": "孙权",
                    "targetCategory": "孙权"
                }, {
                    "source": "孙权",
                    "target": "吴侯",
                    "sourceValue": 9,
                    "targetValue": 24,
                    "sourceCategory": "孙权",
                    "targetCategory": "孙权"
                }, {
                    "source": "三国演义",
                    "target": "吕布",
                    "sourceValue": 15,
                    "targetValue": 9,
                    "sourceCategory": "三国演义",
                    "targetCategory": "吕布"
                }, {
                    "source": "吕布",
                    "target": "奉先",
                    "sourceValue": 9,
                    "targetValue": 9,
                    "sourceCategory": "吕布",
                    "targetCategory": "吕布"
                }, {
                    "source": "吕布",
                    "target": "温侯",
                    "sourceValue": 9,
                    "targetValue": 21,
                    "sourceCategory": "吕布",
                    "targetCategory": "吕布"
                }, {
                    "source": "三国演义",
                    "target": "周瑜",
                    "sourceValue": 15,
                    "targetValue": 3,
                    "sourceCategory": "三国演义",
                    "targetCategory": "周瑜"
                }, {
                    "source": "周瑜",
                    "target": "公瑾60",
                    "sourceValue": 3,
                    "targetValue": 15,
                    "sourceCategory": "周瑜",
                    "targetCategory": "周瑜"
                }, {
                    "source": "周瑜",
                    "target": "周郎",
                    "sourceValue": 3,
                    "targetValue": 21,
                    "sourceCategory": "周瑜",
                    "targetCategory": "周瑜"
                }, {
                    "source": "三国演义",
                    "target": "魏延",
                    "sourceValue": 15,
                    "targetValue": 3,
                    "sourceCategory": "三国演义",
                    "targetCategory": "魏延"
                }, {
                    "source": "魏延",
                    "target": "文长",
                    "sourceValue": 3,
                    "targetValue": 21,
                    "sourceCategory": "魏延",
                    "targetCategory": "魏延"
                }]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'source', comment: '来源名', prop: 'name', state: null},
                            {name: 'target', comment: '目标名', prop: 'target', state: null},
                            {name: 'sourceValue', comment: '来源值', prop: 'sourceValue', state: null},
                            {name: 'targetValue', comment: '目标值', prop: 'targetValue', state: null},
                            {name: 'sourceCategory', comment: '来源类型', prop: 'sourceCategory', state: null},
                            {name: 'targetCategory', comment: '目标类型', prop: 'targetCategory', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        source: null,
                        target: null,
                        sourceValue: null,
                        targetValue: null,
                        sourceCategory: null,
                        targetCategory: null,
                    },
                    // 储存一些临时系数
                    variable: {
                        symbolSize: 1
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let sourceCategoryProp = this.config.transform.mapper.sourceCategory || 'sourceCategoryProp'
                let targetCategoryProp = this.config.transform.mapper.targetCategory || 'targetCategory'
                let sourceProp = this.config.transform.mapper.source || 'source'
                let targetProp = this.config.transform.mapper.target || 'target'
                let sourceValueProp = this.config.transform.mapper.sourceValue || 'sourceValue'
                let targetValueProp = this.config.transform.mapper.targetValue || 'targetValue'
                let links = []
                let nodeMap = {}
                let categoryMap = {}
                for (let i = 0; i < this.config.data.length; i++) {
                    let item = this.config.data[i]
                    links.push({
                        source: item[sourceProp],
                        target: item[targetProp]
                    })
                    categoryMap[item[sourceCategoryProp]] = item[sourceCategoryProp]
                    categoryMap[item[targetCategoryProp]] = item[targetCategoryProp]
                    let sourceKey = item[sourceProp]
                    let targetKey = item[targetProp]
                    if (!nodeMap[sourceKey]) {
                        nodeMap[sourceKey] = {
                            name: item[sourceProp],
                            symbolSize: item[sourceValueProp] * this.config.transform.variable.symbolSize,
                            value: item[sourceValueProp],
                            category: item[sourceCategoryProp]
                        }
                    }
                    if (!nodeMap[targetKey]) {
                        nodeMap[targetKey] = {
                            name: item[targetProp],
                            symbolSize: item[targetValueProp] * this.config.transform.variable.symbolSize,
                            value: item[targetValueProp],
                            category: item[targetCategoryProp]
                        }
                    }
                }
                let legend = []
                let categories = []
                for (let key in categoryMap) {
                    categories.push({name: categoryMap[key]})
                    legend.push(categoryMap[key])
                }
                let nodes = []
                for (let key in nodeMap) {
                    nodes.push(nodeMap[key])
                }
                this.config.option.series[0].data = nodes
                this.config.option.series[0].links = links
                this.config.option.series[0].categories = categories
                this.config.option.legend[0].data = legend
            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
