<template>
    <el-collapse value="0" accordion>
        <!-- 基础属性 -->
        <el-collapse-item title="基础属性" name="0">
            <el-row>
                <el-col :span="8" class="tag">位置</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.left"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">横坐标</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.top"
                                     size="mini"
                                     controls-position="right">
                    </el-input-number>
                    <div class="description">纵坐标</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">样式</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.width"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">宽度</div>
                </el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.iframeHeight"
                                     size="mini"
                                     controls-position="right"
                                     :min="0">
                    </el-input-number>
                    <div class="description">高度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">可见</el-col>
                <el-col :span="8">
                    <el-input-number v-model="component.config.style.opacity"
                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description">透明度</div>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" class="tag">状态</el-col>
                <el-col :span="16">
                    <el-switch :value="modalState()"
                               active-text="显示"
                               inactive-text="隐藏"
                               @change="toggleModal($event)">
                    </el-switch>
                </el-col>
            </el-row>
        </el-collapse-item>
        <!-- 属性 -->
        <el-collapse-item title="弹窗" name="1">
            <el-row>
                <el-col :span="4" class="tag">标题</el-col>
                <el-col :span="8">
                    <el-checkbox v-model="component.config.option.header.show">
                    </el-checkbox>
                </el-col>
            </el-row>
            <el-row v-if="component.config.option.header.show">
                <el-col :span="4">
                    <div class="tag">文本</div>
                </el-col>
                <el-col :span="20">
                    <el-input v-model="component.config.option.header.title" size="mini"></el-input>
                </el-col>
            </el-row>
        </el-collapse-item>
        <el-collapse-item title="IFRAME" name="2">
            <el-row>
                <el-col :span="4">
                    <div class="tag">URL</div>
                    <el-col :span="4">
                        <div class="tag">URL</div>
                        <el-tooltip placement="left">
                            <div slot="content">
                                URL中可以包含占位符，用来引用变量。<br>
                                例如：http://www.tyrival.com/:{abc}，其中的 :{abc} 表示引用变量abc。<br>
                                当abc的值发生变更时，URL地址中的:{abc}会被替换为abc的真实值。
                            </div>
                            <i class="icon iconfont icon-help"></i>
                        </el-tooltip>
                    </el-col>
                </el-col>
                <el-col :span="20">
                    <el-input type="textarea"
                              :rows="12"
                              resize="none"
                              v-model="component.config.trigger[1].binder"
                              size="mini"></el-input>
                </el-col>
            </el-row>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    export default {
        name: 'BaseModal',
        props: ['component', 'state'],
        data() {
            return {
                visibility: [
                    {label: '显示', value: 'visible'},
                    {label: '隐藏', value: 'hidden'},
                ],
            }
        },
        created() {
        },
        computed: {},
        methods: {
            modalState() {
                return this.component.config.style.visibility == 'visible'
            },
            toggleModal(e) {
                this.component.config.style.visibility = e ? 'visible' : 'hidden'
            },
        },
        components: {},
    }
</script>
