import Base from './Base'
import axios from 'axios'
import API from '../../../config/api'
import CONST from '../../../config/const'

class ApiData extends Base {
    constructor(component) {
        super(component)
        let proxy = this.component.config.proxy
        if (!proxy.param || !proxy.param.length) {
            return
        }
        let store = {}
        for (let i = 0; i < proxy.param.length; i++) {
            // 参数名或值为空则跳过
            let item = proxy.param[i]
            if (!item.key || !item.value) {
                continue
            }
            // 获取vuex
            let vuex = this.component.$root.$store
            // 参数值如果是:开头，表示绑定了全局状态
            let val = item.value
            if (val.startsWith(':')) {
                let storeProp = val.substring(1)
                if (!storeProp) {
                    continue
                }
                // 在store中储存全局状态名称和值
                store[storeProp] = vuex.commit(CONST.MUTATIONS.GET, {prop: storeProp})
                // 订阅全局状态的修改消息
                vuex.commit(CONST.MUTATIONS.REGISTER, {
                    // 绑定哪个vuex状态
                    store: storeProp,
                    // 当前的绑定对象
                    comp: this.component,
                    // 当vuex状态变更时，应该修改绑定对象的哪个属性
                    prop: CONST.INTERACTION.COMPONENT_PROXY_STORE + storeProp
                })
            }
        }
        this.component.config.store.proxy = store
    }

    /**
     * 根据proxy配置属性生成代理，并将参数进行格式化，其中参数名为空的去除
     * 如果参数名值以冒号(:)开头，则说明引用了全局状态，此时根据状态名获取值，并写入参数对象中
     */
    do() {
        let proxy = this.component.config.proxy
        if (!proxy || !proxy.url) {
            return new Promise((resolve, reject) => {})
        }
        let param = {}
        let vuex = this.component.$root.$store
        if (proxy.param && proxy.param.length) {
            for (let i = 0; i < proxy.param.length; i++) {
                // 参数名为空则不添加
                let item = proxy.param[i]
                if (!item.key) {
                    continue
                }
                let val = item.value
                if (val.startsWith(':')) {
                    let storeProp = val.substring(1)
                    if (!storeProp) {
                        continue
                    }
                    val = vuex.commit(CONST.MUTATIONS.GET, {prop: storeProp})
                }
                if (item.type === "number") {
                    val = val * 1
                }
                if (item.type === "object") {
                    let store = this.component.config.store.proxy
                    for (let key in store) {
                        let regex = new RegExp(':' + key, 'g')
                        let val = store[key]
                        val = val.replace(regex, val)
                    }
                    val = JSON.parse(val)
                }
                param[item.key] = val
            }
        }
        if (proxy.remote) {
            return new Promise((resolve, reject) => {
                axios({
                    url: API.DATA_SOURCE.API_PROXY,
                    method: CONST.AJAX.POST,
                    data: {
                        url: proxy.url,
                        method: proxy.method || CONST.AJAX.GET,
                        params: JSON.stringify(param)
                    },
                }).then((response) => {
                    response.data = JSON.parse(response.data)
                    resolve(response)
                }).catch((error) => {
                    reject(error)
                })
            })
        } else {
            // 返回Promise方便外部调用时进行回调
            return new Promise((resolve, reject) => {
                axios({
                    url: proxy.url,
                    method: proxy.method || CONST.AJAX.GET,
                    data: param,
                }).then((response) => {
                    resolve(response)
                }).catch((error) => {
                    reject(error)
                })
            })
        }
    }
}

export default ApiData
