const IdGenerator = {
    random: function (prefix) {
        return prefix + new Date().getTime() + Math.ceil(Math.random() * 10000)
    },
    randomCmpId: function () {
        return this.random("cmp-")
    },
    randomFullScreenId: function () {
        return this.random("full-screen-")
    },
    validateCmpId: function (id) {
        let prefix = "cmp-"
        if (!id.startsWith(prefix)) {
            return false
        }
        let num = id.replace(prefix, "") / 10000
        let timestamp = Math.floor(num)
        if (!new Date(timestamp) instanceof Date) {
            return false
        }
        return true
    }
}

export default IdGenerator;