<template>
    <Table :config="config" :base="base"></Table>
</template>

<script>
    import Table from '@/components/screen/layer/base/Table'
    import CONST from '../../../../config/const'
    import _ from 'lodash'

    export default {
        name: 'BaseTable',
        props: ['config', 'base'],
        data() {
            return {}
        },
        created() {
            this.initStyle()
            this.initOption()
            this.initData()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        mounted() {

        },
        beforeDestroy() {
            // 销毁前注销组件的数据绑定
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        methods: {
            /**
             * 初始化样式，对新组件使用默认样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    top: 30,
                    left: 30,
                    height:200,
                    opacity: 1,
                    display: 'block',
                    backgroundColor: 'rgba(0,0,0,0)',
                    borderWidth: 2,
                    borderColor: '#00BAFF',
                    header: {
                        fontSize: 14,
                        color: '#cdcdcd',
                        height: 32,
                        backgroundColor: '#151a23',
                        borderWidth: 1,
                        borderColor: '#00BAFF',
                        paddingLeft: 0,
                        paddingTop: 10,
                    },
                    body: {
                        fontSize: 14,
                        color: '#cdcdcd',
                        height: 32,
                        backgroundColor: '#151a23',
                        borderWidth: 1,
                        borderColor: '#00BAFF',
                        paddingLeft: 10,
                        paddingTop: 10,
                    }
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 声明默认option
             */
            initOption() {
                this.config.option = _.assignIn({}, {
                    border: true,
                    stripe: true,
                    fit: false,
                    size: "medium",
                    maxHeight: null,
                    highlightCurrentRow: true,
                    showSummary: false,
                    sumText: '合计',
                    columns: [{
                        type: 'text', text: '名称', prop: 'name', width: 100, fixed: false, sortable: true
                    }, {
                        type: 'text', text: '值', prop: 'value', width: 100, fixed: false, sortable: true
                    }]
                }, this.config.option)
            },
            /**
             * 初始化数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {name: "中国", value: 300},
                    {name: "美国", value: 200},
                    {name: "俄罗斯", value: 100},
                    {name: "中国", value: 300},
                    {name: "美国", value: 200},
                    {name: "俄罗斯", value: 100},
                    {name: "中国", value: 300},
                    {name: "美国", value: 200},
                    {name: "俄罗斯", value: 100},
                    {name: "中国", value: 300},
                    {name: "美国", value: 200},
                    {name: "俄罗斯", value: 100},
                    {name: "中国", value: 300},
                    {name: "美国", value: 200},
                    {name: "俄罗斯", value: 100}
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'cellClick',
                        comment: '点击单元格事件',
                        binders: [
                            {name: 'name', comment: '文本', prop: 'name', state: null},
                            {name: 'value', comment: '值', prop: 'value', state: null},
                        ]
                    }, {
                        name: 'rowClick',
                        comment: '点击行事件',
                        binders: [
                            {name: 'name', comment: '文本', prop: 'name', state: null},
                            {name: 'value', comment: '值', prop: 'value', state: null},
                        ]
                    }, {
                        name: 'headerClick',
                        comment: '点击表头事件',
                        binders: [
                            {name: 'name', comment: '文本', prop: 'name', state: null},
                            {name: 'value', comment: '值', prop: 'value', state: null},
                        ]
                    },
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: null
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propName = mapper.name || 'name'
                let propValue = mapper.value || 'value'
                let optionData = []
                for (let i = 0; i < data.length; i++) {
                    optionData.push({
                        name: data[i][propName],
                        value: data[i][propValue],
                    })
                }
                this.config.option.data = optionData
            },
            /**
             * 值变更事件
             */
            onChange(val) {
                if (!this.config.option.data || !this.config.option.data.length) {
                    return
                }
                let propValue = this.config.transform.mapper.value || 'value'
                for (let i = 0; i < this.config.option.data.length; i++) {
                    let item = this.config.option.data[i]
                    if (item[propValue] == val) {
                        this.onEvent("change", item, i)
                    }
                }
            },
            /**
             * 触发事件代理
             */
            onEvent(eventName, item, index) {
                this.$children[0].onEvent(eventName, item, index)
            },
        },
        watch: {},
        components: {
            "Table": Table
        }
    }
</script>
