<template>
    <el-dialog width="80%"
               title="图库"
               :visible.sync="state.visible"
               :close-on-click-modal="false">
        <div class="picture-container">
            <template v-if="list && list.length">
                <template v-for="(item, i) in list">
                    <div class="picture-card"
                         :class="currentId === item.id ? 'active' : ''"
                         :key="i"
                         @click="choose($event, item)">
                        <img :src="getPictureUrl(item.id)">
                        <div class="close" @click="deletePicture($event, item)">
                            <i class="icon iconfont icon-cross"></i>
                        </div>
                    </div>
                </template>
            </template>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" type="primary" @click="save($event)">选 择</el-button>
            <el-button size="mini" @click="state.visible = false">取 消</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import API from '../../config/api'
    import CONST from "../../config/const";
    import _ from 'lodash'

    export default {
        name: "PictureStore",
        props: ['config', 'state'],
        data() {
            return {
                currentId: null,
                list: []
            }
        },
        mounted() {
            this.loadProprietaryList()
        },
        methods: {
            /**
             * 选择图片
             */
            choose(e, item) {
                e.stopPropagation()
                this.currentId = item.id
            },
            /**
             * 保存
             */
            save(e) {
                e.stopPropagation()
                if (!this.currentId) {
                    this.$message({
                        message: "未选择图片",
                        type: "error"
                    })
                    return
                }
                let url = this.getPictureUrl(this.currentId)
                _.set(this.state.item, this.state.prop, url)
                this.state.visible = false
            },
            /**
             * 加载私有图库
             */
            loadProprietaryList() {
                this.ajax({
                    url: API.ATTACHMENT.LIST_PROPRIETARY,
                    method: CONST.AJAX.GET,
                }).then(response => {
                    _.set(this, 'list', response.data)
                })
            },
            /**
             * 获取图片地址
             */
            getPictureUrl(id) {
                return API.ATTACHMENT.DOWNLOAD + "?id=" + id
            },
            /**
             * 删除图片
             */
            deletePicture(e, item) {
                e.stopPropagation()
                this.$confirm('图片删除后无法恢复, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.ajax({
                        url: API.ATTACHMENT.DELETE_PROPRIETARY + '?id=' + item.id,
                        method: CONST.AJAX.GET,
                    }).then(response => {
                        if (this.currentId === item.id) {
                            this.currentId = null
                        }
                        this.loadProprietaryList()
                        this.$message("删除成功");
                    })
                }).catch(() => {
                })
            }
        }
    }
</script>

