<template>
    <Plugin :config="config" :base="base">
        <el-button :style="calcButtonStyle()"
                   @click="goUrl()">{{config.option.name}}
        </el-button>
    </Plugin>
</template>

<script>
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'Link',
        props: ['config', 'base'],
        data() {
            return {}
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        methods: {
            /**
             * 计算按钮样式
             */
            calcButtonStyle() {
                return {
                    width: this.config.style.btnWidth + 'px',
                    height: this.config.style.btnHeight + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                    color: this.config.style.color,
                    borderColor: this.config.style.borderColor,
                    backgroundColor: this.config.style.backgroundColor,
                    marginLeft: this.config.style.marginLeft + 'px',
                    marginRight: this.config.style.marginRight + 'px',
                    marginTop: this.config.style.marginTop + 'px',
                    marginBottom: this.config.style.marginBottom + 'px',
                    borderTopLeftRadius: this.config.style.borderTopLeftRadius + 'px',
                    borderTopRightRadius: this.config.style.borderTopRightRadius + 'px',
                    borderBottomLeftRadius: this.config.style.borderBottomLeftRadius + 'px',
                    borderBottomRightRadius: this.config.style.borderBottomRightRadius + 'px',
                }
            },
            /**
             * 初始化样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    width: 95,
                    height: 45,
                    btnHeight: 40,
                    btnWidth: 84,
                    marginLeft: 5,
                    marginRight: 5,
                    marginTop: 5,
                    marginBottom: 5,
                    color: '#00BAFF',
                    borderColor: '#00BAFF',
                    backgroundColor: 'rgba(0, 0, 0, 0)',
                    borderTopLeftRadius: 0,
                    borderTopRightRadius: 0,
                    borderBottomLeftRadius: 0,
                    borderBottomRightRadius: 0,
                    horizon: true,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = null
            },
            /**
             * 声明默认option
             */
            initOption() {
                this.config.option = this.config.option || {
                    name: '链接',
                    newWindow: true,
                    url: ''
                }
            },
            /**
             * 初始化事件
             */
            initEvent() {
                this.config.event = null
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = {type: null}
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = null
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
            },
            /**
             * 触发事件代理
             */
            goUrl() {
                let url = this.config.option.url
                let target = this.config.option.newWindow ? "_blank" : "_self"
                if (!url) {
                    return
                }
                if (url.indexOf('http') === 0) {
                    window.open(url, target)
                } else {
                    this.$router.push(url)
                }
            }
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
