<template>
    <Plugin :config="config" :base="base">
        <el-autocomplete v-model="config.option.value"
                         :style="calcStyle()"
                         :fetch-suggestions="querySearchAsync"
                         @select="onSelect"
                         @keydown.enter.native="onEnter">
        </el-autocomplete>
    </Plugin>
</template>

<script>
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'Input',
        props: ['config', 'base'],
        data() {
            return {
                // 查询候选词线程
                thread: null
            }
        },
        created() {
            this.initStyle()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        methods: {
            /**
             * 计算按钮样式
             */
            calcStyle() {
                return {
                    opacity: this.config.style.opacity,
                    color: this.config.style.color,
                    fontSize: this.config.style.fontSize + 'px',
                    borderColor: this.config.style.borderColor,
                    backgroundColor: this.config.style.backgroundColor,
                    borderTopLeftRadius: this.config.style.borderTopLeftRadius + 'px',
                    borderTopRightRadius: this.config.style.borderTopRightRadius + 'px',
                    borderBottomLeftRadius: this.config.style.borderBottomLeftRadius + 'px',
                    borderBottomRightRadius: this.config.style.borderBottomRightRadius + 'px',
                }
            },
            /**
             * 初始化样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    height: 40,
                    width: 200,
                    fontSize: 16,
                    color: '#00BAFF',
                    borderColor: '#00BAFF',
                    backgroundColor: 'rgba(21, 26, 35, 0.3)',
                    borderTopLeftRadius: 4,
                    borderTopRightRadius: 4,
                    borderBottomLeftRadius: 4,
                    borderBottomRightRadius: 4,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {value: '2011'},
                    {value: '2012'},
                    {value: '2013'},
                    {value: '2014'},
                    {value: '2015'},
                    {value: '2016'},
                    {value: '2017'},
                    {value: '2018'},
                ]
            },
            /**
             * 声明默认option
             */
            initOption() {
                this.config.option = this.config.option || {
                    value: '',
                    data: []
                }
            },
            /**
             * 初始化事件
             */
            initEvent() {
                this.config.event = this.config.event || [{
                    name: 'select',
                    comment: '选中值事件',
                    binders: [
                        {name: 'value', comment: '值', prop: 'value', state: null},
                    ]
                }, {
                    name: 'enter',
                    comment: '回车事件',
                    binders: [
                        {name: 'value', comment: '值', prop: 'value', state: null},
                    ]
                }]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        value: null,
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let data = this.config.data
                let mapper = this.config.transform.mapper
                let propValue = mapper.value || 'value'
                let optionData = []
                for (let i = 0; i < data.length; i++) {
                    optionData.push({
                        value: data[i][propValue],
                    })
                }
                this.config.option.data = optionData
            },
            /**
             * 触发事件代理
             */
            onSelect(item) {
                this.$children[0].onEvent('select', item)
            },
            /**
             * 回车事件
             */
            onEnter() {
                this.$children[0].onEvent('enter', {value: this.config.option.value})
            },
            /**
             * 查询候选词
             */
            querySearchAsync(queryString, cb) {
                let data = this.config.option.data
                let results = queryString ? data.filter(this.queryListFilter(queryString)) : data
                cb(results)
            },
            /**
             * 过滤候选列表
             */
            queryListFilter(queryString) {
                return (state) => {
                    return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
                }
            },
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
