<template>
    <Plugin :config="config" :base="base">
        <div class="label-ins"
             :style="calcStyle"
             v-html="config.option.value" @click="onEvent('click')">
        </div>
    </Plugin>
</template>

<script>
    import Plugin from '@/components/screen/layer/base/Plugin'
    import _ from 'lodash'

    export default {
        name: 'Label',
        props: ['config', 'base'],
        data() {
            return {}
        },
        created() {
            this.initStyle()
            this.initTrigger()
            this.initData()
            this.initOption()
            this.initEvent()
            this.initProxy()
            this.initTransform()
        },
        computed: {
            /**
             * 计算容器样式
             */
            calcStyle() {
                return {
                    width: this.config.style.width + 'px',
                    height: this.config.style.height + 'px',
                    lineHeight: this.config.style.height + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                    color: this.config.style.color,
                    fontWeight: this.config.style.fontWeight,
                    fontSize: this.config.style.fontSize + 'px',
                    borderWidth: this.config.style.borderWidth + 'px',
                    borderStyle: this.config.style.borderStyle,
                    borderColor: this.config.style.borderColor,
                    backgroundColor: this.config.style.backgroundColor,
                    borderTopLeftRadius: this.config.style.borderTopLeftRadius + 'px',
                    borderTopRightRadius: this.config.style.borderTopRightRadius + 'px',
                    borderBottomLeftRadius: this.config.style.borderBottomLeftRadius + 'px',
                    borderBottomRightRadius: this.config.style.borderBottomRightRadius + 'px',
                }
            },
        },
        methods: {
            /**
             * 初始化样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    width: 200,
                    height: 50,
                    opacity: 1,
                    display: true,
                    color: '#ffffff',
                    fontSize: 24,
                    fontWeight: 400,
                    borderWidth: 1,
                    borderStyle: 'solid',
                    borderColor: 'rgba(0,0,0,0)',
                    backgroundColor: 'rgba(0,0,0,0)',
                    borderTopLeftRadius: 0,
                    borderTopRightRadius: 0,
                    borderBottomLeftRadius: 0,
                    borderBottomRightRadius: 0,
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 初始化触发器
             */
            initTrigger() {
                this.config.trigger = this.config.trigger || [
                    {
                        show: true,
                        name: '可见性',
                        binder: null,
                        prop: "config.style.visibility",
                        defaultValue: true,
                        isScript: true,
                        mapper: [
                            {key: true, value: 'visible', comment: '返回 true 时显示'},
                            {key: false, value: 'hidden', comment: '返回 false 时隐藏'},
                        ]
                    },
                    {
                        show: true,
                        name: '颜色',
                        binder: null,
                        prop: "config.style.color",
                        defaultValue: '#ffffff',
                        isScript: true,
                        mapper: null
                    }
                ]
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [{value: '一个标题'}]
            },
            /**
             * 声明默认option
             */
            initOption() {

                this.config.option = this.config.option || {
                    value: null,
                    template: '${value}',
                    data: null
                }
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'value', comment: '值', prop: 'value', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    type: 1,
                }, this.config.proxy)
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        value: null,
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                let data = this.config.data[0]
                let mapper = this.config.transform.mapper
                let propValue = mapper.value || 'value'
                this.config.option.data = {value: data[propValue]}
                this.config.option.value =
                    this.config.option.template.replace(/\${value}/g, this.config.option.data.value)
            },
            /**
             * 触发事件代理
             */
            onEvent(eventName, item, index) {
                this.$children[0].onEvent(eventName, this.config.data[0], 0)
            },
        },
        components: {
            'Plugin': Plugin
        },
    }
</script>
