                                     size="mini"
                                     controls-position="right" :min="0" :max="1" :step="0.05">
                    </el-input-number>
                    <div class="description">透明度</div>
                </el-col>
            </el-row>
        </el-collapse-item>

        <!-- 样式 -->
        <el-collapse-item title="样式" name="1">
            <el-collapse value="0">
                <el-collapse-item title="基础" name="0" v-if="component.config.option.grid">
                    <el-row>
                        <el-col :span="8" class="tag">边距</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.grid.left"
                                             size="mini"
                                             controls-position="right" :min="0" :step="1">
                            </el-input-number>
                            <div class="description">左</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.grid.right"
                                             size="mini"
                                             controls-position="right" :min="0" :step="1">
                            </el-input-number>
                            <div class="description">右</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" :offset="8">
                            <el-input-number v-model="component.config.option.grid.top"
                                             size="mini"
                                             controls-position="right" :min="0" :step="1">
                            </el-input-number>
                            <div class="description">上</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.grid.bottom"
                                             size="mini"
                                             controls-position="right" :min="0" :step="1">
                            </el-input-number>
                            <div class="description">下</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="7" class="tag">背景</el-col>
                        <el-col :span="8">
                            <el-color-picker
                                    v-model="component.config.option.backgroundColor"
                                    size="mini"
                                    show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="提示" name="1">
                    <el-row>
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.tooltip.backgroundColor"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">背景</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.tooltip.textStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">文本</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">边框</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.tooltip.borderWidth"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">线宽</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.tooltip.borderColor"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">颜色</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">参考线</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.tooltip.axisPointer.lineStyle.width"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">粗细</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.tooltip.axisPointer.lineStyle.color"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">颜色</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">内边距</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.tooltip.padding[0]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">上</div>
                            <el-input-number v-model="component.config.option.tooltip.padding[3]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">左</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.tooltip.padding[2]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">下</div>
                            <el-input-number v-model="component.config.option.tooltip.padding[1]"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">右</div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <el-collapse-item title="图例" name="2">
                    <el-row>
                        <el-col :span="8" class="tag">显示</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.legend.show">
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">排列方式</el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.legend.orient"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in legend.orient"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <!--<el-row>-->
                        <!--<el-col :span="8" class="tag">水平对齐</el-col>-->
                        <!--<el-col :span="8">-->
                            <!--<el-select v-model="component.config.option.legend.left"-->
                                       <!--placeholder="请选择"-->
                                       <!--size="mini">-->
                                <!--<el-option v-for="item in legend.alignHorizontal"-->
                                           <!--:key="item.value"-->
                                           <!--:label="item.label"-->
                                           <!--:value="item.value">-->
                                <!--</el-option>-->
                            <!--</el-select>-->
                        <!--</el-col>-->
                    <!--</el-row>-->
                    <el-row>
                        <el-col :span="8" class="tag">水平对齐</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="legend.legendX"
                                             size="mini"
                                             controls-position="right"
                                             @change="changeLengXPositon"
                                             :min="0" :max="100" >
                            </el-input-number>(百分比)
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">垂直对齐</el-col>
                        <el-col :span="8">
                            <el-select v-model="component.config.option.legend.top"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in legend.alignVertical"
                                           :key="item.value"
                                           :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">文本</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.legend.textStyle.fontSize"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                            <div class="description">字号</div>
                        </el-col>
                        <el-col :span="8">
                            <el-color-picker v-model="component.config.option.legend.textStyle.color[0]"
                                             size="mini"
                                             show-alpha></el-color-picker>
                            <div class="description">颜色</div>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>

        <!-- x轴 -->
        <el-collapse-item title="x轴" name="2">
            <el-collapse value="0">
                <!-- 标签 -->
                <el-collapse-item title="标签" name="0">
                    <el-row>
                        <el-col :span="8" class="tag">文本</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.xAxis.axisLabel.show">
                                {{component.config.option.xAxis.axisLabel.show ? '显示' : '隐藏'}}
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row v-if="component.config.option.xAxis.axisLabel.show">
                        <el-col :span="8" class="tag">字号</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.xAxis.axisLabel.textStyle.fontSize"
                                             size="mini"
                                             controls-position="right"
                                             :min="12">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">旋转</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.xAxis.axisLabel.rotate"
                                             size="mini"
                                             controls-position="right"
                                             :min="0">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="4">
                            <el-color-picker v-model="component.config.option.xAxis.axisLabel.textStyle.color"
                                             size="mini" show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- 轴线 -->
                <el-collapse-item title="轴线" name="1">
                    <el-row>
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="4">
                            <el-color-picker v-model="component.config.option.xAxis.axisLine.lineStyle.color"
                                             size="mini" show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- 分隔线 -->
                <el-collapse-item title="分隔线" name="2">
                    <el-row>
                        <el-col :span="8" class="tag">显示</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.xAxis.splitLine.show">
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row v-if="component.config.option.xAxis.splitLine.show">
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="4">
                            <el-color-picker v-model="component.config.option.xAxis.splitLine.lineStyle.color[0]"
                                             size="mini" show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>

        <!-- y轴 -->
        <el-collapse-item title="y轴" name="3">
            <el-collapse value="0">
                <!-- 标签 -->
                <el-collapse-item title="标签" name="0">
                    <el-row>
                        <el-col :span="8" class="tag">文本</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.yAxis[0].axisLabel.show">
                                {{component.config.option.xAxis.axisLabel.show ? '显示' : '隐藏'}}
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row v-if="component.config.option.yAxis[0].axisLabel.show">
                        <el-col :span="8" class="tag">字号</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.yAxis[0].axisLabel.textStyle.fontSize"
                                             size="mini"
                                             controls-position="right"
                                             :min="12">
                            </el-input-number>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">值范围</el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.yAxis[0].min"
                                             size="mini"
                                             controls-position="right">
                            </el-input-number>
                            <div class="description">最小值</div>
                        </el-col>
                        <el-col :span="8">
                            <el-input-number v-model="component.config.option.yAxis[0].max"
                                             size="mini"
                                             controls-position="right">
                            </el-input-number>
                            <div class="description">最大值</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="4">
                            <el-color-picker v-model="component.config.option.yAxis[0].axisLabel.textStyle.color"
                                             size="mini" show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- 轴线 -->
                <el-collapse-item title="轴线" name="1">
                    <el-row>
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="4">
                            <el-color-picker v-model="component.config.option.yAxis[0].axisLine.lineStyle.color"
                                             size="mini" show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
                <!-- 分隔线 -->
                <el-collapse-item title="分隔线" name="2">
                    <el-row>
                        <el-col :span="8" class="tag">显示</el-col>
                        <el-col :span="8">
                            <el-checkbox v-model="component.config.option.yAxis[0].splitLine.show">
                            </el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row v-if="component.config.option.yAxis[0].splitLine.show">
                        <el-col :span="8" class="tag">颜色</el-col>
                        <el-col :span="4">
                            <el-color-picker v-model="component.config.option.yAxis[0].splitLine.lineStyle.color[0]"
                                             size="mini" show-alpha></el-color-picker>
                        </el-col>
                    </el-row>
                </el-collapse-item>
            </el-collapse>
        </el-collapse-item>

        <!-- 系列 -->
        <el-collapse-item title="数据系列" name="4">
            <el-row>
                <el-col :span="4">
                    <el-button type="text" size="mini" @click="addSeries">增加</el-button>
                </el-col>
            </el-row>
            <el-collapse value="0">
                <template v-for="(series, i) in component.config.option.series">
                    <el-collapse-item :key="i" :title="series.name" :name="i + ''">
                        <el-row v-if="i > 0">
                            <el-col :span="4">
                                <el-button type="text"
                                           size="mini"
                                           @click="removeSeries(i)">删除
                                </el-button>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">系列名</el-col>
                            <el-col :span="16">
                                <el-input v-model="component.config.option.series[i].name"
                                          size="mini"
                                          controls-position="right"
                                          @change="changeSeriesName">
                                </el-input>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">平滑</el-col>
                            <el-col :span="8">
                                <el-checkbox v-model="component.config.option.series[i].smooth">
                                </el-checkbox>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">节点</el-col>
                            <el-col :span="8">
                                <el-checkbox v-model="component.config.option.series[i].showSymbol">
                                </el-checkbox>
                            </el-col>
                            <el-col :span="8" v-if="component.config.option.series[i].showSymbol">
                                <el-input-number v-model="component.config.option.series[i].symbolSize"
                                                 size="mini"
                                                 controls-position="right"
                                                 :min="0">
                                </el-input-number>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">线条</el-col>
                            <el-col :span="8">
                                <el-color-picker v-model="component.config.option.series[i].itemStyle.normal.color"
                                                 size="mini"
                                                 show-alpha></el-color-picker>
                                <div class="description">颜色</div>
                            </el-col>
                            <el-col :span="8">
                                <el-input-number v-model="component.config.option.series[i].lineStyle.normal.width"
                                                 size="mini"
                                                 controls-position="right"
                                                 :min="0">
                                </el-input-number>
                                <div class="description">粗细</div>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="8" class="tag">填充</el-col>
                            <el-col :span="4">
                                <el-color-picker v-model="areaColor[i].colorStops[0].color"
                                                 size="mini"
                                                 show-alpha
                                                 @change="setAreaColor(i)"></el-color-picker>
                                <div class="description">顶部</div>
                            </el-col>
                            <el-col :span="4">
                                <el-color-picker v-model="areaColor[i].colorStops[1].color"
                                                 size="mini"
                                                 show-alpha
                                                 @change="setAreaColor(i)"></el-color-picker>
                                <div class="description">中部</div>
                            </el-col>
                            <el-col :span="4">
                                <el-color-picker v-model="areaColor[i].colorStops[2].color"
                                                 size="mini"
                                                 show-alpha
                                                 @change="setAreaColor(i)"></el-color-picker>
                                <div class="description">底部</div>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :span="8" class="tag">图表标线</el-col>
                            <el-col :span="16">
                                <el-row style="padding:3px 0px;">
                                    <el-col :span="16" class="tag">
                                        <el-switch
                                                v-model="component.config.option.markLines[i].show"
                                                @change="changeMarkLineFlag($event,i)">
                                        </el-switch>
                                    </el-col>
                                    <el-col :span="8" class="tag">是否显示</el-col>
                                </el-row>
                                <template v-if="component.config.option.markLines[i].show">
                                    <el-row style="padding:3px 0px;">
                                        <el-col :span="16">
                                            <el-input-number
                                                    v-model="component.config.option.markLines[i].markLine.data[0].yAxis"
                                                    size="mini"
                                                    controls-position="right"
                                                    :min="0">
                                            </el-input-number>
                                        </el-col>
                                        <el-col :span="8" class="tag">标线值</el-col>
                                    </el-row>
                                    <el-row style="padding:3px 0px;">
                                        <el-col :span="16">
                                            <el-input-number
                                                    v-model="component.config.option.markLines[i].markLine.lineStyle.opacity"
                                                    size="mini"
                                                    controls-position="right" :min="0" :max="1" :step="0.05">
                                            </el-input-number>
                                        </el-col>
                                        <el-col :span="8" class="tag">透明度</el-col>
                                    </el-row>
                                    <el-row style="padding:3px 0px;">
                                        <el-col :span="16">
                                            <el-input-number
                                                    v-model="component.config.option.markLines[i].markLine.lineStyle.width"
                                                    size="mini"
                                                    controls-position="right"
                                                    :min="0">
                                            </el-input-number>
                                        </el-col>
                                        <el-col :span="8" class="tag">标线宽</el-col>
                                    </el-row>
                                </template>
                            </el-col>
                        </el-row>
                    </el-collapse-item>
                </template>
            </el-collapse>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
    import ColorUtils from '../../../../utils/ColorUtils'
    import _ from 'lodash'

    export default {
        name: 'BaseLine',
        props: ['component', 'state'],
        data() {
            return {
                //可见
                visibility: [
                    {label: '显示', value: 'visible'},
                    {label: '隐藏', value: 'hidden'},
                ],
                legend: {
                    orient: [
                        {label: '水平', value: 'horizontal'},
                        {label: '垂直', value: 'vertical'},
                    ],
                    alignHorizontal: [
                        {label: '左侧', value: 'left'},
                        {label: '居中', value: 'center'},
                        {label: '右侧', value: 'right'},
                    ],
                    alignVertical: [
                        {label: '顶部', value: 'top'},
                        {label: '居中', value: 'middle'},
                        {label: '底部', value: 'bottom'},
                    ],
                    legendX:0
                },
                areaColor: []
            }
        },
        created() {
            this.initAreaColor()
            this.init();
        },
        computed: {},
        methods: {
            init(){
                this.legend.legendX=parseInt(this.component.config.option.legend.left);
            },
            /**
             * 修改图例水平位置
             */
            changeLengXPositon(val){
                this.component.config.option.legend.left=val+'%';
            },
            /**
             * 初始化缓存AreaColor值
             */
            initAreaColor() {
                this.areaColor = []
                let option = this.component.config.option
                if (!option.series || !option.series.length) {
                    return
                }
                for (let i = 0; i < option.series.length; i++) {
                    let series = option.series[i]
                    this.areaColor.push(series.areaStyle.normal.color)
                }
            },
            /**
             * 设置Area渐变色
             */
            setAreaColor(index) {
                this.component.config.option.series[index].areaStyle.normal.color = this.areaColor[index]
            },
            /**
             * 增加一个系列
             */
            addSeries() {
                let index = this.component.config.option.series.length
                // mapper使用的是对象，此处遍历时，需要使用 this.$set() 方法赋值，
                // 否则无法调用mapper的setter，从而无法触发dom的重新渲染
                this.$set(this.component.config.transform.mapper, 'series' + index, null)
                let seriesName = '系列'
                this.component.config.option.legend.data.push(seriesName)
                let color = {
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    type: 'linear',
                    global: false,
                    colorStops: [
                        {offset: 0, color: ColorUtils.randomHex()},
                        {offset: 0.5, color: 'rgba(0,0,0,0)'},
                        {offset: 1, color: 'rgba(0,0,0,0)'}
                    ]
                }
                this.areaColor.push(color)

                this.component.config.option.markLines.push({
                    show:true,
                    markLine:{
                        data:[{yAxis:50}],
                        lineStyle:{
                            width:'1',
                            opacity:'1'
                        }
                    }
                });
                this.component.config.option.series.push({
                    name: seriesName,
                    type: 'line',
                    smooth: true,
                    showSymbol: false,
                    symbol: 'circle',
                    symbolSize: 6,
                    areaStyle: {
                        normal: {
                            color: color
                        }
                    },
                    itemStyle: {
                        normal: {
                            color: ColorUtils.randomHex()
                        }
                    },
                    lineStyle: {
                        normal: {
                            width: 3
                        }
                    }
                })
            },
            /**
             * 删除一个系列
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                // 删除mapper的属性
                let mapper = _.cloneDeep(this.component.config.transform.mapper)
                for (let i = index; i < this.component.config.option.series.length; i++) {
                    mapper['series' + i] = mapper['series' + (i + 1)]
                }
                delete mapper['series' + (this.component.config.option.series.length - 1)]
                this.component.config.transform.mapper = mapper
                this.component.config.option.series.splice(index, 1)
                this.component.config.option.legend.data.splice(index, 1)
                this.areaColor.splice(index, 1)
            },
            /**
             * 修改series名
             */
            changeSeriesName() {
                let legend = []
                for (let i = 0; i < this.component.config.option.series.length; i++) {
                    let series = this.component.config.option.series[i]
                    legend.push(series.name)
                }
                this.component.config.option.legend.data = legend
            },
            /**
             * 图标标线控制
             */
            changeMarkLineFlag(value, index){
                if(value){
                    this.component.config.option.series[index].markLine=this.component.config.option.markLines[index].markLine;
                }else{
                    this.component.config.option.series[index].markLine=null;
                }
            }
        },
        components: {},
    }
</script>
