<template>
    <Chart :config="config" :base="base"></Chart>
</template>

<script>
    import Chart from '@/components/screen/layer/base/Chart'
    import 'echarts-gl'
    import _ from 'lodash'

    export default {
        name: 'ThreeDimScatter',
        props: ['config', 'base'],
        data() {
            return {}
        },
        methods: {
            /**
             * 声明默认配置项
             */
            initOption() {
                let defaultOption = {
                    backgroundColor: 'transparent',
                    dataset: {
                        dimensions: ['x', 'y', 'z', 'color', 'size'],
                        source: null,
                    },
                    visualMap: [{
                        show: true,
                        left: 'right',
                        top: 'top',
                        dimension: 3,
                        min: 0,
                        max: 100,
                        calculable: true,
                        itemWidth: 15,
                        itemHeight: 60,
                        text: ['尺寸'],
                        textGap: 15,
                        textStyle: {
                            color: '#fff'
                        },
                        inRange: {
                            symbolSize: [10, 30]
                        }
                    }, {
                        show: true,
                        left: 'right',
                        top: 'bottom',
                        calculable: true,
                        dimension: 4,
                        min: 20,
                        max: 60,
                        itemWidth: 15,
                        itemHeight: 60,
                        text: ['颜色'],
                        textGap: 15,
                        textStyle: {
                            color: '#fff'
                        },
                        inRange: {
                            color: ['#1710c0', '#00fea8', '#f5f811', '#fe0300']
                        },
                    }],
                    grid3D: {
                        boxWidth: 120,
                        boxDepth: 50,
                        boxHeight: 80,
                        viewControl: {
                            // projection: 'orthographic'
                        },
                        axisPointer: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            },
                            label: {
                                textStyle: {
                                    color: '#ffffff',
                                    fontSize: 16,
                                }
                            }
                        },
                        light: {
                            main: {
                                intensity: 1.2,
                                shadow: true
                            },
                            ambient: {
                                intensity: 0.3
                            }
                        }
                    },
                    xAxis3D: {
                        type: 'category',
                        nameTextStyle: {
                            color: 'rgba(0,0,0,0)'
                        },
                        axisTick: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLabel: {
                            show: true,
                            textStyle: {
                                color: '#ffffff',
                                fontSize: 12,
                                borderWidth: 1,
                                borderColor: 'rgba(0,0,0,0)'
                            }
                        }
                    },
                    yAxis3D: {
                        type: 'category',
                        nameTextStyle: {
                            color: 'rgba(0,0,0,0)'
                        },
                        axisTick: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLabel: {
                            show: true,
                            color: 'rgb(170,170,170)',
                            textStyle: {
                                color: '#ffffff',
                                fontSize: 12,
                                borderWidth: 1,
                                borderColor: 'rgba(0,0,0,0)'
                            }
                        }
                    },
                    zAxis3D: {
                        type: 'value',
                        nameTextStyle: {
                            color: 'rgba(0,0,0,0)'
                        },
                        axisTick: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#ffffff',
                                opacity: 1,
                                width: 1
                            }
                        },
                        axisLabel: {
                            show: true,
                            color: 'rgb(170,170,170)',
                            textStyle: {
                                color: '#ffffff',
                                fontSize: 12,
                                borderWidth: 1,
                                borderColor: 'rgba(0,0,0,0)'
                            }
                        }
                    },
                    series: [{
                        type: 'scatter3D',
                        dimensions: ['x', 'y', 'z', 'color', 'size'],
                        itemStyle: {
                            borderWidth: 1,
                            borderColor: 'rgba(255,255,255,0.8)'
                        },
                        emphasis: {
                            itemStyle: {
                                color: '#fff'
                            }
                        }
                    }]
                }
                this.config.option = _.assignIn(defaultOption, this.config.option)
            },
            /**
             * 声明默认静态数据
             */
            initData() {
                this.config.data = this.config.data || [
                    {x: '2013', y: '一季度', z: 20, color: 20, size: 20},
                    {x: '2013', y: '二季度', z: 22, color: 22, size: 22},
                    {x: '2013', y: '三季度', z: 24, color: 24, size: 24},
                    {x: '2013', y: '四季度', z: 30, color: 30, size: 30},
                    {x: '2014', y: '一季度', z: 28, color: 28, size: 28},
                    {x: '2014', y: '二季度', z: 32, color: 32, size: 32},
                    {x: '2014', y: '三季度', z: 37, color: 37, size: 37},
                    {x: '2014', y: '四季度', z: 40, color: 40, size: 40},
                    {x: '2015', y: '一季度', z: 32, color: 32, size: 32},
                    {x: '2015', y: '二季度', z: 35, color: 35, size: 35},
                    {x: '2015', y: '三季度', z: 42, color: 42, size: 42},
                    {x: '2015', y: '四季度', z: 47, color: 47, size: 47},
                    {x: '2016', y: '一季度', z: 42, color: 42, size: 42},
                    {x: '2016', y: '二季度', z: 45, color: 45, size: 45},
                    {x: '2016', y: '三季度', z: 42, color: 42, size: 42},
                    {x: '2016', y: '四季度', z: 59, color: 59, size: 59},
                    {x: '2017', y: '一季度', z: 46, color: 46, size: 46},
                    {x: '2017', y: '二季度', z: 46, color: 46, size: 46},
                    {x: '2017', y: '三季度', z: 50, color: 50, size: 50},
                    {x: '2017', y: '四季度', z: 52, color: 52, size: 52},
                    {x: '2018', y: '一季度', z: 45, color: 45, size: 45},
                    {x: '2018', y: '二季度', z: 47, color: 47, size: 47},
                    {x: '2018', y: '三季度', z: 44, color: 44, size: 44},
                    {x: '2018', y: '四季度', z: 50, color: 50, size: 50},
                ]
            },
            /**
             * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
             * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
             * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
             * name: 面板上展示的属性名
             * comment: 面板上展示的属性说明文字
             * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
             * state: 将该属性绑定到全局状态中的哪个属性，
             * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
             */
            initEvent() {
                this.config.event = this.config.event || [
                    {
                        name: 'click',
                        comment: '点击事件',
                        binders: [
                            {name: 'x', comment: 'x坐标', prop: 'x', state: null},
                            {name: 'y', comment: 'y坐标', prop: 'y', state: null},
                            {name: 'z', comment: 'z坐标', prop: 'z', state: null},
                            {name: 'color', comment: '颜色', prop: 'color', state: null},
                            {name: 'size', comment: '尺寸', prop: 'size', state: null},
                        ]
                    }
                ]
            },
            /**
             * 初始化数据代理配置，默认静态数据
             */
            initProxy() {
                this.config.proxy = this.config.proxy || {
                    type: 1,
                }
            },
            /**
             * 初始化数据属性映射规则
             */
            initTransform() {
                this.config.transform = this.config.transform || {
                    mapper: {
                        x: null,
                        y: null,
                        z: null,
                        color: null,
                        size: null
                    }
                }
            },
            /**
             * 将config.data按照规则转换为option中的展示数据的过程
             */
            handler() {
                if (!this.config.data || !this.config.data.length) {
                    return
                }
                // let sizeMax = 0
                // let colorMax = 0
                let xProp = this.config.transform.mapper.x || 'x'
                let yProp = this.config.transform.mapper.y || 'y'
                let zProp = this.config.transform.mapper.z || 'z'
                let sizeProp = this.config.transform.mapper.size || 'size'
                let colorProp = this.config.transform.mapper.color || 'color'
                let source = []
                for (let i = 0; i < this.config.data.length; i++) {
                    let item = this.config.data[i]
                    let dataModel = []
                    dataModel.push(item[xProp])
                    dataModel.push(item[yProp])
                    dataModel.push(item[zProp])
                    dataModel.push(item[sizeProp])
                    dataModel.push(item[colorProp])
                    source.push(dataModel)
                    // if (item[sizeProp] > sizeMax) {
                    // 	sizeMax = item[sizeProp]
                    // }
                    // if (item[colorProp] > colorMax) {
                    // 	colorMax = item[colorProp]
                    // }
                }
                this.config.option.dataset = {source: source}
                // this.config.option.visualMap[0].max = _.ceil(sizeMax, ((sizeMax + '').length - 1) * -1)
                // this.config.option.visualMap[1].max = _.ceil(colorMax, ((colorMax + '').length - 1) * -1)

            }
        },
        components: {
            'Chart': Chart
        },
    }
</script>
