<script>
    export default {
        name: 'ClusterScatter',
        props: ['component', 'state'],
        data() {
            return {
                seriesOption: []
            }
        },
        computed: {},
        methods: {
            /**
             * 设置隐藏范围参数
             */
            setOutOfRangeSize() {
                this.component.config.option.visualMap[0].outOfRange.symbolSize =
                    this.component.config.option.visualMap[0].inRange.symbolSize
            },
            /**
             * 删除一个系列
             */
            removeSeries(index) {
                if (this.component.config.option.series.length <= 1) {
                    return
                }
                this.component.config.option.color.splice(index, 1)
                this.component.config.option.series.splice(index, 1)
            },
        },
        components: {},
    }
</script>
