<template>
    <div :id="state && state.grid && state.grid.id" :class="isDesignMode() ? 'grid is-design' : 'grid'">
        <Row v-if="config.base.enableGrid"
             :config="config"
             :row="config.base.grid"
             :state="state"
             :loader="loader"/>
    </div>
</template>

<script>
    import Row from '@/components/screen/layer/base/Row'

    export default {
        name: "Grid",
        props: ['config', 'state', 'loader'],
        data() {
            return {}
        },
        mounted() {
        },
        methods: {
            isDesignMode() {
                return !!this.state
            }
        },
        components: {
            'Row': Row
        }
    }
</script>

