<template>
    <div class="config-base">
        <el-tabs type="border-card" stretch>
            <el-tab-pane label="配置">
                <div class="title">{{getTitle}}</div>
                <component v-if="isActiveSingleComp()"
                           :is="loader[getActiveComponent()['config']['id']].default"
                           :component="getActiveComponent()"
                           :state="state"></component>
            </el-tab-pane>
            <el-tab-pane label="数据">
                <div class="title">{{getTitle}}</div>
                <Data v-if="getActiveComponent()"
                      :component="getActiveComponent()"
                      :state="state"></Data>
            </el-tab-pane>
            <el-tab-pane label="事件">
                <div class="title">{{getTitle}}</div>
                <Interaction v-if="getActiveComponent()"
                             :component="getActiveComponent()"></Interaction>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
    import Data from "./Data"
    import Interaction from "./Interaction"

    export default {
        name: "Base",
        props: ["config", "state"],
        data() {
            return {
                // 动态加载组件的存储
                loader: {},
            }
        },
        created() {
            this.loadConfig()
        },
        computed: {
            getTitle() {
                let ids = Object.keys(this.state.select)
                if (!ids || !ids.length || ids.length !== 1) {
                    return
                }
                let id = ids[0];
                return this.config.components[id] ? this.config.components[id].text : ""
            }
        },
        methods: {
            /**
             * 获得当前组件的配置文件
             */
            getActiveComponent() {
                let ids = Object.keys(this.state.select)
                if (!ids || !ids.length || ids.length !== 1) {
                    return
                }
                let id = ids[0];
                return this.config.components[id]
            },
            /**
             * 是否仅选中了一个组件
             */
            isActiveSingleComp() {
                return Object.keys(this.state.select).length === 1
            },
            /**
             * 动态加载组件
             */
            loadConfig() {
                if (!this.config.sequence || !this.config.sequence.length) {
                    return
                }
                for (let i = 0; i < this.config.sequence.length; i++) {
                    let id = this.config.sequence[i]
                    let comp = this.config.components[id]
                    if (!comp || !comp.name || this.loader[id]) {
                        continue
                    }
                    let path = "components/screen/config/" + comp.category + "/" + comp.name
                    this.loader[id] = (require("../../../" + path + ".vue"))
                }
            },
        },
        watch: {
            "config.components": {
                immediate: true,
                handler: function (val, oldVal) {
                    this.loadConfig()
                }
            }
        },
        components: {
            "Data": Data,
            "Interaction": Interaction
        }
    }
</script>

